import { useCallback, useEffect, useState } from "react";
import { findCharacter } from "../../../data/controllers";
import { SharedSpace } from "../styles";
import Details from "./Details";
import Overview from "./Overview";

export default function TalentList(props) {
  const [index, setIndex] = useState(-1);
  const [atDetails, setAtDetails] = useState(false);
  const charData = findCharacter(props.char);

  useEffect(() => {
    setAtDetails(false);
  }, [charData.code]);

  const forcedClose =
    index === -1 ||
    index >= charData.actvTalents.length + charData.pasvTalents.length;
  return (
    <SharedSpace>
      <div className={"left_part " + (atDetails ? "close" : "open")}>
        <Overview
          {...props}
          charData={charData}
          toDetails={useCallback((i) => {
            setAtDetails(true);
            setIndex(i);
          }, [])}
        />
      </div>
      {forcedClose ? (
        <div className="right_part close" />
      ) : (
        <div className={"right_part " + (atDetails ? "open" : "close")}>
          <Details
            charData={charData}
            index={index}
            setIndex={setIndex}
            close={() => {
              setAtDetails(false);
              setTimeout(() => setIndex(-1), 200);
            }}
          />
        </div>
      )}
    </SharedSpace>
  );
}
