import cn from "classnames";
import { useRef, useState } from "react";
import { FaInfoCircle, FaTimes } from "react-icons/fa";
import styled from "styled-components";
import encodeExportedData from "../../helpers/encode";
import { findArtifact } from "../../data/controllers";
import {
  CollapseContent,
  CollapseHeading,
  Green,
  List,
  Span,
  Text
} from "../../styledCpns/DataDisplay";
import { Button, IconBtn, TextArea } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { colors } from "../../theme";
import { Collapse } from "./HiddenSpace";
import Modal from "./Modal";

export const CloseBtn = (props) => (
  <IconBtn variant="negative" size="1.75rem" {...props}>
    <FaTimes />
  </IconBtn>
);

const StyledInfoSign = styled(IconBtn)`
  color: white;
  background-color: black;
  &:hover {
    color: ${colors.lightGold};
  }
`;
export const InfoSign = ({ on, selfHover }) =>
  on ? (
    <CloseBtn size="1.5rem" fontSize="0.875rem" />
  ) : (
    <StyledInfoSign
      className={cn({ info_sign: !selfHover })}
      size="1.5rem"
      fontSize="1.5rem"
    >
      <FaInfoCircle />
    </StyledInfoSign>
  );

export const SeeDetails = styled((props) => (
  <Text {...props}>See details</Text>
))`
  color: ${colors.white};
  cursor: pointer;
  &:hover {
    color: ${colors.lightGold};
  }
  &.active {
    color: ${colors.green};
  }
`;

export function ButtonBar({
  className,
  texts,
  availables,
  variants = [],
  distance = "ml-5",
  handlers
}) {
  return (
    <div className={"flex justify-center " + (className || "")}>
      {texts.map((txt, i) => {
        const available = !availables || availables[i];
        let variant = variants[i];
        if (!variant) {
          variant = i
            ? i === texts.length - 1
              ? "positive"
              : "neutral"
            : "negative";
        }
        return (
          <Button
            key={i}
            className={
              (i ? distance + " " : "") +
              (available ? "glow-on-hover" : "unavailable")
            }
            variant={variant}
            onClick={handlers[i]}
          >
            {txt}
          </Button>
        );
      })}
    </div>
  );
}

export function EquippedChar({ user }) {
  return (
    <Box className="mt-3 px-4 py-1 flex" bgColor="#ffe7bb">
      <Text bold color="black">
        Equipped: {user}
      </Text>
    </Box>
  );
}

export function NoItems(props) {
  return (
    <div className="pt-5 flex-center">
      <Text variant="h5" bold color="lightRed">
        No {props.type} to display
      </Text>
    </div>
  );
}

const StyledSvg = styled.svg`
  width: 1.25rem;
  height: 1.25rem;
  fill: ${(props) => colors[props.rarity]};
`;

export const Star = (props) => (
  <StyledSvg viewBox="0 0 24 24" rarity={props.rarity}>
    <path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z" />
  </StyledSvg>
);

export const StarLine = ({ rarity, className = "" }) => {
  const result = [];
  for (let i = 0; i < rarity; i++) {
    result.push(<Star key={i} rarity={rarity} />);
  }
  return <div className={"flex align-center " + className}>{result}</div>;
};

export function SetBonus({ sets }) {
  return (
    <div>
      <Text variant="h6" color="orange" bold>
        Set Bonus
      </Text>
      {sets.length > 0 ? (
        sets.map(({ code, bonusLv }, i) => {
          const content = [];
          const artData = findArtifact({ code });
          for (let i = 0; i <= bonusLv; i++) {
            const { type, desc } = artData.setBnes[i];
            content.push(
              <li key={i} className="mt-1">
                <Span color="orange">{type}:</Span> <Span>{desc}</Span>
              </li>
            );
          }
          return (
            <div key={i} className="mt-1">
              <Text variant="h6" bold color="green">
                {artData.name}
              </Text>
              <List className="pl-4">{content}</List>
            </div>
          );
        })
      ) : (
        <Text color="rgb(185, 185, 185)" bold>
          No Set Bonus
        </Text>
      )}
    </div>
  );
}

export const TalentXtraLv = ({ xtraLv }) =>
  xtraLv ? (
    <Text className="ml-2" bold color="green">
      +{xtraLv}
    </Text>
  ) : null;

export const AmpRxnDesc = ({ elmt, mult }) => (
  <>
    Increases <Span color={elmt}>{elmt} DMG</Span> by{" "}
    <Green b>{Math.round(mult * 1000) / 1000}</Green> times.
  </>
);

export function Confirmation({ close, message, otherBtn, thirdBtn, confirm }) {
  let distance;
  let texts = [otherBtn?.text || "Cancel", "Confirm"];
  let handlers = [
    () => {
      if (otherBtn?.handleClick) otherBtn.handleClick();
      close();
    },
    () => {
      confirm();
      close();
    }
  ];
  if (thirdBtn?.text) {
    distance = "ml-3";
    texts.splice(1, 0, thirdBtn.text);
    handlers.splice(1, 0, () => {
      thirdBtn.handleClick();
      close();
    });
  }
  return (
    <Modal close={close}>
      <Box
        className="p-3 max-width-95"
        width="22.5rem"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <div className="py-3">
          <Text style={{ fontSize: "1.375rem" }} align="center">
            {message}
          </Text>
        </div>
        <ButtonBar
          className="mt-3"
          texts={texts}
          distance={distance}
          handlers={handlers}
        />
      </Box>
    </Modal>
  );
}

export function ExpandableList(props) {
  const [expanded, setExpanded] = useState([]);
  return (
    <ExpandableListCore
      expanded={expanded}
      setExpanded={setExpanded}
      {...props}
    />
  );
}
export function ExpandableListCore({
  expanded,
  setExpanded,
  headingList,
  contentList
}) {
  return (
    <div>
      {headingList.map((heading, i) => (
        <div key={i} className={cn({ "mb-4": expanded[i] })}>
          <CollapseHeading
            className={cn("mb-2p5 pointer", { active: expanded[i] })}
            onClick={() =>
              setExpanded((prev) => {
                const newEpd = [...prev];
                newEpd[i] = !newEpd[i];
                return newEpd;
              })
            }
          >
            {heading}
          </CollapseHeading>
          <Collapse in={Boolean(expanded[i])}>
            <CollapseContent>{contentList[i]}</CollapseContent>
          </Collapse>
        </div>
      ))}
    </div>
  );
}

export function ShareLayout({ data, setupName, close }) {
  const [status, setStatus] = useState(0);
  const inputRef = useRef();
  return (
    <Modal close={close}>
      <Box
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 px-2"
          variant="h5"
          color="orange"
          bold
          align="center"
        >
          Share "{setupName}"
        </Text>
        <TextArea
          ref={inputRef}
          className="p-2"
          rows="15"
          value={encodeExportedData(data)}
          readOnly
        />
        {status > 0 && (
          <Text
            className="mt-2"
            align="center"
            color={status === 1 ? "green" : "lightRed"}
          >
            {status === 1
              ? "Successfully copied to Clipboard."
              : "We're sorry. Your browser does not allow/support this function."}
          </Text>
        )}
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Copy"]}
          handlers={[
            close,
            () => {
              navigator.clipboard.writeText(inputRef.current.value).then(
                () => setStatus(1),
                () => setStatus(2)
              );
            }
          ]}
        />
      </Box>
    </Modal>
  );
}

export const BetaMark = styled((props) => <div {...props}>BETA</div>)`
  border-radius: 0.25rem;
  padding: 0 0.25rem;
  background: white;
  color: red;
  border: 2px solid red;
  font-size: 0.75rem;
  font-weight: 700;
`;

export function Maintenance({ close }) {
  return (
    <Modal close={close}>
      <Box
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text variant="h5" color="orange" bold align="center">
          This function is currently under maintenance. Sorry for the
          inconveniences.
        </Text>
        <ButtonBar className="mt-3" texts={["Close"]} handlers={[close]} />
      </Box>
    </Modal>
  );
}

export function HowToModal({ content, close }) {
  return (
    <Modal contentClass="p-4" close={close}>
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <CloseBtn onClick={close} />
      </AbsoluteBox>
      <Text className="mb-2" variant="h4" color="orange">
        HOW-TOs
      </Text>
      {content}
    </Modal>
  );
}
