import { memo, useState } from "react";
import { useSelector } from "react-redux";
import { calculator } from "../../../app/calculatorSlice";
import { getPartyData } from "../../../data/controllers";
import { ExpandableList } from "../../CommonCpns";
import { Card, MainSelect } from "../styles";
import { PartyBuffs, SelfBuffs } from "./Buffs/CharBuffs";
import { ArtBuffs, ElmtBuffs } from "./Buffs/OtherBuffs";
import WpBuffs from "./Buffs/WpBuffs";
import CustomMods from "./CustomMods";
import { PartyDebuffs, SelfDebuffs } from "./Debuffs/CharDebuffs";
import { ArtDebuffs, ElmtDebuffs } from "./Debuffs/OtherDebuffs";
import Target from "./Target";

export default memo(Modifiers);

function Modifiers() {
  const [tab, setTab] = useState("Buffs");
  return (
    <Card className="px-4 py-3 flex-col" bgColor="cardBg1">
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={["Buffs", "Debuffs", "Target"]}
      />
      <div className="mt-3 grow-1 custom-sb">
        {tab === "Buffs" ? (
          <Buffs />
        ) : tab === "Debuffs" ? (
          <Debuffs />
        ) : (
          <Target />
        )}
      </div>
    </Card>
  );
}

function Buffs() {
  const { parties, curSetupI } = useSelector(calculator);
  const partyData = getPartyData(parties[curSetupI]);
  return (
    <ExpandableList
      headingList={[
        "Resonance & Reactions",
        "Self",
        "Party",
        "Weapons",
        "Artifacts",
        "Custom",
      ]}
      contentList={[
        <ElmtBuffs />,
        <SelfBuffs partyData={partyData} />,
        <PartyBuffs partyData={partyData} />,
        <WpBuffs />,
        <ArtBuffs />,
        <CustomMods modType="BCs" />,
      ]}
    />
  );
}

function Debuffs() {
  return (
    <ExpandableList
      headingList={[
        "Resonance & Reactions",
        "Self",
        "Party",
        "Artifacts",
        "Custom",
      ]}
      contentList={[
        <ElmtDebuffs />,
        <SelfDebuffs />,
        <PartyDebuffs />,
        <ArtDebuffs />,
        <CustomMods modType="DCs" />,
      ]}
    />
  );
}
