import { round2, round3, roundMaker } from "../../../helpers";
import { lib } from "../../../library";
import { Green, Orange, Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Lesser, LesserSpan } from "./styles";

const Divider = ({ width, bgColor = "white", ...rest }) => (
  <Box width={width} height="1px" bgColor={bgColor} {...rest} />
);

function DefMultDisplay({ _, defIg }) {
  return (
    <div className="flex align-center pt-2">
      <Text className="mr-3" color="lightGold">
        DEF Mult:
      </Text>
      <div className="flex-col align-center">
        <Text>{_.char}</Text>
        <Divider className="my-1" width="100%" />
        <Text className="px-2" align="center">
          {_.rd}{" "}
          {!!defIg && (
            <>
              (1 - <LesserSpan>Def ignore</LesserSpan> <Green>{defIg}</Green> /
              100) *
            </>
          )}{" "}
          {_.target} + {_.char}
        </Text>
      </div>
    </div>
  );
}

export default function Damage({ infusion, forDefMult, dmg, defIg, tracker }) {
  return (
    <div>
      {infusion && Object.values(infusion).some((inf) => inf !== "Physical") && (
        <div className="flex align-center">
          <Text className="pt-1" color="lightGold">
            Infusion
          </Text>
          <div className="pl-3">
            {Object.entries(infusion).map(
              ([key, elmt]) =>
                elmt !== "Physical" && (
                  <Lesser key={key}>
                     {lib[key]}: <Green>{elmt}</Green>
                  </Lesser>
                )
            )}
          </div>
        </div>
      )}
      <DefMultDisplay _={forDefMult} defIg={defIg} />
      {Object.keys(dmg).map((hit) => {
        const { values, tlBnes } = tracker[hit];
        const defMult = round3(values[7]);
        const { "Non-crit": nonCrit, Crit, Average } = dmg[hit];
        const Nc = apply(nonCrit);
        const cDmg = round3(values[10]);
        return (
          <div key={hit} className="pt-2">
            <Text bold>{hit}</Text>
            <div className="pl-3">
              {!!Object.keys(tlBnes).length && (
                <>
                  <Text className="pt-1" color="lightGold">
                     Exclusive:
                  </Text>
                  <div className="pl-3">
                    {Object.entries(tlBnes).map(([key, { desc, value }]) => (
                      <Lesser key={key}>
                        + {lib[key]}: {desc}{" "}
                        <Green>
                          {key === "mult" ? round2(value) : value}
                          {key !== "flat" && "%"}
                        </Green>
                      </Lesser>
                    ))}
                  </div>
                </>
              )}
              <Lesser>
                 Non-crit <Orange b>{Nc}</Orange>
                {values[2] ? (
                  <>
                    {" "}
                    = ({values[1]} <Green>{values[0]}</Green> <Green>*</Green>{" "}
                    Talent Mult. <Green>{apply(values[2], roundPct)}</Green>
                    {display("Flat Bonus", values[3], "+", 0, Math.round)})
                  </>
                ) : null}
                {display("Percent Mult.", values[4] * 100, "*", 0, roundPct)}
                {display("Special Mult.", values[5], "*", 1, roundMaker(4))}
                {display("Reaction Mult.", values[6], "*", 1, round3)}
                {display("DEF Mult.", defMult)}
                {display("RES Mult.", values[8])}
                {values[11]}
              </Lesser>
              {!!Crit && (
                <Lesser>
                   Crit: <Orange b>{apply(Crit)}</Orange> = {Nc}{" "}
                  <Green>*</Green> (<Green>1 +</Green> Crit DMG{" "}
                  <Green>{cDmg}</Green>)
                </Lesser>
              )}
              {Average !== nonCrit && (
                <Lesser>
                   Average: <Orange b>{apply(Average)}</Orange> = {Nc}{" "}
                  <Green>*</Green> (<Green>1 +</Green> Crit DMG{" "}
                  <Green>{cDmg}</Green> <Green>*</Green> Crit Rate{" "}
                  <Green>{round3(values[9])}</Green>)
                </Lesser>
              )}
            </div>
          </div>
        );
      })}
    </div>
  );
}

function display(desc, value, sign = "*", cond = 0, cb) {
  return value && value !== cond ? (
    <>
      {" "}
      <Green>{sign}</Green> {desc} <Green>{cb ? cb(value) : value}</Green>
    </>
  ) : null;
}

function roundPct(pct) {
  return round2(pct) + "%";
}

function apply(subject, cb = Math.round) {
  return Array.isArray(subject)
    ? `[${subject.map(cb).join(", ")}]`
    : cb(subject);
}
