import styled from "styled-components";
import { colors } from "../../theme";

export const StyledIntro = styled.div`
  .content > p,
  .hidden_content > p {
    line-height: 1.6;
  }
  .content > p {
    margin-top: 0.5rem;
  }
  .hidden_content > p:not(:first-child) {
    margin-top: 0.5rem;
  }
`;

export const LoadingBar = styled.div`
  width: 20rem;
  height: 2rem;
  border: 3px solid ${colors.green};
  border-radius: 0.5rem;
  overflow: hidden;
  & > div {
    width: 0;
    height: 100%;
    background-color: ${colors.green};
    animation-name: loading;
    animation-duration: 90ms;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards;
  }
  @keyframes loading {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }
`;

export const ImportOpts = styled.div`
  width: 22.5rem;
  border-radius: 1rem;
  background-color: ${colors.cardBg3};
  .table_wrapper {
    max-width: 18rem;
  }
`;
