import { artifactIcons, artTypes, wikiImg } from "../../../configs";
import { findArtifact, findWeapon } from "../../../data/controllers";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box, GradientBox } from "../../../styledCpns/Layout";

export default function Gears({ weapon, artPieces, setModalType }) {
  const { beta, icon, rarity } = findWeapon(weapon);
  return (
    <Box className="flex-wrap" width="15.75rem">
      <GearIcon
        bgImg={rarity}
        beta={beta}
        icon={icon}
        popModal={() => setModalType("Weapon")}
      />
      {artPieces.map((artP, i) => {
        if (artP) {
          const { beta, icon } = findArtifact(artP);
          return (
            <GearIcon
              key={i}
              bgImg={artP.rarity || 5}
              beta={beta}
              icon={icon}
              popModal={() => setModalType("Artifacts")}
            />
          );
        }
        return <GearIcon key={i} icon={artifactIcons[artTypes[i]]} />;
      })}
    </Box>
  );
}

const GearIcon = ({ bgImg, popModal, beta, icon }) => (
  <div className="p-1 glow-on-hover">
    <GradientBox
      className={"p-1" + (bgImg ? " pointer" : "")}
      bgImg={bgImg}
      borderRadius="0.25rem"
      onClick={popModal}
    >
      <Img width="4.25rem" src={beta ? icon : wikiImg(icon)} alt="" />
    </GradientBox>
  </div>
);
