import styled from "styled-components";
import { colors } from "../../theme";
import { Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { DmgTables } from "../CommonCpns/DmgTables";

const StyledDiv = styled.div`
  min-width: 21.75rem;
  border-radius: 0.5rem;
  background-color: ${colors.cardBg3};
`;

export default function DmgResults({ setup, dmg }) {
  if (!setup) {
    return <StyledDiv className="ml-3 full-h" />;
  }
  return (
    <StyledDiv className="ml-3 px-3 pt-2 pb-3 flex-col">
      <div>
        <Text className="ellipsis" align="center">
          {setup.name}
        </Text>
      </div>
      <Box className="mt-2 grow-1 hide-sb">
        <DmgTables
          resetSignal={setup?.ID ? setup.ID : 0}
          charName={setup.char.name}
          dmg={dmg}
          focus={null}
        />
      </Box>
    </StyledDiv>
  );
}
