import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import { colors } from "../../../../theme";
import { calculator, MODIFY_TARGET } from "../../../../app/calculatorSlice";
import {
  CollapseHeading,
  Span,
  Link,
  Text,
} from "../../../../styledCpns/DataDisplay";
import { Collapse } from "../../../CommonCpns/HiddenSpace";
import Monster from "./Monster";
import { findCharacter } from "../../../../data/controllers";

export default function Target() {
  const { char, target, curSetupI, allInfusion } = useSelector(calculator);
  const dispatch = useDispatch();
  const [dropped, setDropped] = useState(false);
  const resTypes = Object.keys(target).slice(1);

  return (
    <div>
      <div className="mt-2 px-3">
        <Line
          label="Level"
          color="lightGold"
          value={target.Level}
          handleChange={(e) => {
            const lv = +e.target.value;
            if (!isNaN(lv) && lv >= 0 && lv <= 100) {
              dispatch(MODIFY_TARGET({ type: "Level", value: Math.round(lv) }));
            }
          }}
        />
        <Monster />
      </div>

      <CollapseHeading
        className={dropped ? "active" : ""}
        onClick={() => setDropped(!dropped)}
      >
        Resistances
      </CollapseHeading>
      <Collapse in={dropped}>
        <div className="pt-3 px-3">
          {resTypes.map((type, i) => {
            const elmt = type.split(" ")[0];
            return (
              <Line
                key={i}
                label={type}
                color={elmt === "Physical" ? "White" : elmt}
                value={target[type]}
                handleChange={(e) => {
                  const res = +e.target.value;
                  if (!isNaN(res) && res <= 999) {
                    dispatch(MODIFY_TARGET({ type, value: Math.round(res) }));
                  }
                }}
              />
            );
          })}
          <Text>
            You can search for your target's Resistances on{" "}
            <Link
              href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
              rel="noreferrer"
              target="_blank"
            >
              this page of the Genshin Impact Wiki
            </Link>
            .
          </Text>
        </div>
      </Collapse>
      <Notes infusion={allInfusion[curSetupI]} charName={char.name} />
    </div>
  );
}

const Input = styled.input`
  padding: 0.5rem 0.5rem 0.25rem;
  width: 5rem;
  border: none;
  border-radius: 0.25rem;
  text-align: right;
  font-size: 1.125rem;
  &:focus {
    box-shadow: 0 0 0 4px ${colors.red};
  }
`;

function Line(props) {
  return (
    <div className="mb-3 flex justify-between align-center">
      <Text variant="h6" color={props.color}>
        {props.label}
      </Text>
      <Input type="text" value={props.value} onChange={props.handleChange} />
    </div>
  );
}

const destructedTypes = {
  NA: "Normal Attacks",
  CA: "Charged Attacks",
  AS: "Aimed Shot",
  CAS: "Fully-charged Aimed Shot",
  PA: "Plunging Attacks",
};

// [here, ...MySetups/Setup/Modifiers/index]
export function Notes({ infusion, charName }) {
  const { vision, weapon } = findCharacter({ name: charName });
  const notes =
    weapon === "Catalyst"
      ? [
          ["NA", vision],
          ["CA", vision],
          ["PA", vision],
        ]
      : Object.entries(infusion);
  if (weapon === "Bow") {
    notes[1][0] = "AS";
    notes.splice(2, 0, ["CAS", vision]);
  }
  return (
    <div className="mt-2 pr-2">
      <Text variant="h6" color="lightGold">
        Notes:
      </Text>
      {notes.map((pair, i) => (
        <Text key={i} className="mt-1">
          <b>{destructedTypes[pair[0]]}</b> deal{" "}
          <Span color={pair[1]}>{pair[1]} DMG</Span>.
        </Text>
      ))}
    </div>
  );
}
