import { memo } from "react";
import { visionIcons, wikiImg } from "../../configs";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { AbsoluteBox, Box, GradientBox } from "../../styledCpns/Layout";
import { BetaMark } from "../CommonCpns";
import { BodyCell } from "./styles";

// massAdd & each are undefined if singlePicker
export default function Body({ data, forChar, massAdd, each, pick }) {
  return (
    <div className="pr-3 full-h overflow-auto custom-sb">
      <div className="flex-wrap">
        {data.map((item, i) => (
          <MemoItem
            key={item.code.toString() + item.rarity}
            forChar={forChar}
            item={item}
            massAdd={massAdd}
            pickedAmount={each?.[i]}
            pick={() => pick(item, i)}
          />
        ))}
      </div>
    </div>
  );
}

const MemoItem = memo(Item, (prev, next) => {
  return (
    prev.massAdd === next.massAdd && prev.pickedAmount === next.pickedAmount
  );
});

function Item({ item, forChar, pickedAmount, pick }) {
  const hasCons = item.constellation !== undefined;
  return (
    <BodyCell className={forChar ? "w-vision" : "wo-vision"}>
      <Thumb
        item={item}
        forChar={forChar}
        pickedAmount={pickedAmount}
        pick={pick}
      />
      {forChar && (
        <div className={"badge" + (hasCons ? " w-cons" : "")}>
          {hasCons && <p className="cons">C{item.constellation}</p>}
          <Img
            width="20px"
            src={wikiImg(visionIcons[item.vision])}
            alt="vision"
            draggable={false}
          />
        </div>
      )}
    </BodyCell>
  );
}

function Thumb({ item, forChar, pickedAmount, pick }) {
  return (
    <div className="pointer zoomin-on-hover relative" onClick={pick}>
      {item.beta && (
        <AbsoluteBox top={0} left={0} zIndex={1}>
          <BetaMark />
        </AbsoluteBox>
      )}
      <div className="relative">
        <GradientBox
          className={"overflow-hidden flex " + (forChar ? "pt-3" : "p-1")}
          borderRadius="0.5rem 0.5rem 0 0"
          bgImg={item.rarity}
        >
          <Img
            src={item.beta && !forChar ? item.icon : wikiImg(item.icon)}
            alt=""
            width="100%"
            height="100%"
            draggable={false}
          />
        </GradientBox>
        {Boolean(pickedAmount) && (
          <AbsoluteBox bottom={0} right="0.25rem">
            <Text color="black" bold>
              {pickedAmount}
            </Text>
          </AbsoluteBox>
        )}
      </div>
      <Box borderRadius="0 0 0.5rem 0.5rem" bgColor="defaultBg">
        <Text className="px-2 ellipsis" color="black" bold align="center">
          {item.name}
        </Text>
      </Box>
    </div>
  );
}
