import { addAndTrack } from "../../../calculators/helpers";
import { elmtDmgBnes } from "../../../configs";
import { applyPct, findByCode } from "../../../helpers";
import { Green } from "../../../styledCpns/DataDisplay";
import { wpSimpleAnTmaker } from "../config";
import { LiyueSeries } from "../series";

const GoldCatalysts = [
  {
    code: 122,
    name: "Kagura's Verity",
    icon: "b/b7/Weapon_Kagura%27s_Verity",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "CRIT DMG", scale: "14.4%" },
    buffs: [
      {
        index: 0,
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [3],
        addBnes: ({ ATTRs, hitBnes, wpRfm, inputs, tkDesc, tracker }) => {
          const bnValue1 = (9 + wpRfm * 3) * inputs[0];
          addAndTrack(tkDesc, hitBnes, "ES.pct", bnValue1, tracker);
          if (inputs[0] === 3) {
            const bnValue2 = 9 + wpRfm * 3;
            addAndTrack(tkDesc, ATTRs, elmtDmgBnes, bnValue2, tracker);
          }
        },
        desc: ({ wpRfm }) =>
          findByCode(GoldCatalysts, 122).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Kagura Dance of the Sacred Sakura",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            Gains the Kagura Dance effect when using an Elemental Skill, causing
            the <Green>Elemental Skill DMG</Green> of the character wielding
            this weapon to increase by <Green b>{9 + wpRfm * 3}%</Green> for
            16s. Max <Green b>3</Green> <Green>stacks</Green>. This character
            will gain <Green b>{9 + wpRfm * 3}%</Green>{" "}
            <Green>All Elemental DMG Bonus</Green> when they possess 3 stacks.
          </>
        )
      }
    ]
  },
  {
    code: 34,
    name: "Everlasting Moonglow",
    icon: "e/e1/Weapon_Everlasting_Moonglow",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "HP%", scale: "10.8%" },
    addBnes: wpSimpleAnTmaker("ATTRs", "Healing Bonus", 2.5),
    addFinalBnes: ({ ATTRs, hitBnes, wpRfm, tkDesc, tracker }) => {
      if (hitBnes) {
        const bnValue = applyPct(ATTRs.HP, 0.75 + wpRfm * 0.25);
        addAndTrack(tkDesc, hitBnes, "NA.flat", bnValue, tracker);
      }
    },
    pasvName: "Byakuya Kougetsu",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <Green>Healing Bonus</Green> increased by{" "}
            <Green b>{7.5 + wpRfm * 2.5}%</Green>,{" "}
            <Green>Normal Attack DMG</Green> is increased by{" "}
            <Green b>{0.75 + wpRfm * 0.25}%</Green> of the <Green>Max HP</Green>{" "}
            of the character equipping this weapon.
          </>
        ),
        others: (
          <>
            For 12s after using an Elemental Burst, Normal Attacks that hit
            opponents will restore 0.6 Energy. Energy can be restored this way
            once every 0.1s.
          </>
        )
      }
    ]
  },
  {
    code: 31,
    name: "Skyward Atlas",
    icon: "3/33/Weapon_Skyward_Atlas",
    rarity: 5,
    mainStatScale: 48,
    subStat: { type: "ATK%", scale: "7.2%" },
    addBnes: wpSimpleAnTmaker("ATTRs", elmtDmgBnes, 3),
    pasvName: "Wandering Clouds",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <Green>Elemental DMG Bonus</Green> by{" "}
            <Green b>{9 + wpRfm * 3}%</Green>. Normal Attack hits have a{" "}
            <Green>50% chance</Green> to earn the favor of the clouds. which
            actively seek out nearby opponents to attack for 15s, dealing{" "}
            <Green b>{120 + wpRfm * 40}%</Green> <Green>ATK</Green> DMG. Can
            only occur once every <Green>30s</Green>.
          </>
        )
      }
    ]
  },
  {
    code: 32,
    name: "Lost Prayer to the Sacred Winds",
    icon: "9/98/Weapon_Lost_Prayer_to_the_Sacred_Winds",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "CRIT Rate", scale: "7.2%" },
    buffs: [
      {
        index: 0,
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [5],
        addBnes: ({ ATTRs, wpRfm, inputs, tkDesc, tracker }) => {
          const bnValue = (6 + wpRfm * 2) * inputs[0];
          addAndTrack(tkDesc, ATTRs, elmtDmgBnes, bnValue, tracker);
        },
        desc: ({ wpRfm }) =>
          findByCode(GoldCatalysts, 32).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Boundless Blessing",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: <>Increases Movement SPD by 10%.</>,
        buff: (
          <>
            When in battle, gain an <Green b>{6 + wpRfm * 2}%</Green>{" "}
            <Green>Elemental DMG Bonus</Green> every 4s. Max <Green b>4</Green>{" "}
            stacks.
          </>
        ),
        others: <>Lasts until the character falls or leaves combat.</>
      }
    ]
  },
  {
    code: 33,
    name: "Memory of Dust",
    icon: "c/ca/Weapon_Memory_of_Dust",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "ATK%", scale: "10.8%" },
    ...LiyueSeries
  }
];

export default GoldCatalysts;
