import styled from "styled-components";
import {
  bgColorProp,
  brProps,
  colorProp,
  fsProp,
  fwProp,
  getColor
} from "./helpers";
import { colors } from "../theme";

export const Select = styled(
  ({
    borderRadius,
    bgColor,
    color,
    bold,
    fontSize,
    align,
    optColor,
    optFS,
    ...rest
  }) => (
    <div>
      <select {...rest} />
    </div>
  )
)`
  height: 100%;
  width: 100%;
  display: block;
  border: none;
  ${brProps};
  background-color: ${(pr) => getColor(pr.bgColor) || "transparent"};
  ${colorProp};
  font-family: "Nunito", "Helvetica", "Roboto", "Arial", sans-serif;
  ${fsProp};
  ${fwProp};
  outline: none;
  text-align-last: ${(pr) => pr.align};
  option {
    color: ${(pr) => getColor(pr.optColor) || "black"};
    font-size: ${(pr) => pr.optFS || "1rem"};
  }
  &.artSS-select {
    padding: 0 0.5rem 0 2.5rem;
    background-position: 0.5rem;
  }
  &.calc-main-select {
    background-origin: content-box;
    background-position: 100%;
  }
`;

export const Button = styled.button`
  padding: ${(pr) => pr.padding || "0.25rem 1rem"};
  border-radius: ${(pr) => pr.borderRadius || "1rem"};
  background-color: ${(pr) => {
    const map = { positive: "lightGold", neutral: "green", negative: "red" };
    return colors[map[pr.variant] || "defaultBg"];
  }};
  color: ${(pr) => (pr.variant === "negative" ? "white" : "black")};
  ${bgColorProp};
  ${colorProp};
  font-family: "Nunito", "Helvetica", "Roboto", "Arial", sans-serif;
  font-weight: ${(pr) => pr.fontWeight || 700};
  ${fsProp};
  box-shadow: var(--cmn-shadow);
  &:not(.unavailable) {
    cursor: pointer;
  }
`;

export const IconBtn = styled(({ size, ...rest }) => (
  <Button padding="0" borderRadius="50%" {...rest} />
))`
  ${({ size }) => `
    width: ${size || "2rem"};
    height: ${size || "2rem"};
    min-height: ${size || "2rem"};
  `}
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const TextInput = styled.input`
  width: ${(pr) => pr.width};
  ${brProps};
  ${fsProp};
  text-align: ${(pr) => pr.align};
  border: ${(pr) => pr.border || "none"};
  :focus {
    background-color: ${colors.green};
  }
`;

export const TextArea = styled.textarea`
  border-radius: 0.25rem;
  border: none;
  font-size: 1rem;
  word-break: break-all;
  resize: none;
  outline: none;
  :focus {
    box-shadow: 0 0 0 3px ${colors.orange};
  }
`;

export const Checkbox = styled(({ scale, ...rest }) => (
  <input type="checkbox" {...rest} />
))`
  transform: ${(pr) => `scale(${pr.scale || 1.6})`};
`;
