import classnames from "classnames";
import { useState } from "react";
import { FaCaretDown, FaCaretUp } from "react-icons/fa";
import styled from "styled-components";
import { getRxnBnesFromEM } from "../../calculators/helpers";
import { baseSTypes, flatSTypes, pctSTypes } from "../../configs";
import { Green, Text } from "../../styledCpns/DataDisplay";
import { colors } from "../../theme";
import { Collapse } from "../CommonCpns/HiddenSpace";

// also [./TalentList/DetailsContent]
export const Table = styled.div`
  width: 100%;
  cursor: default;
  .row {
    display: flex;
    justify-content: space-between;
    padding: 0.25rem 0.5rem 0;
    font-weight: 700;
  }
  .row:hover {
    background-color: #8a1200;
  }
  & > div:nth-child(2n + 1) {
    background-color: ${colors.cardBg2};
  }
  .brokedown {
    display: none;
    white-space: nowrap;
  }
  .wrapper:hover .brokedown {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }
  .wrapper:hover .total {
    display: none;
  }
  .caret {
    position: relative;
    bottom: 2px;
    color: ${colors.white};
    &.dropped {
      color: ${colors.green};
    }
  }
`;

const statsTypes = flatSTypes.concat(pctSTypes.slice(3));

export default function StatsTable({ stats, breakdown }) {
  const EM = stats?.["Elemental Mastery"] || 0;
  const rxnBnFromEM = getRxnBnesFromEM(EM);
  return (
    <div>
      <Table>
        {baseSTypes.map((type, i) => (
          <div key={i} className="row wrapper">
            <Text>{type}</Text>
            <div className="relative">
              <Text className={classnames("mr-2", { total: breakdown })}>
                {stats?.[type]}
              </Text>
              {breakdown && (
                <Text className="mr-2 brokedown">
                  {stats?.["Base " + type]} +{" "}
                  <Green>
                    {(stats?.[type] || 0) - (stats?.["Base " + type] || 0)}
                  </Green>
                </Text>
              )}
            </div>
          </div>
        ))}
        <Droppable EM={EM} rxnBnFromEM={rxnBnFromEM} />
        {statsTypes.slice(4).map((type, i) => (
          <div key={i} className="row">
            <Text>{type}</Text>
            <Text className="mr-2">
              {Math.round((stats?.[type] || 0) * 10) / 10}%
            </Text>
          </div>
        ))}
      </Table>
    </div>
  );
}

function Droppable({ EM, rxnBnFromEM }) {
  const [dropped, setDropped] = useState(false);
  const Icon = dropped ? FaCaretUp : FaCaretDown;
  return (
    <div>
      <div className="row pointer" onClick={() => setDropped(!dropped)}>
        <div className="flex align-center">
          <Text className="mr-1">Elemental Mastery</Text>
          <Icon className={classnames("caret", { dropped })} />
        </div>
        <Text className="mr-2">{EM}</Text>
      </div>
      <Collapse in={dropped}>
        <div className="px-2 py-1">
          <Text variant="subtitle1">
            Increases damage caused by Vaporize and Melt by{" "}
            <Green>{rxnBnFromEM.amplifying}%</Green>.
          </Text>
          <Text className="mt-1" variant="subtitle1">
            Increases damage caused by Overloaded, Superconduct,
            Electro-Charged, Shattered, and Swirl by{" "}
            <Green>{rxnBnFromEM.transformative}%</Green>.
          </Text>
          <Text className="mt-1" variant="subtitle1">
            Increases damage absorption power of shields created through
            Crystallize by <Green>{rxnBnFromEM.shield}%</Green>.
          </Text>
        </div>
      </Collapse>
    </div>
  );
}
