import cn from "classnames";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { UNEQUIP_ART } from "../../../app/databaseSlice";
import { colors } from "../../../theme";
import { ButtonBar, SetBonus } from "../../CommonCpns";
import { MyArtCard, MyWpCard } from "../../Readers/MyItemCard";
import StatsTable from "../../Readers/StatsTable";

const StyledDetails = styled.div`
  .details_card {
    max-height: ${(props) =>
      props.maxHeight ? props.maxHeight + "px" : "none"};
  }
  &.large .details_card {
    width: 20.25rem;
    padding: 1rem 0.75rem;
    border-left: 2px solid ${colors.cardBg2};
    border-radius: 0 0.5rem 0.5rem 0;
    background-color: ${colors.cardBg1};
  }
`;

export default function Details({
  large,
  height,
  opened,
  setOpened,
  weapon,
  setWpInvOn,
  art,
  openArtInv,
  artSBnes,
  setShowing,
  close
}) {
  const { pieces, sets } = art;
  const { user } = weapon;

  useEffect(() => {
    if (
      (opened >= 0 && opened < 5 && !pieces[opened]) ||
      (opened === "set bonus" && !sets.length)
    ) {
      close();
    }
  }, [opened, user]);

  let Cpn;
  if (opened === "weapon") {
    Cpn = (
      <div className="details_card flex-col">
        <div className="px-1 grow-1 hide-sb">
          <MyWpCard weapon={weapon} mutable={true} />
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Switch"]}
          variants={["positive"]}
          handlers={[() => setWpInvOn(true)]}
        />
      </div>
    );
  } else {
    if (opened === "set bonus") {
      Cpn = (
        <div className="details_card flex">
          <div className="px-1 hide-sb">
            <SetBonus sets={sets} />
          </div>
        </div>
      );
    } else if (opened === "stats bonus") {
      Cpn = (
        <div className="details_card flex">
          <div className="custom-sb">
            <StatsTable stats={artSBnes} />
          </div>
        </div>
      );
    } else {
      if (opened === -1 || !pieces[opened]) {
        return null;
      }
      Cpn = (
        <Artifact
          artP={pieces[opened]}
          setShowing={setShowing}
          opened={opened}
          setOpened={setOpened}
          openArtInv={openArtInv}
        />
      );
    }
  }
  return (
    <StyledDetails
      className={cn("py-2", { large })}
      maxHeight={large ? height - 16 : height - 68}
    >
      {Cpn}
    </StyledDetails>
  );
}

function Artifact({ artP, setShowing, opened, setOpened, openArtInv }) {
  const dispatch = useDispatch();
  return (
    <div className="details_card">
      <div className="pb-2 hide-sb">
        <MyArtCard art={artP} mutable={true} />
      </div>
      <ButtonBar
        className="mt-4"
        texts={["Unequip", "Switch"]}
        handlers={[
          () => {
            setShowing(false);
            setTimeout(() => {
              setOpened(-1);
              dispatch(
                UNEQUIP_ART({ user: artP.user, artID: artP.ID, pos: opened })
              );
            }, 200);
          },
          openArtInv
        ]}
      />
    </div>
  );
}
