import { useEffect, useState } from "react";
import { FaMinus } from "react-icons/fa";
import { useSelector } from "react-redux";
import { calculator } from "../../../app/calculatorSlice";
import { getRxnBnesFromEM } from "../../../calculators/helpers";
import { bareLv, pickOne } from "../../../helpers";
import { lib } from "../../../library";
import { Green, Text } from "../../../styledCpns/DataDisplay";
import { IconBtn } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { CloseBtn, ExpandableListCore } from "../../CommonCpns";
import { ModalContent } from "../../CommonCpns/Modal";
import { Attributes, Bonuses } from "./AllStats";
import Damage from "./Damage";
import Reactions from "./Reactions";
import Debuffs from "./Debuffs";
import { calculateAndTrack, getTotal } from "./helpers";
import { HiddenTracker, Lesser, LesserSpan } from "./styles";

export default function Tracker({ trackerState, setTrackerState }) {
  const calcInfo = useSelector(calculator);
  const [expanded, setExpanded] = useState([]);
  const { name } = calcInfo.setups[calcInfo.curSetupI];
  const result = calculateAndTrack(calcInfo);
  const [tracker, infusion, ATTRs, hitBnes, rxnBnes, dmg] = result;

  const rxnBnesFrEM = getRxnBnesFromEM(ATTRs["Elemental Mastery"]);

  const totalDefRd = getTotal(tracker.Def_rd);
  const charLv = bareLv(pickOne(calcInfo.char.level, calcInfo.curSetupI));
  const forDefMult = {
    char: (
      <>
        <LesserSpan>char. Lv.</LesserSpan> <Green>{charLv}</Green> + 100
      </>
    ),
    target: (
      <>
        (<LesserSpan>target Lv.</LesserSpan>{" "}
        <Green>{calcInfo.target.Level}</Green> + 100)
      </>
    ),
    rd: totalDefRd ? (
      <>
        (1 - <LesserSpan>DEF reduction</LesserSpan>{" "}
        <Green>{getTotal(tracker.Def_rd)}</Green> / 100) *
      </>
    ) : null
  };

  const notMobile = window.innerWidth >= 769;
  const close = () => setTrackerState(0);

  useEffect(() => {
    const handlePressEsc = (e) => {
      if (e.key === "Escape") close();
    };
    document.addEventListener("keydown", handlePressEsc, true);
    return () => document.removeEventListener("keydown", handlePressEsc, true);
  }, []);

  return (
    <HiddenTracker state={trackerState}>
      <Box
        className="full-w full-h"
        bgColor="rgba(0, 0, 0, .6)"
        onClick={close}
      />
      <ModalContent className="p-4 flex-col">
        <AbsoluteBox className="flex" top="0.75rem" right="0.75rem">
          <IconBtn
            className="mr-3 glow-on-hover"
            size="1.75rem"
            variant="default"
            onClick={() => setTrackerState(2)}
          >
            <FaMinus size="1.25rem" />
          </IconBtn>
          <CloseBtn className="glow-on-hover" onClick={close} />
        </AbsoluteBox>
        {notMobile ? (
          <div className="flex justify-center">
            <Text
              variant="h2"
              color="orange"
              onClick={() => console.log(tracker)}
            >
              Tracking Results
            </Text>
            <Lesser className="ml-2">({name})</Lesser>
          </div>
        ) : (
          <Text variant="h2" color="orange">
            {name}
          </Text>
        )}
        <div className="mt-3 custom-sb scroll-smooth">
          <ExpandableListCore
            expanded={expanded}
            setExpanded={setExpanded}
            headingList={[
              "Attributes",
              "Bonuses",
              "Debuffs on Target",
              "Normal Attacks",
              "Elemental Skill",
              "Elemental Burst",
              "Reactions"
            ]}
            contentList={[
              <Attributes ATTRs={ATTRs} tracker={tracker} />,
              <Bonuses
                hitBnes={hitBnes}
                rxnBnesFrEM={rxnBnesFrEM}
                rxnBnes={rxnBnes}
                tracker={tracker}
              />,
              <Debuffs target={calcInfo.target} tracker={tracker} />,
              <Damage
                infusion={infusion}
                forDefMult={forDefMult}
                dmg={dmg[lib.NA]}
                defIg={getTotal(tracker.NA_ig)}
                tracker={tracker[lib.NA]}
              />,
              <Damage
                forDefMult={forDefMult}
                dmg={dmg[lib.ES]}
                defIg={getTotal(tracker.ES_ig)}
                tracker={tracker[lib.ES]}
              />,
              <Damage
                forDefMult={forDefMult}
                dmg={dmg[lib.EB]}
                defIg={getTotal(tracker.EB_ig)}
                tracker={tracker[lib.EB]}
              />,
              <Reactions
                dmg={dmg["Reactions DMG"]}
                charLv={charLv}
                tracker={tracker.Reactions}
              />
            ]}
          />
        </div>
      </ModalContent>
    </HiddenTracker>
  );
}
