import cn from "classnames";
import { useState } from "react";
import styled from "styled-components";
import { wikiImg } from "../../configs";
import { findCharacter, findWeapon } from "../../data/controllers";
import { findByIndex } from "../../helpers";
import { Img, Span, Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, SeeDetails } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";

const Details = styled.div`
  height: 0;
  transition: height 200ms linear;
  &.active {
    margin-top: 0.5rem;
    height: 60vh;
  }
`;

export default function Outdates({ outdates, close }) {
  const [active, setActive] = useState(false);
  return (
    <Modal close={close}>
      <Box
        className="p-3 white-glow-shadow max-width-95"
        width="30rem"
        borderRadius="0.5rem"
        bgColor="cardBg2"
      >
        <Text color="green" variant="h6" align="center">
          Successfully loaded your data
        </Text>
        <Text className="mt-1" color="lightRed">
          We have removed outdated buffs / debuffs in your Setup(s).
        </Text>
        <SeeDetails
          className={cn("mt-1", { active })}
          onClick={() => setActive(!active)}
        />
        <Details className={cn("hide-sb", { active })}>
          {outdates.map((od, i) => (
            <Outdate key={i} i={i} od={od} />
          ))}
        </Details>
        <ButtonBar
          className="mt-3"
          texts={["Confirm"]}
          variants={["positive"]}
          handlers={[close]}
        />
      </Box>
    </Modal>
  );
}

function Outdate({ i, od }) {
  return (
    <Box
      className={cn({ "mt-2": i }, "p-3")}
      borderRadius="0.5rem"
      bgColor="cardBg3"
    >
      <div className="flex justify-between align-start">
        <Text variant="h6" color="orange" bold>
          {od.name}
        </Text>
        <Members members={od.members} />
      </div>
      {od.list.map((item, j) => (
        <Item key={j} {...item} />
      ))}
    </Box>
  );
}

function Members({ members }) {
  return (
    <div className="ml-2 flex">
      {members.map((name, k) => (
        <Box
          key={k}
          className={cn("flex align-center", { "ml-1": k })}
          height="2rem"
        >
          <Img
            height="100%"
            src={wikiImg(findCharacter({ name }).icon)}
            alt={name}
          />
          {!k && members.length > 1 && (
            <Text className="ml-1" variant="h4">
              +
            </Text>
          )}
        </Box>
      ))}
    </div>
  );
}

function Item({ cate, name, type, index }) {
  let headingType;
  let headingColor;
  let headingText;
  let src;
  let desc;
  if (cate === 1) {
    const { vision, [type]: mods } = findCharacter({ name });
    headingType = "Character";
    headingColor = vision;
    headingText = name;
    ({ src, desc } = findByIndex(mods, index));
  } else if (cate === 2) {
    const wpData = findWeapon({ type, code: name });
    headingType = "Weapon";
    headingColor = wpData.rarity;
    headingText = wpData.name;
    ({ desc } = findByIndex(wpData.buffs, index));
  }
  return (
    <Box className="mt-2 px-3 p-2" borderRadius="0.25rem" bgColor="cardBg1">
      <Text>
        {headingType}:{" "}
        <Span color={headingColor}>
          <b>{headingText}</b>
        </Span>
      </Text>
      {src ? (
        <Text>
          Source: <Span color="lightGold">{src}</Span> ({type.slice(0, -1)})
        </Text>
      ) : null}
      <Text className="mt-2">{desc()}</Text>
    </Box>
  );
}
