import { useState } from "react";
import { Green, Text } from "../../../styledCpns/DataDisplay";
import { Checkbox } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { HowToModal, InfoSign } from "../../CommonCpns";

const options = [
  { field: "sepCharInfo", desc: "Separate Character's Info on each Setup" },
  {
    field: "keepArtStats",
    desc: "Keep Artifact Stats when switching to new Set"
  }
];

export default function Configs({ configs, setConfigs }) {
  const [infoOn, setInfoOn] = useState(false);
  return (
    <Box className="mt-4 p-3 relative" borderRadius="0.5rem" bgColor="cardBg2">
      <AbsoluteBox
        top="0.75rem"
        right="0.75rem"
        onClick={() => setInfoOn(true)}
      >
        <InfoSign selfHover={true} />
      </AbsoluteBox>
      <Text variant="h5" color="orange">
        Configurations
      </Text>
      {options.map(({ field, desc }, i) => (
        <div key={i} className={(i ? "mt-3" : "mt-2") + " flex align-center"}>
          <Checkbox
            scale={1.8}
            className="ml-1 mr-3"
            checked={configs[field]}
            onChange={() =>
              setConfigs((prev) => ({ ...prev, [field]: !prev[field] }))
            }
          />
          <Text>{desc}</Text>
        </div>
      ))}
      {infoOn && (
        <HowToModal
          content={
            <div>
              <Text>
                - Be careful when the Calculator is under the effect of{" "}
                <Green>Separate Character's Info</Green> (level, constellation,
                talents) on each Setup. It can make things complicated.
              </Text>
              <Text className="mt-2">
                - When Separate Character's Info is deactivated. Info on the
                Standard Setup will be used for others.
              </Text>
              <Text className="mt-2">
                - Separate Character's Info will be reset to not activated at
                the start of every session.
              </Text>
            </div>
          }
          close={() => setInfoOn(false)}
        />
      )}
    </Box>
  );
}
