import { useEffect, useState } from "react";
import { FaTimes } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { SORT_ARTS } from "../../app/databaseSlice";
import { initArtStatFilter } from "../../helpers";
import useArtSetFilter from "../../helpers/hooks/useArtSetFilter";
import useArtStatsFilter from "../../helpers/hooks/useArtStatsFilter";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";
import { Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";

export default function BtnBar({ filter, typeFilter, add }) {
  const [filterOn, setFilterOn] = useState(false);
  const isFiltered =
    filter.types.length ||
    filter.sets.length ||
    filter.stats.main !== "All" ||
    filter.stats.subs.some((s) => s !== "All");
  const dispatch = useDispatch();

  return (
    <div className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[add, () => dispatch(SORT_ARTS())]}
      />
      {window.innerWidth >= 600 && typeFilter}
      <Box className="flex pointer">
        <Box
          className={
            (isFiltered ? "pr-2" : "pr-3") + " pl-3 py-1 glow-on-hover"
          }
          bgColor="lightGold"
          borderRadius={isFiltered ? "1rem 0 0 1rem" : "1rem"}
          onClick={() => setFilterOn(true)}
        >
          <Text color="black" bold>
            Filter
          </Text>
        </Box>
        {isFiltered && (
          <Box
            className="pl-2 flex-center glow-on-hover"
            bgColor="red"
            borderRadius="0 1rem 1rem 0"
            style={{ paddingRight: "0.75rem" }}
            onClick={() => {
              filter.setTypes([]);
              filter.setSets([]);
              filter.setStats(initArtStatFilter());
            }}
          >
            <FaTimes />
          </Box>
        )}
      </Box>
      {filterOn && <Filter filter={filter} close={() => setFilterOn(false)} />}
    </div>
  );
}

export const selAllArts = (state) => {
  const { byId, allIds } = state.database.myArts;
  return allIds.map((id) => byId[id]);
};

function Filter({ filter, close }) {
  const [err, setErr] = useState(false);
  const allArts = useSelector(selAllArts);
  const [tempTypes, typeFilter] = useTypeFilter(false, filter.types);
  const [stats, statsFilter] = useArtStatsFilter(filter.stats, err);
  const [sets, setsFilter] = useArtSetFilter(allArts, filter.sets);

  useEffect(() => setErr(false), [stats]);

  return (
    <Modal close={close}>
      <Box
        className="p-3 white-glow-shadow max-width-95"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <div className="pb-2 flex custom-sb">
          <div className="flex-col">
            <div className="pt-2 pb-3">{typeFilter}</div>
            {statsFilter}
          </div>
          {setsFilter}
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Confirm"]}
          variants={["negative", "positive"]}
          handlers={[
            close,
            () => {
              if (hasDupStat(stats)) {
                setErr(true);
                return;
              }
              filter.setTypes(tempTypes);
              filter.setSets(sets);
              filter.setStats(stats);
              close();
            }
          ]}
        />
      </Box>
    </Modal>
  );
}

// [here, ../Readers/ArtFilter]
export function hasDupStat(stats) {
  const existed = [stats.main];
  for (let ss of stats.subs) {
    if (ss === "All") continue;
    if (existed.includes(ss)) return true;
    else existed.push(ss);
  }
  return false;
}
