import { useSelector } from "react-redux";
import {
  getSubWpBCs,
  getWpRange,
  parseData
} from "../../app/calculatorSlice/helpers";
import {
  initCharMCs,
  initCustomMCs
} from "../../app/calculatorSlice/initiators";
import { findCharacter } from "../../data/controllers";
import {
  deepCopy,
  findByIndex,
  findByName,
  indexByCode,
  indexByIndex
} from "../../helpers";
import { restoreAllMCs } from "../../helpers/processors";

export function useImportedSetup() {
  const { myChars, myWps, myArts } = useSelector((state) => state.database);
  const { importInfo, importType } = useSelector((state) => state.ui);
  if (importType === 1) {
    // import from MySetups
    return restoreSetup(importInfo);
  } else if (importType === 2) {
    // import from outside
    return importInfo;
  }
  return createTmSetup(importInfo, myChars, myWps, myArts);
}

function restoreSetup(setup) {
  const { ID, name, type, char, customMCs = initCustomMCs(), target } = setup;
  return { ID, name, type, char, ...restoreAllMCs(setup), customMCs, target };
}

function createTmSetup({ setup, tmIndex }, chars, wps, arts) {
  const newSetup = restoreAllMCs(setup);
  const { name, BCs, DCs } = newSetup.party[tmIndex];
  const charInfo = findByName(chars, name) || { name };
  const { buffs, debuffs } = findCharacter({ name });
  const [char, wp, art] = parseData(charInfo, wps, arts);
  const selfMCs = initCharMCs(name, true);
  for (const ctrl of BCs) {
    const buff = findByIndex(buffs, ctrl.index);
    if (!ctrl.activated || buff.affect === "teammates") continue;
    const i = indexByIndex(selfMCs.BCs, ctrl.index);
    selfMCs.BCs[i].activated = true;
    if (buff.selfLabels) {
      for (let j = 0; j < buff.selfLabels.length; j++) {
        selfMCs.BCs[i].inputs[j] = ctrl.inputs[j];
      }
    }
  }
  for (const ctrl of DCs) {
    if (!ctrl.activated) continue;
    const debuff = findByIndex(debuffs, ctrl.index);
    const i = indexByIndex(selfMCs.DCs, ctrl.index);
    selfMCs.DCs[i].activated = true;
    if (debuff.selfLabels) {
      for (let j = 0; j < debuff.selfLabels.length; j++) {
        selfMCs.DCs[i].inputs[j] = ctrl.inputs[j];
      }
    }
  }
  const ogName = setup.char.name;
  const party = deepCopy(newSetup.party);
  party[tmIndex] = { name: ogName, ...initCharMCs(ogName, false) };

  const subWpMCs = deepCopy(newSetup.subWpMCs);
  const oldWpRange = getWpRange(newSetup.party);
  const newWpRange = getWpRange(party);
  const { weapon: newTmWp } = findCharacter(setup.char);
  if (!oldWpRange[newTmWp])
    subWpMCs.BCs[newTmWp] = getSubWpBCs(newTmWp, wp.code);
  if (!newWpRange[wp.type]) delete subWpMCs.BCs[wp.type];

  const { subBCs, subDCs } = newSetup.art;
  art.subBCs = deepCopy(subBCs);
  art.subDCs = deepCopy(subDCs);
  if (art.sets[0]?.bonusLv) {
    const ctrlI = indexByCode(subBCs, art.sets[0].code);
    if (ctrlI !== -1) {
      art.subBCs.splice(ctrlI, 1);
      if (subBCs[ctrlI].activated) {
        art.BCs[0].activated = true;
        if (subBCs[ctrlI].inputs) art.BCs[0].inputs = [...subBCs[ctrlI].inputs];
      }
    }
  }
  return {
    char,
    selfMCs,
    weapon: wp,
    subWpMCs,
    art,
    party,
    elmtMCs: { ...newSetup.elmtMCs, ampRxn: null, naAmpRxn: null },
    customMCs: initCustomMCs(),
    target: { ...setup.target }
  };
}

export function areEqual(obj1, obj2) {
  for (const k of Object.keys(obj1)) {
    if (obj1[k] !== obj2[k]) return false;
  }
  return true;
}
