import cn from "classnames";
import { useSelector } from "react-redux";
import styled from "styled-components";
import { selSetupById } from "../../app/setupsSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { colors } from "../../theme";
import { TeammateIcon } from "../Calculator/SetupManager/Teammates";

export const Layout = styled.div`
  max-width: 90%;
  height: 90%;
  border-radius: 0.5rem;
  background-color: ${colors.cardBg2};
  overflow-wrap: break-word;
  cursor: default;
  .setup {
    display: flex;
    flex-direction: column;
  }
  .picked {
    box-shadow: 0 0 10px 3px green inset;
  }
  .members {
    margin-top: 0.5rem;
  }
  .icon_wrapper {
    width: 4rem;
    border-radius: 50%;
  }
  .icon_wrapper:first-child {
    box-shadow: 0 0 3px 2px ${colors.orange};
  }
  .icon_wrapper:not(:last-child) {
    margin-right: 1rem;
  }
  @media (min-width: 39.5rem) {
    .setup {
      flex-direction: row;
      align-items: center;
    }
    .setup_name {
      width: 10rem;
      margin-right: 1rem;
    }
    .members {
      margin-top: 0;
    }
  }
`;

export function CombineMenu({ optionIds, pickedIDs, setPickedIDs, notFull }) {
  return (
    <div className="mt-2 pr-3 grow-1 custom-sb">
      <div>
        {!optionIds.length && (
          <Box className="full-h flex-center" height="10rem">
            <Text variant="h6" bold>
              No Setups available for choosing...
            </Text>
          </Box>
        )}
        {optionIds.map((ID, i) => {
          const picked = pickedIDs.includes(ID);
          return (
            <Option
              key={ID}
              ID={ID}
              picked={picked}
              notFull={notFull}
              handleClick={() => {
                if (picked) {
                  setPickedIDs((prev) => {
                    const IDs = [...prev];
                    IDs.splice(IDs.indexOf(ID), 1);
                    return IDs;
                  });
                } else if (notFull) {
                  setPickedIDs((prev) => [...prev, ID]);
                }
              }}
            />
          );
        })}
      </div>
    </div>
  );
}

function Option({ ID, picked, handleClick, notFull }) {
  const { name, char, party } = useSelector(selSetupById(ID));
  return (
    <Box
      className={cn("mb-2 p-3 setup", { picked }, { unavailable: !notFull })}
      borderRadius="0.5rem"
      bgColor="cardBg1"
      onClick={handleClick}
    >
      <div className="setup_name">
        <Text variant="h6" bold color="orange">
          {name}
        </Text>
      </div>
      <div className="members flex">
        {[char.name, ...party.map((tm) => tm.name)].map((name, j) => (
          <div key={j} className="icon_wrapper">
            <TeammateIcon mutable={false} name={name} />
          </div>
        ))}
      </div>
    </Box>
  );
}
