import { useState } from "react";
import { FaFilter } from "react-icons/fa";
import { useSelector } from "react-redux";
import { artNames, artTypes } from "../../configs";
import { findArtifact } from "../../data/controllers";
import { findById, initArtStatFilter } from "../../helpers";
import { Card, Cell, StyledHeading } from "../../styledCpns/Database";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box, HiddenSpace } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn, EquippedChar, NoItems } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { filterArtIds } from "../MyArtifacts";
import ArtFilter from "./ArtFilter";
import { MyArtCard } from "./MyItemCard";
import Thumb from "./Thumb";

export default function ArtInventory({
  artType,
  curPieces,
  owner,
  btnText,
  btnClick,
  close
}) {
  const [filterOn, setFilterOn] = useState(false);
  const [comparing, setComparing] = useState(false);
  const curArt = curPieces[artTypes.indexOf(artType)];

  const data = useSelector((state) => state.database.myArts).filter(
    (art) => art.type === artType
  );
  const [stats, setStats] = useState(initArtStatFilter());
  const [sets, setSets] = useState([]);
  const filteredData = filterArtIds(data, [], sets, stats);
  const [chosenID, setChosenID] = useState(filteredData[0]?.ID || 0);
  const chosenArt = findById(filteredData, chosenID);

  return (
    <Modal standard close={close}>
      <Box height="10%" className="p-2">
        <StyledHeading>
          {window.innerWidth > 380 && (
            <p className="heading_text">{artNames[artType]}</p>
          )}
          <CloseBtn className="close_btn" onClick={close} />
          <IconBtn
            className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
            onClick={() => setFilterOn(!filterOn)}
          >
            <FaFilter />
          </IconBtn>
          <ArtFilter
            filterOn={filterOn}
            arts={data}
            artType={artType}
            filter={{ stats, setStats, sets, setSets }}
            close={() => setFilterOn(false)}
          />
        </StyledHeading>
      </Box>
      <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
        <div className="full-h flex hide-sb">
          {filteredData.length ? (
            <Rack
              data={filteredData}
              chosenID={chosenID}
              setChosenID={setChosenID}
            />
          ) : (
            <Box className="grow-1" minWidth="18.75rem">
              <NoItems type={artNames[artType]} />
            </Box>
          )}
          <div className="flex-col justify-between">
            <Card className="relative">
              {curArt ? (
                <AbsoluteBox
                  className="full-h"
                  top={0}
                  right="calc(100% - 1rem)"
                  zIndex={1}
                >
                  <HiddenSpace
                    className={"full-h hide-sb" + (comparing ? " show" : "")}
                    openWidth="15.75rem"
                  >
                    <Box
                      className="pl-3 pr-2 py-3 full-h flex-col"
                      width="15.75rem"
                      bgColor="cardBg1"
                      borderRadius="0.5rem 0 0 0.5rem"
                    >
                      <MyArtCard art={curArt} mutable={false} ssSpace={3} />
                      <div className="pt-3 grow-1 flex-center">
                        <Text align="center" color="orange">
                          Current equipment
                        </Text>
                      </div>
                    </Box>
                  </HiddenSpace>
                </AbsoluteBox>
              ) : null}
              <Box className="hide-sb" width="16rem" height="24rem">
                {chosenArt ? (
                  <MyArtCard art={chosenArt} mutable={false} ssSpace={3} />
                ) : null}
              </Box>
              {chosenArt && chosenArt.user !== owner ? (
                <ButtonBar
                  className="mt-4"
                  variants={[comparing ? "neutral" : "default", "positive"]}
                  texts={["Compare", btnText]}
                  availables={[Boolean(curArt), true]}
                  handlers={[
                    () => {
                      if (curArt) setComparing(!comparing);
                    },
                    () => {
                      btnClick(chosenArt);
                      close();
                    }
                  ]}
                />
              ) : null}
            </Card>
            {chosenArt?.user ? <EquippedChar user={chosenArt.user} /> : null}
          </div>
        </div>
      </Box>
    </Modal>
  );
}

function Rack({ data, chosenID, setChosenID }) {
  const [clickedID, setClickedID] = useState(null);
  return (
    <Box
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      minWidth="18.75rem"
    >
      <div className="flex-wrap">
        {data.map(({ code, type, ID, user, rarity, level }, i) => {
          const { icon } = findArtifact({ code, type });
          return (
            <Cell key={ID}>
              <Thumb
                index={i}
                item={{ user, rarity, level, icon }}
                chosen={ID === chosenID}
                clicked={ID === clickedID}
                handleMouseDown={() => setClickedID(ID)}
                choose={() => {
                  setClickedID(null);
                  setChosenID(ID);
                }}
              />
            </Cell>
          );
        })}
      </div>
    </Box>
  );
}
