import Picker from "./index";
import weapons from "../../data/weapons";
import artifacts from "../../data/artifacts";
import { initArtPiece, initWeapon } from "../../app/calculatorSlice/initiators";

export function WpPicker({ wpType, needMassAdd, pick, close }) {
  const data = weapons[wpType].map(({ code, name, beta, icon, rarity }) => ({
    code,
    name,
    beta,
    icon,
    rarity
  }));
  return (
    <Picker
      needMassAdd={needMassAdd}
      data={data}
      dataType={wpType}
      close={close}
      pick={({ code }) => pick(initWeapon({ type: wpType, code }))}
    />
  );
}

export function ArtPicker({ artType, needMassAdd, close, pick }) {
  const gold = [];
  const purple = [];
  for (const set of artifacts) {
    const { code, beta, name } = set;
    for (const rarity of set.variants) {
      const { icon } = set[artType];
      if (rarity === 5) {
        gold.push({ code, beta, name, icon, rarity });
      } else {
        purple.push({ code, beta, name, icon, rarity });
      }
    }
  }
  return (
    <Picker
      needMassAdd={needMassAdd}
      data={gold.concat(purple)}
      dataType={artType}
      close={close}
      pick={({ code, rarity }) =>
        pick(initArtPiece({ type: artType, code, rarity }))
      }
    />
  );
}
