import { getResMult } from "../../../calculators/helpers";
import { physAndElmts, rdAndIg } from "../../../configs";
import { round2 } from "../../../helpers";
import { lib } from "../../../library";
import { Green, Text } from "../../../styledCpns/DataDisplay";
import { getTotal } from "./helpers";
import { Columns, Divider, heading, Lesser } from "./styles";

export default function Debuffs({ tracker, target }) {
  const content = [];
  const rdMult = {};
  for (const field of rdAndIg) {
    const total = getTotal(tracker[field]);
    if (!total) continue;
    const [front, back] = field.split("_");
    let xtraTxt;
    if (back === "rd") {
      xtraTxt = "RES Reduction";
      rdMult[field] = total;
    } else {
      xtraTxt = "DEF Ignore";
    }
    content.push(
      <div key={field}>
        {heading(`${lib[front] || front} ${xtraTxt}`, `${total}%`)}
        <div className="px-2">
          {tracker[field].map(({ desc, value }, i) => (
            <Lesser key={i}>
               {desc}: <Green>{value}%</Green>
            </Lesser>
          ))}
        </div>
      </div>
    );
  }
  return (
    <div>
      {!!content.length && (
        <>
          <Columns>{content}</Columns>
          <Divider />
        </>
      )}
      <Text variant="h5" color="orange">
        Resistance Multipliers
      </Text>
      <Columns>
        {physAndElmts.map((type) => {
          const base = target[`${type} RES`];
          const reduction = rdMult[`${type}_rd`] || 0;
          const actual = (base - reduction) / 100;
          let text =
            actual < 0
              ? `1 - (${actual} / 2)`
              : actual >= 0.75
              ? `1 / (4 * ${actual} + 1)`
              : `1 - ${actual}`;
          const mult = getResMult(target, rdMult, type);
          return (
            <div key={type}>
              {heading(type, mult)}
              <div className="px-2">
                <Lesser>
                   RES: base {base}% - reduction {reduction}% ={" "}
                  {round2(actual * 100)}% or <Green>{actual}</Green>
                </Lesser>
                <Lesser>
                   Equation: <Green>{text}</Green>
                </Lesser>
              </div>
            </div>
          );
        })}
      </Columns>
    </div>
  );
}
