export const lib = {
  NA: "Normal Attack",
  CA: "Charged Attack",
  PA: "Plunging Attack",
  ES: "Elemental Skill",
  EB: "Elemental Burst",
  cRate: "CRIT Rate",
  cDmg: "CRIT DMG",
  flat: "Flat Bonus",
  pct: "Percent Bonus",
  mult: "Extra Multiplier",
  sMult: "Special Multiplier"
};

export function getAcro(word) {
  for (const acro in lib) {
    if (lib[acro] === word) {
      return acro;
    }
  }
}
