import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { calculator } from "../../app/calculatorSlice";
import { END_IMPORT } from "../../app/uiSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { Checkbox } from "../../styledCpns/Inputs";
import { ButtonBar, Confirmation, SeeDetails } from "../CommonCpns";
import { StyledTable } from "../CommonCpns/DmgTables/styles";
import { Collapse } from "../CommonCpns/HiddenSpace";
import Modal from "../CommonCpns/Modal";
import { ImportOpts } from "./styles";

export default function ImportPending({
  imported,
  code,
  startSession,
  importSetup
}) {
  const dispatch = useDispatch();

  if (code === 1 || code === 2) {
    return (
      <Confirmation
        message={
          (code === 1
            ? "We're calculating another Character."
            : "The number of Setups on Calculator has reach the limit of 4.") +
          " Start a new session?"
        }
        confirm={startSession}
        close={() => dispatch(END_IMPORT())}
      />
    );
  }
  return (
    <OverrideOpts code={code} imported={imported} importSetup={importSetup} />
  );
}

function OverrideOpts({ code, imported, importSetup }) {
  const { char, target } = useSelector(calculator);
  const [ticked, setTicked] = useState([false, false]);
  const [expanded, setExpanded] = useState(-1);
  const dispatch = useDispatch();

  const close = () => dispatch(END_IMPORT());
  return (
    <Modal close={close}>
      <ImportOpts className="p-3 relative">
        <div className="py-2">
          <Text variant="h5" align="center">
            We detect difference(s) between the Calculator and this Setup.
            Choose what you want to overwrite.
          </Text>
          <div>
            {["Character's Info.", "Target's Info."].map((text, i) =>
              code >= 300 || code % 10 === i ? (
                <Info
                  key={i}
                  mt={expanded ? "mt-3" : "mt-2"}
                  text={text}
                  checked={ticked[i]}
                  check={() =>
                    setTicked((prev) => {
                      const result = [...prev];
                      result[i] = !result[i];
                      return result;
                    })
                  }
                  isExpanded={expanded === i}
                  toggle={() => setExpanded(expanded === i ? -1 : i)}
                  obj1={i ? target : char}
                  obj2={i ? imported.target : imported.char}
                />
              ) : null
            )}
          </div>
        </div>
        <ButtonBar
          className={expanded === code % 10 ? "mt-2" : "mt-3"}
          texts={["Cancel", "Confirm"]}
          handlers={[close, () => importSetup(...ticked)]}
        />
      </ImportOpts>
    </Modal>
  );
}

function Info(props) {
  const { isExpanded, obj1 } = props;
  return (
    <div className={props.mt}>
      <div className="px-5 flex align-center">
        <Checkbox checked={props.checked} onChange={props.check} />
        <Text className="ml-3" variant="h6">
          {props.text}
        </Text>
        <SeeDetails
          className={"ml-2" + (isExpanded ? " active" : "")}
          variant="h6"
          onClick={props.toggle}
        />
      </div>
      <Collapse in={isExpanded}>
        <div className="pt-2 flex justify-center">
          <div className="table_wrapper">
            <StyledTable>
              <tbody>
                <tr>
                  <th></th>
                  <th className="lightGold-text">Old</th>
                  <th className="lightGold-text">New</th>
                </tr>
                {Object.keys(obj1).map((type, i) =>
                  type === "ascension" ? null : (
                    <tr key={i}>
                      <td style={{ textTransform: "capitalize" }}>{type}</td>
                      <td>
                        {Array.isArray(obj1[type])
                          ? `[${obj1[type].join(", ")}]`
                          : obj1[type]}
                      </td>
                      <td>{props.obj2[type]}</td>
                    </tr>
                  )
                )}
              </tbody>
            </StyledTable>
          </div>
        </div>
      </Collapse>
    </div>
  );
}
