import styled from "styled-components";
import { colors } from "../../theme";

export const NavRight = styled.div`
  background-color: ${colors.cardBg3};
  .last-btn {
    padding: 0.25rem 0.5rem;
  }
`;

export const StyledMenu = styled.div`
  border-radius: 0.25rem;
  .last-btn {
    padding: 0.5rem 1.5rem;
  }
  .border {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
`;

export const NavBtn = styled.div`
  display: flex;
  align-items: center;
  background-color: ${colors.cardBg3};
  &:hover {
    color: ${colors.lightGold};
  }
  &.current {
    background-color: ${colors.cardBg1};
    color: ${colors.orange};
  }
`;

export const BarsBtn = styled.button`
  width: 2.5rem;
  height: 2.5rem;
  border: none;
  background-color: ${colors.cardBg3};
  color: white;
  font-size: 1.5rem;
  &.showing {
    background-color: ${colors.cardBg1};
    color: ${colors.lightGold};
  }
`;

export const LoadOptions = styled.div`
  width: 90%;
  max-width: 28rem;
  border-radius: 0.5rem;
  background-color: ${colors.cardBg2};
  .option {
    padding: 0.5rem 1rem 0.75rem;
  }
  .option:hover {
    background-color: ${colors.cardBg1};
  }
`;

export const Divider = styled.div`
  width: 100%;
  border-bottom: 1px solid ${colors.defaultBg};
`;
