import { useState } from "react";
import { FaUserSlash } from "react-icons/fa";
import { useDispatch } from "react-redux";
import { LEVEL_CHAR, REMOVE_CHAR } from "../../../app/databaseSlice";
import { levels, wikiImg } from "../../../configs";
import { Card } from "../../../styledCpns/Database";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { Confirmation, StarLine } from "../../CommonCpns";
import StatsTable from "../../Readers/StatsTable";

export default function SectionAttr({
  charData: { name, rarity, vision, icon },
  level,
  ATTRs
}) {
  const [removing, setRemoving] = useState(false);
  const isMobile = window.innerWidth <= 700;
  const dispatch = useDispatch();

  return (
    <Card className="flex-col relative">
      <AbsoluteBox top="1rem" right="1rem">
        <IconBtn
          className="glow-on-hover"
          variant="negative"
          fontSize="1.125rem"
          onClick={() => setRemoving(true)}
        >
          <FaUserSlash />
        </IconBtn>
      </AbsoluteBox>
      <div className="pb-1 flex">
        {isMobile && (
          <Img
            className="mr-3 mb-3"
            width="4.875rem"
            src={wikiImg(icon)}
            alt={name}
          />
        )}
        <div>
          {!isMobile && (
            <Text variant="h1" color={vision}>
              {name}
            </Text>
          )}
          <StarLine rarity={rarity} />
          <div className="ml-1 my-1 flex">
            <Text className="mr-1" variant="h5">
              Level
            </Text>
            <Select
              color={vision}
              fontSize="1.25rem"
              bold
              align="right"
              value={level}
              onChange={(e) =>
                dispatch(LEVEL_CHAR({ name, level: e.target.value }))
              }
            >
              {levels.map((lv, i) => (
                <option key={i}>{lv}</option>
              ))}
            </Select>
          </div>
        </div>
      </div>
      <Box className="grow-1 custom-sb" width="18.75rem" height={0}>
        <StatsTable stats={ATTRs} breakdown={true} />
      </Box>
      {removing && (
        <Confirmation
          message={
            <>
              Remove <b>{name}</b>?
            </>
          }
          confirm={() => dispatch(REMOVE_CHAR(name))}
          close={() => setRemoving(false)}
        />
      )}
    </Card>
  );
}
