import { FaPlus, FaTimes } from "react-icons/fa";
import { wikiImg } from "../../../configs";
import { findCharacter } from "../../../data/controllers";
import { Img } from "../../../styledCpns/DataDisplay";
import { IconBtn } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";

// also used in MySetups
export function TeammateIcon({ name, mutable, handleClick, removeTeammate }) {
  const { icon } = findCharacter({ name });
  return (
    <>
      <Box
        className="zoomin-on-hover overflow-hidden"
        borderRadius="50%"
        bgColor="cardBg3"
      >
        <Img
          className={mutable ? "pointer" : ""}
          src={wikiImg(icon)}
          alt={name}
          width="100%"
          borderRadius="inherit"
          draggable={false}
          onClick={handleClick}
        />
      </Box>
      {mutable && (
        <AbsoluteBox bottom="-0.25rem" right="-0.625rem">
          <IconBtn
            className="glow-on-hover"
            variant="negative"
            size="1.75rem"
            onClick={removeTeammate}
          >
            <FaTimes />
          </IconBtn>
        </AbsoluteBox>
      )}
    </>
  );
}

export function TeammateSlot(props) {
  return (
    <IconBtn
      color="rgb(255, 255, 255, 0.7)"
      bgColor="cardBg3"
      size="100%"
      fontSize="1.5rem"
      className="glow-on-hover"
      onClick={props.handleClick}
    >
      <FaPlus />
    </IconBtn>
  );
}
