import { useState } from "react";
import { FaMinus, FaPlus, FaTrashAlt } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  ADD_CUSTOM_MC,
  calculator,
  CHANGE_CUSTOM_MC_VAL,
  CLEAR_CUSTOM_MCS,
  COPY_CUSTOM_MCS,
  REMOVE_CUSTOM_MC,
} from "../../../../app/calculatorSlice";
import { pctOf } from "../../../../configs";
import { indexByName } from "../../../../helpers";
import { processNumInp } from "../../../../helpers/processors";
import { Text } from "../../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../../styledCpns/Inputs";
import { Box } from "../../../../styledCpns/Layout";
import { ButtonBar } from "../../../CommonCpns";
import Modal from "../../../CommonCpns/Modal";
import { CopySection } from "../../styles";
import { buffCats, buffTypes, debuffTypes, Input, TabBar } from "./Children";

export default function CustomMods({ modType }) {
  const { setups, customMCs, curSetupI } = useSelector(calculator);
  const dispatch = useDispatch();
  const [forgeOn, setForgeOn] = useState(false);
  const mods = customMCs[curSetupI][modType];

  const copyOpts = [];
  if (!mods.length) {
    for (const i in customMCs) {
      if (customMCs[i][modType].length) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <div className="flex-col">
      <div className="mx-auto mt-3 flex">
        {mods.length > 0 && (
          <IconBtn
            className="mr-5 glow-on-hover"
            variant="negative"
            onClick={() => dispatch(CLEAR_CUSTOM_MCS(modType))}
          >
            <FaTrashAlt />
          </IconBtn>
        )}
        {mods.length <= 10 && (
          <IconBtn
            className="glow-on-hover"
            variant="positive"
            onClick={() => setForgeOn(true)}
          >
            <FaPlus />
          </IconBtn>
        )}
      </div>
      {copyOpts.length ? (
        <div className="mt-5">
          <CopySection
            options={copyOpts}
            copy={(name) =>
              dispatch(
                COPY_CUSTOM_MCS({
                  type: modType,
                  targetIndex: indexByName(setups, name),
                })
              )
            }
          />
        </div>
      ) : null}
      {mods.map(({ type, val }, i) => (
        <div key={i} className="mt-4 flex align-center">
          <IconBtn
            className="mr-3 glow-on-hover"
            variant="negative"
            fontSize="1.25rem"
            onClick={() =>
              dispatch(REMOVE_CUSTOM_MC({ type: modType, index: i }))
            }
          >
            <FaMinus />
          </IconBtn>
          <div className="grow-1 flex align-center">
            <Text className="pr-2">{type}</Text>
            <Input
              className="ml-auto p-2"
              value={val}
              onChange={(e) =>
                dispatch(
                  CHANGE_CUSTOM_MC_VAL({
                    type: modType,
                    inpIndex: i,
                    value: processNumInp(e.target.value, val, 999),
                  })
                )
              }
            />
          </div>
        </div>
      ))}
      {forgeOn && <Forge modType={modType} close={() => setForgeOn(false)} />}
    </div>
  );
}

function initState(forBCs) {
  const state = { type: forBCs ? "ATK%" : "DEF reduction", val: 0 };
  if (forBCs) {
    state.cat = "Attributes";
  }
  return state;
}

function Forge({ modType, close }) {
  const forBCs = modType === "BCs";
  const [state, setState] = useState(initState(forBCs));
  const { cat, type, val } = state;
  const options = forBCs ? buffTypes[cat] : debuffTypes;

  const dispatch = useDispatch();
  return (
    <Modal close={close}>
      <Box
        className="p-3 flex-col white-glow-shadow max-width-95"
        bgColor="cardBg1"
        borderRadius="0.5rem"
      >
        {forBCs && <TabBar state={state} setState={setState} />}
        <div
          className={(forBCs ? "mt-5" : "mt-3") + " mx-auto flex align-center"}
        >
          <Select
            className="pr-2"
            color="white"
            value={type}
            onChange={(e) =>
              setState((prev) => ({ ...prev, type: e.target.value }))
            }
          >
            {options.map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
          <Input
            className="ml-3 p-2"
            value={val}
            onChange={(e) =>
              setState((prev) => ({
                ...prev,
                val: processNumInp(e.target.value, val, 999),
              }))
            }
          />
          <Text className="ml-2">
            {!forBCs || ["Talents", "Reactions"].includes(cat)
              ? "%"
              : pctOf(type)}
          </Text>
        </div>
        <ButtonBar
          className="mt-5"
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              const newMC = { type, val };
              if (forBCs) {
                newMC.catKey = buffCats.indexOf(cat);
              }
              dispatch(ADD_CUSTOM_MC({ type: modType, newMC }));
              close();
            },
          ]}
        />
      </Box>
    </Modal>
  );
}
