import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { REMOVE_ART, selArtById, SWAP_ART_USER } from "../../app/databaseSlice";
import { findArtifact, findCharacter } from "../../data/controllers";
import { Card } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, EquippedChar } from "../CommonCpns";
import Picker from "../Picker";
import { MyArtCard } from "../Readers/MyItemCard";

export default function RightSection({ chosenID, ids, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(false);
  const [removing, setRemoving] = useState(false);
  const [newUser, setNewUser] = useState(null);
  const art = useSelector(selArtById(chosenID));
  const dispatch = useDispatch();

  const swap = (name) =>
    dispatch(
      SWAP_ART_USER({
        newUser: name,
        artID: art.ID,
        artType: art.type,
        oldUser: art.user
      })
    );

  return (
    <div className="flex-col justify-between">
      <Card>
        <Box className="hide-sb" width="18.75rem" height="25rem">
          {art ? <MyArtCard art={art} mutable={true} /> : null}
        </Box>
        {art ? (
          <ButtonBar
            className="mt-3"
            texts={["Remove", "Equip"]}
            handlers={[() => setRemoving(true), () => setPickerOn(true)]}
          />
        ) : null}
      </Card>
      {art?.user ? <EquippedChar user={art.user} /> : null}
      {pickerOn && (
        <CharPicker
          user={art.user}
          setNewUser={setNewUser}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {removing && (
        <ConfirmRemove
          art={art}
          chooseNewID={() => {
            const index = ids.indexOf(art.ID);
            if (index !== -1 && ids.length > 1) {
              const move = index < ids.length - 1 ? 1 : -1;
              setChosenID(ids[index + move]);
            }
          }}
          close={() => setRemoving(false)}
        />
      )}
      {newUser ? (
        <Confirmation
          message={
            <>
              <b>{art.user}</b> is currently using "
              <b>{findArtifact(art).name}</b>". Swap?
            </>
          }
          close={() => setNewUser(null)}
          confirm={() => swap(newUser)}
        />
      ) : null}
    </div>
  );
}

function CharPicker({ user, setNewUser, swap, close }) {
  const { myChars } = useSelector((state) => state.database);
  const data = [];
  for (const char of myChars) {
    const { name, constellation } = char;
    if (name !== user) {
      const { code, icon, rarity, vision, weapon } = findCharacter(char);
      data.push({ code, name, icon, rarity, vision, weapon, constellation });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        if (user) {
          setNewUser(name);
        } else {
          swap(name);
        }
      }}
    />
  );
}

function ConfirmRemove({ art: { ID, user, code, type }, chooseNewID, close }) {
  const dispatch = useDispatch();
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{findArtifact({ code, type }).name}</b>
          "?{" "}
          {Boolean(user) && (
            <>
              It is currently used by <b>{user}</b>.
            </>
          )}
        </>
      }
      close={close}
      confirm={() => {
        dispatch(REMOVE_ART({ ID, user, type }));
        chooseNewID();
      }}
    />
  );
}
