import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_MCS_INPUT,
  CHANGE_TM_MCS_INPUT,
  TOGGLE_MCS,
  TOGGLE_TM_MCS
} from "../../../../app/calculatorSlice";
import { getThisChar } from "../../../../app/calculatorSlice/helpers";
import { findCharacter } from "../../../../data/controllers";
import { findByIndex } from "../../../../helpers";
import { processNumInp } from "../../../../helpers/processors";
import { Text } from "../../../../styledCpns/DataDisplay";
import { CharModSetter, NoContent, Section } from "../CmnCpns";

export function SelfBuffs({ partyData }) {
  const { char, charData, selfMCs, curSetupI, allATTRs } = useSelector(
    calculator
  );
  const { buffs } = findCharacter(char);
  const thisChar = getThisChar(char, curSetupI);
  const dispatch = useDispatch();
  const content = [];

  selfMCs[curSetupI].BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = findByIndex(buffs, index);
    if (buff.isGranted(thisChar)) {
      content.push(
        <Section
          key={bcIndex}
          checked={activated}
          handleCheck={() =>
            dispatch(
              TOGGLE_MCS({
                cat: "selfMCs",
                type: "BCs",
                mcIndex: bcIndex,
                activated: !activated
              })
            )
          }
          heading={buff.src}
          desc={buff.desc({
            toSelf: true,
            ATTRs: allATTRs[curSetupI],
            char: thisChar,
            charBCs: selfMCs[curSetupI].BCs,
            inputs,
            charData,
            partyData
          })}
          setters={
            <SelfSetterSection buff={buff} inputs={inputs} bcIndex={bcIndex} />
          }
        />
      );
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SelfSetterSection({ buff, inputs, bcIndex }) {
  const dispatch = useDispatch();
  if (!buff.selfLabels) return null;
  const path = (inpIndex) => ({
    cat: "selfMCs",
    type: "BCs",
    mcIndex: bcIndex,
    inpIndex
  });
  return (
    <CharModSetter
      labels={buff.selfLabels}
      mod={buff}
      inputs={inputs}
      startsAt0={buff.startsAt0}
      handleText={(value, i) =>
        dispatch(
          CHANGE_MCS_INPUT({
            ...path(i),
            value: processNumInp(value, inputs[i], buff.maxs[i])
          })
        )
      }
      handleCheck={(i) =>
        dispatch(CHANGE_MCS_INPUT({ ...path(i), value: !inputs[i] }))
      }
      handleSelect={(value, i) =>
        dispatch(
          CHANGE_MCS_INPUT({
            ...path(i),
            value: isNaN(value) ? value : +value
          })
        )
      }
    />
  );
}

export function PartyBuffs({ partyData }) {
  const { parties, curSetupI } = useSelector(calculator);
  const content = [];
  parties[curSetupI].forEach((teammate, tmIndex) => {
    if (teammate && teammate.BCs.length)
      content.push(
        <TeammateBuffs
          key={tmIndex}
          teammate={teammate}
          tmIndex={tmIndex}
          partyData={partyData}
        />
      );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function TeammateBuffs({ teammate, tmIndex, partyData }) {
  const { char, charData } = useSelector(calculator);
  const { buffs, vision } = findCharacter(teammate);
  const dispatch = useDispatch();
  const subContent = [];

  teammate.BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = findByIndex(buffs, index);
    const path = {
      tmIndex,
      type: "BCs",
      mcIndex: bcIndex
    };
    subContent.push(
      <Section
        key={bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(TOGGLE_TM_MCS({ ...path, activated: !activated }))
        }
        heading={buff.src}
        desc={buff.desc({
          toSelf: false,
          charBCs: teammate.BCs,
          inputs,
          char,
          charData,
          partyData
        })}
        setters={<TmSetterSection buff={buff} inputs={inputs} path={path} />}
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        bold
        color={vision}
        align="center"
      >
        {teammate.name.toUpperCase()}
      </Text>
      {subContent}
    </>
  );
}

function TmSetterSection({ buff, inputs, path }) {
  const dispatch = useDispatch();
  if (!buff.labels) return null;
  return (
    <CharModSetter
      labels={buff.labels}
      mod={buff}
      inputs={inputs}
      handleText={(value, i) =>
        dispatch(
          CHANGE_TM_MCS_INPUT({
            ...path,
            inpIndex: i,
            value: processNumInp(value, inputs[i], buff.maxs[i])
          })
        )
      }
      handleCheck={(inpIndex) =>
        dispatch(
          CHANGE_TM_MCS_INPUT({
            ...path,
            inpIndex,
            value: !inputs[inpIndex]
          })
        )
      }
      handleSelect={(value, inpIndex) =>
        dispatch(
          CHANGE_TM_MCS_INPUT({
            ...path,
            inpIndex,
            value: isNaN(value) ? value : +value
          })
        )
      }
    />
  );
}
