import styled from "styled-components";
import { elmtDmgBnes, physAndElmts, reactions } from "../../../../configs";
import { artSSTypes } from "../../../../data/artifacts/config";
import { Text } from "../../../../styledCpns/DataDisplay";
import { TextInput } from "../../../../styledCpns/Inputs";
import { Box } from "../../../../styledCpns/Layout";
import { colors } from "../../../../theme";

export const Input = (props) => (
  <TextInput
    width="4rem"
    borderRadius="0.25rem"
    fontSize="1.125rem"
    align="right"
    {...props}
  />
);

export function TabBar({ state, setState }) {
  return (
    <StyledTabBar>
      {buffCats.map((cat) => {
        const chosen = state.cat === cat;
        return (
          <Box
            key={cat}
            className={
              "px-3 py-2 tab " + cat + (chosen ? " chosen" : "") + " pointer"
            }
            onClick={() => {
              if (!chosen)
                setState((prev) => ({
                  cat,
                  type: buffTypes[cat][0],
                  val: prev.val
                }));
            }}
          >
            <Text
              variant="h6"
              color={chosen ? "black" : "defaultBg"}
              bold
              align="center"
            >
              {cat}
            </Text>
          </Box>
        );
      })}
    </StyledTabBar>
  );
}

// these 3 also used in encode & decode
export const buffCats = ["Attributes", "Elements", "Talents", "Reactions"];

export const buffTypes = {
  Attributes: artSSTypes.concat("Shield Strength", "Healing Bonus"),
  Elements: elmtDmgBnes.concat("Physical DMG Bonus"),
  Talents: [
    "Normal Attack",
    "Charged Attack",
    "Plunging Attack",
    "Elemental Skill",
    "Elemental Burst"
  ],
  Reactions: reactions
};

// also [../../Settings/Tracker.helpers]
export const debuffTypes = ["DEF reduction"].concat(
  physAndElmts.map((e) => e + " RES reduction")
);

const StyledTabBar = styled.div`
  display: flex;
  flex-direction: column;
  .tab {
    background-color: ${colors.cardBg3};
  }
  .tab:hover {
    box-shadow: 0 0 0 2px ${colors.defaultBg} inset;
  }
  .tab.chosen {
    background-color: ${colors.defaultBg};
  }
  .Attributes {
    border-radius: 0.5rem 0.5rem 0 0;
  }
  .Reactions {
    border-radius: 0 0 0.5rem 0.5rem;
  }
  @media (min-width: 500px) {
    flex-direction: row;
    .Attributes {
      border-radius: 0.5rem 0 0 0.5rem;
    }
    .Reactions {
      border-radius: 0 0.5rem 0.5rem 0;
    }
  }
`;
