import { FaArrowAltCircleUp } from "react-icons/fa";
import { pctOf, wikiImg } from "../../../configs";
import { artMS } from "../../../data/artifacts/config";
import { findArtifact } from "../../../data/controllers";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { btnState } from "../../../styledCpns/helpers";
import { Button, IconBtn, Select } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box, GradientBox } from "../../../styledCpns/Layout";
import { BetaMark } from "../../CommonCpns";
import ArtSubstats from "./ArtSubstats";

export default function ArtifactCard({
  art,
  mutable,
  enhance,
  up4,
  toMax,
  changeMSType,
  changeSSType,
  changeSSVal,
  ssSpace,
}) {
  const { beta, name, icon } = findArtifact(art);
  const { rarity = 5 } = art;
  const msType = art.mainSType;
  const availableMSTypes = artMS[art.type];
  const isFoP = ["flower", "plume"].includes(art.type);
  const msValues = availableMSTypes[msType][rarity];
  return (
    <div className="full-w">
      <Box className="px-3 pt-1" bgColor={rarity}>
        <Text className="ellipsis" variant="h5" bold color="black">
          {name}
        </Text>
      </Box>
      <div className="mt-3 mx-3 flex">
        <LevelSection
          mutable={mutable}
          rarity={rarity}
          level={art.level}
          enhance={enhance}
          up4={up4}
          toMax={toMax}
        />
        <GradientBox className="relative" borderRadius="0.5rem" bgImg={rarity}>
          <Img
            src={beta ? icon : wikiImg(icon)}
            alt=""
            width="7rem"
            draggable={false}
          />
          {beta && (
            <AbsoluteBox bottom={0} right={0}>
              <BetaMark />
            </AbsoluteBox>
          )}
        </GradientBox>
      </div>

      <div className="mt-2 ml-4">
        {isFoP || !mutable ? (
          <Text className={(!mutable ? "pl-2" : "pl-5") + " pt-1"} variant="h6">
            {msType}
          </Text>
        ) : (
          <div className="py-1">
            <Select
              className="pl-5 appearance-none bg-for-icon white-arrow-bg"
              fontSize="1.125rem"
              color="white"
              value={msType}
              onChange={changeMSType}
            >
              {Object.keys(availableMSTypes).map((type) => (
                <option key={type}>{type}</option>
              ))}
            </Select>
          </div>
        )}
        <Text
          className={!mutable ? "pl-2" : "pl-5"}
          variant="h3"
          color={rarity}
        >
          {msValues[art.level] + pctOf(msType)}
        </Text>
      </div>

      <div className={mutable ? "px-2" : ""}>
        <ArtSubstats
          mutable={mutable}
          substats={art.subS}
          changeSSType={changeSSType}
          changeSSVal={changeSSVal}
          ssSpace={ssSpace}
          msType={msType}
          rarity={rarity}
        />
      </div>
    </div>
  );
}

function LevelSection({ mutable, level, rarity, enhance, up4, toMax }) {
  if (!mutable) {
    return (
      <Box width="9.75rem">
        <Box
          className="px-2 pt-2 pb-1"
          width="3.05rem"
          bgColor="cardBg3"
          borderRadius="50%"
        >
          <Text variant="h6" bold color={rarity}>
            {"+" + level}
          </Text>
        </Box>
      </Box>
    );
  }
  const maxLv = rarity === 5 ? 20 : 16;
  const atMax = level === maxLv;
  const state = btnState(!atMax);
  const btnColor = atMax ? "defaultBg" : "orange";
  return (
    <div className="mr-4 pr-2 grow-1 flex justify-between">
      <div>
        <Box bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            bold
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + level}
            onChange={enhance}
          >
            {[...Array(maxLv + 1).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
      </div>
      <div className="mt-1 flex-col align-center">
        <IconBtn
          className={state}
          bgColor="black"
          color={btnColor}
          fontSize="2rem"
          size="2rem"
          onClick={up4}
        >
          <FaArrowAltCircleUp />
        </IconBtn>
        <Button
          className={`mt-4 ${state}`}
          padding="0.25rem 0.375rem"
          borderRadius="0.25rem"
          bgColor={btnColor}
          fontWeight={900}
          onClick={toMax}
        >
          MAX
        </Button>
      </div>
    </div>
  );
}
