import { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../data/characters";
import {
  ADD_CHAR,
  CHOOSE_MY_CHAR,
  selAllCharNames
} from "../../app/databaseSlice";
import { Button, Select } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import Info from "./Info";
import TopBar from "./TopBar";

export default function MyCharacters() {
  const [pickerOn, setPickerOn] = useState(false);
  const charNames = useSelector(selAllCharNames);
  const charListRef = useRef();

  return (
    <Box className="full-h flex-col" bgColor="cardBg3">
      {window.innerWidth <= 700 ? (
        <Box className="py-3 flex" bgColor="cardBg2">
          {charNames.length ? (
            <CharSelect charNames={charNames} setPickerOn={setPickerOn} />
          ) : (
            <Button
              className="mx-auto"
              variant="positive"
              onClick={() => setPickerOn(true)}
            >
              Add New Characters
            </Button>
          )}
        </Box>
      ) : (
        <TopBar setPickerOn={setPickerOn} charListRef={charListRef} />
      )}
      <div className="grow-1 flex-center">
        <Box className="full-w flex justify-center" height="98%">
          {!!charNames.length && <Info />}
        </Box>
      </div>
      {pickerOn && (
        <CharPicker
          charListRef={charListRef}
          close={() => setPickerOn(false)}
        />
      )}
    </Box>
  );
}

function CharSelect({ charNames, setPickerOn }) {
  const chosen = useSelector((state) => state.database.chosenChar);
  const dispatch = useDispatch();
  return (
    <div className="full-w flex-center relative">
      <Select
        className="px-2 py-1"
        color="black"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.5rem"
        bold
        align="center"
        value={chosen}
        onChange={(e) => dispatch(CHOOSE_MY_CHAR(e.target.value))}
      >
        {charNames.map((name, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      <Button
        className="ml-4"
        variant="positive"
        onClick={() => setPickerOn(true)}
      >
        Add
      </Button>
    </div>
  );
}

function CharPicker({ charListRef, close }) {
  const charNames = useSelector(selAllCharNames);
  const wishlist = getRemainingChars(charNames);
  const dispatch = useDispatch();
  return (
    <Picker
      needMassAdd={true}
      data={wishlist}
      dataType="Character"
      close={close}
      pick={({ name, weapon }) => {
        dispatch(ADD_CHAR({ name, weapon }));
        if (charListRef.current) charListRef.current.scrollLeft = 0;
      }}
    />
  );
}

function getRemainingChars(existedNames) {
  const result = [];
  for (const { name, code, beta, icon, rarity, vision, weapon } of characters) {
    if (!existedNames.includes(name)) {
      result.push({ name, code, beta, icon, rarity, vision, weapon });
    }
  }
  return result;
}
