import { addAndTrack } from "../../../calculators/helpers";
import { applyPct, findByCode } from "../../../helpers";
import { Green } from "../../../styledCpns/DataDisplay";
import { wpSimpleAnTmaker } from "../config";
import { LiyueSeries } from "../series";

const GoldClaymores = [
  {
    code: 53,
    name: "Song of Broken Pines",
    icon: "d/dd/Weapon_Song_of_Broken_Pines",
    rarity: 5,
    mainStatScale: 49,
    subStat: { type: "Physical DMG Bonus", scale: "4.5%" },
    addBnes: wpSimpleAnTmaker("ATTRs", "ATK%", 4),
    buffs: [
      {
        index: 0,
        affect: "party",
        addBnes: wpSimpleAnTmaker("ATTRs", ["Normal ATK SPD", "ATK%"], [3, 5]),
        desc: ({ wpRfm }) =>
          findByCode(GoldClaymores, 53).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Rebel's Banner Hymn",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            A part of the "Millennial Movement" that wanders amidst the winds.
            Increases <Green>ATK</Green> by <Green b>{12 + wpRfm * 4}%</Green>,
            and when Normal or Charged Attacks hit opponents, the character
            gains a Sigil of Whispers. This effect can be triggered once every
            0.3s. When you possess four Sigils of Whispers, all of them will be
            consumed and all nearby party members will obtain the "Millennial
            Movement: Banner-Hymn" effect for 12s.
          </>
        ),
        buff: (
          <>
            "Millennial Movement: Banner-Hymn" increases{" "}
            <Green>Normal ATK SPD</Green> by <Green b>{9 + wpRfm * 3}%</Green>{" "}
            and increases <Green>ATK</Green> by{" "}
            <Green b>{15 + wpRfm * 5}%</Green>. Once this effect is triggered,
            you will not gain Sigils of Whispers for 20s.
          </>
        ),
        others: (
          <>
            Of the many effects of the "Millennial Movement", buffs of the same
            type will not stack.
          </>
        )
      }
    ]
  },
  {
    code: 54,
    name: "Skyward Pride",
    icon: "0/0b/Weapon_Skyward_Pride",
    rarity: 5,
    mainStatScale: 48,
    subStat: { type: "Energy Recharge", scale: "8%" },
    addBnes: wpSimpleAnTmaker("hitBnes", "All.pct", 2),
    pasvName: "Sky-ripping Dragon Spine",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <Green>all DMG</Green> by{" "}
            <Green b>{6 + wpRfm * 2}%</Green>. After using an Elemental Burst,
            Normal or Charged Attack, on hit, creates a vacuum blade that does{" "}
            <Green b>{60 + wpRfm * 20}%</Green> of <Green>ATK</Green> as DMG to
            opponents along its path. Lasts for 20s or 8 vacuum blades.
          </>
        )
      }
    ]
  },
  {
    code: 55,
    name: "The Unforged",
    icon: "f/f7/Weapon_The_Unforged",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "ATK%", scale: "10.8%" },
    ...LiyueSeries
  },
  {
    code: 56,
    name: "Wolf's Gravestone",
    icon: "4/4f/Weapon_Wolf%27s_Gravestone",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "ATK%", scale: "10.8%" },
    addBnes: wpSimpleAnTmaker("ATTRs", "ATK%", 5),
    buffs: [
      {
        index: 0,
        affect: "party",
        addBnes: wpSimpleAnTmaker("ATTRs", "ATK%", 10),
        desc: ({ wpRfm }) =>
          findByCode(GoldClaymores, 56).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Wolfish Tracker",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <Green>ATK</Green> by <Green b>{15 + wpRfm * 5}%</Green>.
          </>
        ),
        buff: (
          <>
            On hit, attacks against opponents with less than 30% HP increase all
            party members' <Green>ATK</Green> by{" "}
            <Green b>{30 + wpRfm * 10}%</Green> for 12s. Can only occur once
            every 30s.
          </>
        )
      }
    ]
  },
  {
    code: 57,
    name: "Redhorn Stonethresher",
    icon: "d/d4/Weapon_Redhorn_Stonethresher",
    rarity: 5,
    mainStatScale: "44b",
    subStat: { type: "CRIT DMG", scale: "19.2%" },
    addBnes: wpSimpleAnTmaker("ATTRs", "DEF%", 7),
    addFinalBnes: ({ hitBnes, wpRfm, ATTRs, tkDesc, tracker }) => {
      if (hitBnes) {
        const bnValue = applyPct(ATTRs.DEF, 30 + wpRfm * 10);
        addAndTrack(tkDesc, hitBnes, ["NA.flat", "CA.flat"], bnValue, tracker);
      }
    },
    pasvName: "Gokadaiou Otogibanashi",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <Green>DEF</Green> is increased by{" "}
            <Green b>{21 + wpRfm * 7}%</Green>.{" "}
            <Green>Normal and Charged Attack DMG</Green> is increased by{" "}
            <Green b>{30 + wpRfm * 10}%</Green> of <Green>DEF</Green>.
          </>
        )
      }
    ]
  }
];

export default GoldClaymores;
