import { createSelector } from "@reduxjs/toolkit";
import { useState } from "react";
import { FaEllipsisH } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { ADD_WP, SORT_WPS } from "../../app/databaseSlice";
import { weaponIcons } from "../../configs";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";
import { StyledInv } from "../../styledCpns/Database";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { ButtonBar } from "../CommonCpns";
import { Collapse } from "../CommonCpns/HiddenSpace";
import { WpPicker } from "../Picker/ItemPicker";
import PrePicker from "../Picker/PrePicker";
import LeftSection from "./LeftSection";
import RightSection from "./RightSection";

const selFilteredIds = createSelector(
  (state) => state.database.myWps.byId,
  (state) => state.database.myWps.allIds,
  (_, types) => types,
  (byId, allIds, types) => {
    return types.length
      ? allIds.filter((id) => types.includes(byId[id].type))
      : allIds;
  }
);

export default function MyWeapons() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [wpType, setWpType] = useState(null);
  const [types, typeFilter] = useTypeFilter(true);
  const filteredIds = useSelector((state) => selFilteredIds(state, types));
  const [chosenID, setChosenID] = useState(filteredIds[0] || 0);
  const dispatch = useDispatch();

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <BtnBar
          add={() => setPrePickerOn(true)}
          types={types}
          typeFilter={typeFilter}
        />
        <div className="body">
          <LeftSection
            ids={filteredIds}
            chosenID={chosenID}
            setChosenID={setChosenID}
          />
          <RightSection
            ids={filteredIds}
            chosenID={chosenID}
            setChosenID={setChosenID}
          />
        </div>
      </StyledInv>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={weaponIcons}
          pick={(wpType) => {
            setWpType(wpType);
            setPrePickerOn(false);
          }}
        />
      )}
      {wpType && (
        <WpPicker
          needMassAdd={true}
          wpType={wpType}
          close={() => setWpType(null)}
          pick={(item) => {
            const ID = Date.now();
            dispatch(ADD_WP({ ID, item }));
            setChosenID(ID);
          }}
        />
      )}
    </Box>
  );
}

function BtnBar({ add, typeFilter }) {
  const [dropped, setDropped] = useState(false);
  const dispatch = useDispatch();
  return (
    <Box className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[add, () => dispatch(SORT_WPS())]}
      />
      {window.innerWidth >= 500 ? (
        typeFilter
      ) : (
        <IconBtn
          className="ml-1"
          bgColor={dropped ? "green" : "white"}
          onClick={() => setDropped(!dropped)}
        >
          <FaEllipsisH />
        </IconBtn>
      )}
      <AbsoluteBox className="full-w" top="100%" left={0} zIndex={2}>
        <Collapse in={dropped}>
          <Box className="px-3 py-4 cmn-shadow" bgColor="cardBg2">
            {typeFilter}
          </Box>
        </Collapse>
      </AbsoluteBox>
    </Box>
  );
}
