import { createSlice, createSelector } from "@reduxjs/toolkit";
import { resetAndLoad } from "./databaseSlice";

export const setupsSlice = createSlice({
  name: "setup",
  initialState: {
    chosenID: 0,
    mySetups: { byId: {}, allIds: [] }
  },
  reducers: {
    UPLOAD_SETUPS: (state, action) => {
      resetAndLoad(state, "mySetups", action.payload);
      for (const st of action.payload) {
        if (st.type !== "combine") {
          state.chosenID = st.ID;
          break;
        }
      }
    },
    CHOOSE_SETUP: (state, action) => {
      state.chosenID = action.payload;
    },
    REMOVE_SETUP: (state, action) => {
      const removedID = action.payload;
      const { mySetups } = state;
      const { byId, allIds } = mySetups;
      const visibleIDs = allIds.reduce((filtered, id) => {
        if (byId(id).type !== "combined") {
          filtered.push(id);
        }
        return filtered;
      }, []);
      const removedIndexInVisible = visibleIDs.indexOf(removedID);
      const lastIndex = visibleIDs.length - 1;
      const newID =
        removedIndexInVisible === lastIndex
          ? visibleIDs[lastIndex - 1] || 0
          : visibleIDs[removedIndexInVisible + 1];
      delete byId[removedID];
      allIds.splice(allIds.indexOf(removedID), 1);
      state.chosenID = newID;
    },
    COMBINE_SETUPS: (state, action) => {
      const { pickedIDs, name } = action.payload;
      const { mySetups } = state;
      const allIDs = {};
      for (const id of pickedIDs) {
        const setup = mySetups.byId[id];
        setup.type = "combined";
        allIDs[setup.char.name] = id;
      }
      const ID = Date.now();
      mySetups.byId[ID] = {
        name,
        ID,
        type: "complex",
        shownID: pickedIDs[0],
        allIDs
      };
      mySetups.allIds.unshift(ID);
      state.chosenID = ID;
    },
    ADD_TO_COMPLEX: ({ mySetups }, action) => {
      const { complexID, pickedIDs } = action.payload;
      pickedIDs.forEach((ID, i) => {
        mySetups.byId[ID].type = "combined";
        const { name } = mySetups.byId[ID].char;
        mySetups.byId[complexID].allIDs[name] = ID;
      });
    },
    UNCOMBINE_COMPLEX: ({ mySetups: { byId, allIds } }, action) => {
      const complexID = action.payload;
      for (const ID of Object.values(byId[complexID].allIDs)) {
        byId[ID].type = "original";
      }
      delete byId[complexID];
      allIds.splice(allIds.indexOf(complexID), 1);
    },
    SWITCH_SETUP_ON_COMPLEX: (state, action) => {
      const { complexID, newID } = action.payload;
      state.mySetups.byId[complexID].shownID = newID;
    },
    SAVE_SETUP: (state, action) => {
      const { ID, name, data } = action.payload;
      const { byId, allIds } = state.mySetups;
      const existed = byId[ID];
      let newChosenID;
      if (existed?.type === "combined") {
        for (const id of allIds) {
          if (
            byId[id].type === "complex" &&
            byId[id].allIDs[existed.char.name] === ID
          ) {
            newChosenID = id;
            byId[id].shownID = ID;
            break;
          }
        }
      }
      byId[ID] = {
        name,
        ID,
        type: existed ? existed.type : "original",
        ...data
      };
      if (!existed) allIds.unshift(ID);
      state.chosenID = newChosenID || ID;
    }
  }
});

export const {
  UPLOAD_SETUPS,
  CHOOSE_SETUP,
  REMOVE_SETUP,
  COMBINE_SETUPS,
  ADD_TO_COMPLEX,
  UNCOMBINE_COMPLEX,
  SWITCH_SETUP_ON_COMPLEX,
  SAVE_SETUP
} = setupsSlice.actions;

const selActualChosenID = createSelector(
  (state) => state.setups.mySetups.byId,
  (state) => state.setups.chosenID,
  (byId, chosenID) => {
    return chosenID && byId[chosenID].type === "complex"
      ? byId[chosenID].shownID
      : chosenID;
  }
);

export const selActualChosenSetup = createSelector(
  (state) => state.setups.mySetups.byId,
  selActualChosenID,
  (byId, actualChosenID) => byId[actualChosenID]
);

export const selSetupById = (id) => (state) => state.setups.mySetups.byId[id];

export default setupsSlice.reducer;
