import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_MONSTER,
  CHANGE_MONSTER_INPUT,
} from "../../../../app/calculatorSlice";
import monsters from "../../../../data/monsters";
import { findByName } from "../../../../helpers";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Checkbox, Select } from "../../../../styledCpns/Inputs";
import { StyledSelect } from "../CmnCpns";

export default function Monster() {
  const { monster } = useSelector(calculator);
  const dispatch = useDispatch();
  const monData = findByName(monsters, monster.name);
  return (
    <div>
      <Select
        className="mb-3 p-1"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.125rem"
        bold
        value={monster.name}
        onChange={(e) => dispatch(CHANGE_MONSTER(e.target.value))}
      >
        {monsters.map(({ name }, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      {monster.inputs.map((inp, inpIndex) => (
        <InputLine
          key={inpIndex}
          inp={inp}
          inpIndex={inpIndex}
          monData={monData}
        />
      ))}
    </div>
  );
}

function InputLine({ inp, inpIndex, monData }) {
  const dispatch = useDispatch();
  const inpType = monData.inputTypes[inpIndex];
  let inpCpn;
  if (inpType === "variant") {
    inpCpn = (
      <StyledSelect
        value={inp}
        onChange={(e) =>
          dispatch(CHANGE_MONSTER_INPUT({ inpIndex, value: e.target.value }))
        }
      >
        {monData.inputs[0].map((opt, j) => (
          <option key={j}>{opt}</option>
        ))}
      </StyledSelect>
    );
  } else if (inpType === "check") {
    inpCpn = (
      <Checkbox
        className="mr-1"
        checked={inp}
        onChange={() =>
          dispatch(CHANGE_MONSTER_INPUT({ inpIndex, value: !inp }))
        }
      />
    );
  }
  return (
    <div className="mb-3 flex justify-end align-center">
      <Text className="mr-3">{monData.labels[inpIndex]}</Text>
      {inpCpn}
    </div>
  );
}
