import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  ADD_TO_COMPLEX,
  COMBINE_SETUPS,
  REMOVE_SETUP,
  selSetupById
} from "../../app/setupsSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { TextInput } from "../../styledCpns/Inputs";
import { ButtonBar, Confirmation, ShareLayout } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { CombineMenu, Layout } from "./CombineLayout";

export function RemoveCtrl({ removedID, close }) {
  const { name } = useSelector(selSetupById(removedID));
  const dispatch = useDispatch();
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{name}</b>"?
        </>
      }
      confirm={() => {
        dispatch(REMOVE_SETUP(removedID));
      }}
      close={close}
    />
  );
}

export function ShareCtrl({ setup: { name, ID, type, ...data }, close }) {
  return <ShareLayout setupName={name} data={data} close={close} />;
}

export function CombineCtrl({ close }) {
  const [pickedIDs, setPickedIDs] = useState([]);
  const [inp, setInp] = useState("Team Setup");
  const [err, setErr] = useState(false);
  const { byId, allIds } = useSelector((state) => state.setups.mySetups);

  const dispatch = useDispatch();
  useEffect(() => setErr(false), [pickedIDs]);

  const tryCombine = () => {
    try {
      if (!inp) throw new Error("1");
      const mains = [];
      const all = [];
      for (const ID of pickedIDs) {
        const { char, party } = byId[ID];
        if (mains.includes(char.name)) {
          throw new Error();
        } else {
          mains.push(char.name);
        }
        if (!all.includes(char.name)) {
          if (all.length === 4) {
            throw new Error();
          } else {
            all.push(char.name);
          }
        }
        for (let tm of party) {
          if (tm.name && !all.includes(tm.name)) {
            if (all.length === 4) {
              throw new Error();
            } else {
              all.push(tm.name);
            }
          }
        }
      }
      dispatch(COMBINE_SETUPS({ pickedIDs, name: inp }));
      close();
    } catch (e) {
      if (e.message !== "1") setErr(true);
    }
  };
  return (
    <Modal close={close}>
      <Layout className="pl-4 pr-2 py-3 flex-col white-glow-shadow">
        <Text
          className="pr-3"
          color={err ? "lightRed" : "lightGold"}
          align="center"
        >
          {err
            ? "You cannot combine these Setups."
            : "Choose at least 2 setups with the same party members."}
        </Text>
        <CombineMenu
          optionIds={allIds.filter((ID) => {
            const { type, party } = byId[ID];
            return !(type !== "original" || party.some((tm) => !tm));
          })}
          notFull={pickedIDs.length < 4}
          pickedIDs={pickedIDs}
          setPickedIDs={setPickedIDs}
        />
        <div className="pr-3 flex-col">
          <TextInput
            type="text"
            className="mt-4 px-3 py-2"
            borderRadius="0.25rem"
            fontSize="1.25rem"
            align="center"
            value={inp}
            onChange={(e) => {
              const { value } = e.target;
              if (value.length <= 32) setInp(value);
            }}
            onKeyDown={(e) => {
              if (e.key === "Enter" && pickedIDs.length > 1) tryCombine();
            }}
          />
          <ButtonBar
            className="mt-3"
            texts={["Cancel", "Combine"]}
            handlers={[
              close,
              () => {
                if (pickedIDs.length > 1) tryCombine();
              }
            ]}
          />
        </div>
      </Layout>
    </Modal>
  );
}

export function AddCtrl({ addingID, close }) {
  const [pickedIDs, setPickedIDs] = useState([]);
  const [err, setErr] = useState(false);
  const { name, shownID, allIDs } = useSelector(selSetupById(addingID));
  const { char, party } = useSelector(selSetupById(shownID));
  const { byId, allIds } = useSelector((state) => state.setups.mySetups);

  const all = [char.name, ...party.filter((tm) => tm).map((tm) => tm.name)];
  const remains = all.filter((name) => !allIDs[name]);
  const optionIds = allIds.filter((ID) => {
    const { type, party, char } = byId[ID];
    return (
      type === "original" &&
      party.every((tm) => tm && all.includes(tm.name)) &&
      remains.includes(char.name)
    );
  });

  useEffect(() => setErr(false), [pickedIDs]);
  const dispatch = useDispatch();

  const combine = () => {
    if (pickedIDs.length) {
      const existedNames = [];
      for (const pID of pickedIDs) {
        const { name } = byId[pID].char;
        if (existedNames.includes(name)) {
          setErr(true);
          return;
        } else {
          existedNames.push(name);
        }
      }
      dispatch(ADD_TO_COMPLEX({ complexID: addingID, pickedIDs }));
      close();
    }
  };

  return (
    <Modal close={close}>
      <Layout className="pl-4 pr-2 py-3 flex-col white-glow-shadow">
        <Text
          className="pr-3"
          color={err ? "lightRed" : "lightGold"}
          align="center"
        >
          {err ? (
            "These 2 Setups feature the same Character."
          ) : (
            <>
              Choose setups to be combined into "<b>{name}</b>".
            </>
          )}
        </Text>
        <CombineMenu
          optionIds={optionIds}
          notFull={pickedIDs.length < remains.length}
          pickedIDs={pickedIDs}
          setPickedIDs={setPickedIDs}
        />
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Combine"]}
          handlers={[close, combine]}
        />
      </Layout>
    </Modal>
  );
}
