import cn from "classnames";
import { useState } from "react";
import { FaSort } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { SORT_CHARS } from "../../app/databaseSlice";
import { findCharacter } from "../../data/controllers";
import { deepCopy, splitLv } from "../../helpers";
import { Span, Text } from "../../styledCpns/DataDisplay";
import { AbsoluteBox } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { StyledSortList } from "./styles";

export default function CharSorting({ close }) {
  // use reselect
  const processedChars = useSelector((state) => {
    const { byName, allNames } = state.database.myChars;
    return allNames.map((name, index) => {
      const { rarity } = findCharacter({ name });
      return { name, level: byName[name].level, rarity, index };
    });
  });
  const [list, setList] = useState(processedChars);
  const [dragIndex, setDragIndex] = useState(null);
  const [dropIndex, setDropIndex] = useState(null);
  const dispatch = useDispatch();

  const handleDragStart = (e) => {
    setDragIndex(+e.target.id);
  };
  const handleDragEnter = (e) => {
    setDropIndex(+e.target.id);
  };
  const handleDragOver = (e) => {
    e.preventDefault();
  };
  const handleDrop = () => {
    if (dropIndex !== dragIndex) {
      setList((prev) => {
        const newList = deepCopy(prev);
        newList.splice(
          dragIndex < dropIndex ? dropIndex - 1 : dropIndex,
          0,
          newList.splice(dragIndex, 1)[0]
        );
        return newList;
      });
    }
    setDropIndex(null);
  };

  return (
    <Modal close={close}>
      <StyledSortList className="px-2 py-3 white-glow-shadow">
        <AbsoluteBox top="0.5rem" right="0.5rem">
          <CloseBtn className="glow-on-hover" onClick={close} />
        </AbsoluteBox>
        <Text variant="h4" color="orange" align="center">
          Sort by
        </Text>
        <ButtonBar
          className="mt-3"
          distance="ml-3"
          texts={["Name", "Level", "Rarity"]}
          variants={["neutral", "neutral", "neutral"]}
          handlers={[
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                newList.sort((a, b) => a.name.localeCompare(b.name));
                return newList;
              }),
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                return newList.sort((a, b) => {
                  const [fA, sA] = splitLv(a);
                  const [fB, sB] = splitLv(b);
                  if (fA !== fB) {
                    return fB - fA;
                  }
                  return sB - sA;
                });
              }),
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                return newList.sort((a, b) => {
                  return b.rarity - a.rarity;
                });
              })
          ]}
        />
        <div className="list_wrapper mt-3 custom-sb">
          <div>
            {list.map((unit, i) => (
              <div
                key={i}
                id={i}
                className={cn("line px-2 py-1 flex align-center", {
                  "dragged-over": dropIndex === i
                })}
                draggable="true"
                onDragStart={handleDragStart}
                onDragOver={handleDragOver}
                onDragEnter={handleDragEnter}
                onDrop={handleDrop}
              >
                <button className="grab_btn mr-2 no-pointer-events">
                  <FaSort />
                </button>
                <Text className="no-pointer-events">
                  <Span color={unit.rarity}>
                    <strong>{unit.name}</strong>
                  </Span>{" "}
                  (Lv. {unit.level})
                </Text>
              </div>
            ))}
          </div>
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Confirm"]}
          variants={["positive"]}
          handlers={[
            () => {
              dispatch(SORT_CHARS(list));
              close();
            }
          ]}
        />
      </StyledSortList>
    </Modal>
  );
}
