import styled from "styled-components";
import { colors } from "../../theme";

export const StyledTopBar = styled.div`
  position: relative;
  width: 70%;
  max-width: 60%;
  .cell {
    border-bottom: 3px solid transparent;
    &.chosen {
      border-bottom-color: rgb(92, 188, 212);
      .icon_wrapper {
        border-color: rgb(92, 188, 212);
        background-color: rgb(92, 188, 212);
      }
    }
  }
  .icon_wrapper {
    border-radius: 50%;
    border: 3.5px solid rgba(185, 185, 185, 0.3);
    background: rgba(0, 0, 0, 0.3);
  }
  .side_wrapper {
    width: 60px;
    min-width: 60px;
    height: 60px;
    img {
      width: 85px;
      transform: translate(-15px, -32px);
    }
  }
  .beta_wrapper {
    width: 68px;
    min-width: 68px;
    height: 68px;
    img {
      width: 65px;
      transform: translate(-2.5px, -0px);
    }
  }
  .wish_btn {
    position: absolute;
    top: 1rem;
    left: calc(100% + 1.5rem);
    width: 60px;
    border-radius: 50%;
  }
  .wish_btn:hover {
    box-shadow: 0 0 3px 3px #fff;
  }
  @media (min-width: 768px) {
    max-width: 70%;
  }
`;

export const StyledSortList = styled.div`
  width: 90%;
  max-width: 22rem;
  border-radius: 0.5rem;
  background-color: ${colors.cardBg2};
  .list_wrapper {
    height: 60vh;
  }
  .line {
    cursor: default;
    user-select: none;
  }
  .line:hover {
    background-color: ${colors.cardBg1};
  }
  .grab_btn {
    width: 2rem;
    height: 2rem;
    border: none;
    color: ${colors.defaultBg};
    background-color: transparent;
    font-size: 1.25rem;
  }
  .no-pointer-events {
    pointer-events: none;
  }
  .dragged-over {
    border-top: 1px solid white;
  }
`;
