import {
  buffCats,
  buffTypes,
  debuffTypes
} from "../components/Calculator/Modifiers/CustomMods/Children";
import { ampRxnTrigElmts, levels, physAndElmts, rsnNames } from "../configs";
import { allStatsTypes } from "../data/artifacts/config";
import { findCharacter } from "../data/controllers";

export const specialCodes = {
  Pyro: "PR",
  Hydro: "HD",
  Dendro: "DD",
  Electro: "ELT",
  Anemo: "ANM",
  Cryo: "CO",
  Geo: "GO",
  Recitative: "RCTT",
  Aria: "ARA",
  Interlude: "ITL"
};

export default function encodeExportedData(data) {
  const { level, ascension, constellation, ...talents } = data.char;
  return [
    [
      findCharacter(data.char).code,
      levels.indexOf(level),
      constellation,
      ...["Normal Attack", "Elemental Skill", "Elemental Burst"].map(
        (type) => talents[type]
      )
    ].join("&"),
    [getMCsCode(data.selfMCs.BCs), getMCsCode(data.selfMCs.DCs)].join("&"),
    getWpCode(data.weapon),
    getSubWpBCsCode(data.subWpMCs.BCs),
    // subWpDCs can be joined with subWpBCs later by "&"
    getArtCode(data.art),
    getPartyCode(data.party),
    getElmtMCsCode(data.elmtMCs),
    getTgCode(data.target),
    getCustomMCsCode(data.customMCs || { BCs: [], DCs: [] })
  ].join("?");
}

function getWpCode(weapon) {
  const { code, level, refinement, BCs } = weapon;
  return [code, levels.indexOf(level), refinement, getMCsCode(BCs)].join("&");
}

function getSubWpBCsCode(BCs) {
  const result = [];
  for (const type in BCs) {
    for (const ctrl of BCs[type]) {
      result.push(
        ctrl.code + ":" + ctrl.refinement + getInputsCode(ctrl.inputs)
      );
    }
  }
  return result.join("!");
}

function getArtCode(art) {
  const { pieces, BCs } = art;
  const result = [];

  const piecesCodes = [];
  for (const P of pieces) {
    if (P === null) {
      piecesCodes.push("X");
    } else {
      const infoCodes = [
        P.code,
        P.rarity || 5,
        P.level,
        allStatsTypes.indexOf(P.mainSType)
      ];
      const subSCodes = [];
      for (const ss of P.subS) {
        subSCodes.push(allStatsTypes.indexOf(ss.type) + ":" + ss.val);
      }
      infoCodes.push(subSCodes.join("/"));
      piecesCodes.push(infoCodes.join("#"));
    }
  }
  result.push(piecesCodes.join("!"));
  result.push(getMCsCode(BCs));

  for (const mcs of ["subBCs", "subDCs"]) {
    const mcsCode = [];
    for (const { code, inputs } of art[mcs]) {
      mcsCode.push(code + getInputsCode(inputs));
    }
    result.push(mcsCode.join("!"));
  }

  return result.join("&");
}

const getPartyCode = (party) =>
  party
    .filter((tm) => tm)
    .map(
      (tm) =>
        findCharacter(tm).code +
        "#" +
        getMCsCode(tm.BCs) +
        "#" +
        getMCsCode(tm.DCs)
    )
    .join("&");

function getElmtMCsCode(elmtMCs) {
  const { naAmpRxn, ampRxn } = elmtMCs;
  return [
    !naAmpRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(naAmpRxn),
    !ampRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(ampRxn),
    elmtMCs.superconduct ? "T" : "F",
    elmtMCs.resonance
      .map(({ name }) => Object.values(rsnNames).indexOf(name))
      .join("#")
  ].join("&");
}

function getTgCode({ Level, ...Res }) {
  return [Level]
    .concat(physAndElmts.map((type) => Res[type + " RES"]))
    .join("&");
}

function getCustomMCsCode(customMCs) {
  return ["BCs", "DCs"]
    .map((type) => {
      return customMCs[type]
        .map(({ catKey, type, val }) => {
          return (catKey !== undefined
            ? [catKey, buffTypes[buffCats[catKey]].indexOf(type), val]
            : [debuffTypes.indexOf(type), val]
          ).join("#");
        })
        .join("!");
    })
    .join("&");
}

// helpers
function getMCsCode(MCs) {
  return MCs.map((ctrl) => ctrl.index + getInputsCode(ctrl.inputs)).join("!");
}

function getInputsCode(inputs) {
  if (inputs) {
    const result = inputs
      .map((inp) => {
        if (inp === true) {
          return "T";
        } else if (inp === false) {
          return "F";
        } else if (specialCodes[inp]) {
          return specialCodes[inp];
        }
        return inp;
      })
      .join("/");
    return ":" + result;
  }
  return "";
}
