import { findByName } from ".";
import { initWeapon } from "../app/calculatorSlice/initiators";
import { artTypes } from "../configs";
import artifacts from "../data/artifacts";
import characters from "../data/characters";
import { findCharacter } from "../data/controllers";
import weapons from "../data/weapons";

const keywords = {
  atk: "ATK",
  atk_: "ATK%",
  hp: "HP",
  hp_: "HP%",
  def: "DEF",
  def_: "DEF%",
  eleMas: "Elemental Mastery",
  enerRech_: "Energy Recharge",
  critRate_: "CRIT Rate",
  critDMG_: "CRIT DMG",
  heal_: "Healing Bonus",
  pyro_dmg_: "Pyro DMG Bonus",
  hydro_dmg_: "Hydro DMG Bonus",
  electro_dmg_: "Electro DMG Bonus",
  geo_dmg_: "Geo DMG Bonus",
  anemo_dmg_: "Anemo DMG Bonus",
  dendro_dmg_: "Dendro DMG Bonus",
  cryo_dmg_: "Cryo DMG Bonus",
  physical_dmg_: "Physical DMG Bonus",
};

export default function convertToGOOD(data) {
  // helpers
  function convertLevel(level, ascension) {
    return `${level}/${ascension ? ascension * 10 + 30 : 20}`;
  }
  function UpFirstLetter(word) {
    return word[0].toUpperCase() + word.slice(1);
  }
  function searchByKey(key, type) {
    const convertName = (str) =>
      str.replace(/'|"|-/g, "").split(" ").map(UpFirstLetter).join("");
    if (type === 1) {
      if (!key) return null;
      for (const { name, GOOD } of characters) {
        if (name === key || GOOD === key) {
          return name;
        }
      }
    } else if (type === 2) {
      for (const { name, code } of artifacts) {
        const converted = convertName(name);
        if (key === converted) return code;
      }
    } else if (type === 3) {
      for (const wpType in weapons) {
        for (const wp of weapons[wpType]) {
          const converted = convertName(wp.name);
          if (key === converted) return [wp.code, wpType];
        }
      }
    }
    return null;
  }
  const result = {
    version: 2.1,
    Characters: [],
    Artifacts: [],
    Weapons: [],
  };
  let ID = Date.now();

  for (const char of data.characters || []) {
    let name;
    if (char.key === "Traveler" && char.elementKey) {
      name = UpFirstLetter(char.elementKey) + " Traveler";
    } else {
      name = searchByKey(char.key, 1);
    }
    if (!name) {
      continue;
    }
    const { ascension, talent } = char;
    const charInfo = {
      name,
      level: convertLevel(char.level, ascension),
      ascension,
      "Normal Attack": talent.auto,
      "Elemental Skill": talent.skill,
      "Elemental Burst": talent.burst,
      constellation: char.constellation,
      weaponID: null,
      artIDs: [null, null, null, null, null],
    };
    result.Characters.push(charInfo);
  }

  for (const artP of data.artifacts || []) {
    const { rarity, slotKey, level } = artP;
    const code = searchByKey(artP.setKey, 2);
    if (![4, 5].includes(rarity) || !code) {
      continue;
    }
    const subS = [];
    const user = searchByKey(artP.location, 1);
    for (const { key, value } of artP.substats) {
      subS.push({ type: keywords[key || "atk"], val: value });
    }
    const artID = ++ID;
    const artInfo = {
      ID: artID,
      type: slotKey,
      rarity,
      level,
      mainSType: keywords[artP.mainStatKey],
      subS,
      user,
      code,
    };
    result.Artifacts.push(artInfo);
    if (user) {
      const char = findByName(result.Characters, user);
      if (char) char.artIDs[artTypes.indexOf(slotKey)] = artID;
    }
  }

  const starterWps = {
    Bow: 11,
    Catalyst: 36,
    Claymore: 59,
    Polearm: 84,
    Sword: 108,
  };
  for (const wp of data.weapons || []) {
    const wpCodeAndType = searchByKey(wp.key, 3);
    if (!wpCodeAndType) {
      continue;
    }
    const [code, type] = wpCodeAndType;
    const user = searchByKey(wp.location, 1);
    if (starterWps[type] === code && !user) {
      continue;
    }
    const wpID = ++ID;
    const wpInfo = {
      ID: wpID,
      type,
      level: convertLevel(wp.level, wp.ascension),
      refinement: wp.refinement,
      user,
      code,
    };
    result.Weapons.push(wpInfo);
    if (user) {
      const char = findByName(result.Characters, user);
      if (char) char.weaponID = wpID;
    }
  }

  for (const char of result.Characters) {
    if (!char.weaponID) {
      const wpID = ++ID;
      const newWp = initWeapon({ type: findCharacter(char).weapon });
      result.Weapons.unshift({ ID: wpID, ...newWp, user: char.name });
      char.weaponID = wpID;
    }
  }
  return result;
}
