import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { REMOVE_WP, selWpById, SWAP_WP_USER } from "../../app/databaseSlice";
import { findCharacter, findWeapon } from "../../data/controllers";
import { useGetHeight } from "../../helpers/hooks";
import { Card } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, EquippedChar } from "../CommonCpns";
import Picker from "../Picker";
import { MyWpCard } from "../Readers/MyItemCard";

export default function RightSection({ ids, chosenID, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(null);
  const [removing, setRemoving] = useState(false);
  const weapon = useSelector(selWpById(chosenID));
  const [ref, height] = useGetHeight();

  return (
    <div ref={ref} className="full-h flex-col">
      <div className="grow-1 flex align-start">
        <Card
          className="flex-col"
          style={{ minHeight: "27rem", maxHeight: height / 16 - 3 + "rem" }}
        >
          <Box className="grow-1 hide-sb" width="18.75rem">
            {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
          </Box>
          {weapon ? (
            <ButtonBar
              className="mt-3"
              texts={["Remove", "Equip"]}
              handlers={[() => setRemoving(true), () => setPickerOn(true)]}
            />
          ) : null}
        </Card>
      </div>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
      {pickerOn && (
        <CharPicker weapon={weapon} close={() => setPickerOn(false)} />
      )}
      {removing && (
        <ConfirmRemove
          weapon={weapon}
          chooseNewID={() => {
            const index = ids.indexOf(weapon.ID);
            if (index !== -1 && ids.length > 1) {
              const move = index < ids.length - 1 ? 1 : -1;
              setChosenID(ids[index + move]);
            }
          }}
          close={() => setRemoving(false)}
        />
      )}
    </div>
  );
}

function CharPicker({ weapon: { ID, user, type }, close }) {
  const { myChars } = useSelector((state) => state.database);
  const data = [];
  for (const char of myChars) {
    const select = "name code icon rarity vision weapon";
    const info = findCharacter(char, select);
    if (info.weapon === type && info.name !== user) {
      data.push({ ...info, constellation: char.constellation });
    }
  }
  const dispatch = useDispatch();
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) =>
        dispatch(SWAP_WP_USER({ newUser: name, wpID: ID, oldUser: user }))
      }
    />
  );
}

function ConfirmRemove({
  weapon: { ID, user, type, code },
  chooseNewID,
  close
}) {
  const dispatch = useDispatch();
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{findWeapon({ type, code }).name}</b>
          "?{" "}
          {Boolean(user) && (
            <>
              It is currently used by <b>{user}</b>.
            </>
          )}
        </>
      }
      close={close}
      confirm={() => {
        dispatch(REMOVE_WP({ ID, user, type }));
        chooseNewID();
      }}
    />
  );
}
