import cn from "classnames";
import { memo } from "react";
import isEqual from "react-fast-compare";
import { FaShareAlt, FaTrashAlt, FaWrench } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  CHOOSE_SETUP,
  SWITCH_SETUP_ON_COMPLEX
} from "../../../app/setupsSlice";
import { START_IMPORT } from "../../../app/uiSlice";
import { Card } from "../../../styledCpns/Database";
import { Text } from "../../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { TeammateIcon } from "../../Calculator/SetupManager/Teammates";
import { AddButton, BuildTmBtn, LinkButton } from "./Buttons";
import Gears from "./Gears";
import Members from "./Members";
import { Divider, MemberWrapper } from "./styles";

export default memo(Setup, isEqual);

function Setup({ ID, setup, stName, allIDs, setUtil, setModalType }) {
  const { chosenID } = useSelector((state) => state.setups);
  const isOg = setup.type === "original";
  const dispatch = useDispatch();

  const teammates = (
    <Box className="mt-4 flex" width="17rem">
      {setup.party.map((tm, tmIndex) => {
        if (!tm) return null;
        const memberID = allIDs?.[tm.name];
        const clickable = !isOg && memberID;
        return (
          <MemberWrapper key={tmIndex} className={cn({ clickable })}>
            <TeammateIcon
              mutable={false}
              name={tm.name}
              handleClick={() => {
                if (clickable)
                  dispatch(
                    SWITCH_SETUP_ON_COMPLEX({
                      complexID: ID,
                      newID: memberID
                    })
                  );
              }}
            />
            {!clickable && (
              <BuildTmBtn
                buildTmSetup={() =>
                  dispatch(
                    START_IMPORT({ importInfo: { setup, tmIndex }, type: 3 })
                  )
                }
              />
            )}
          </MemberWrapper>
        );
      })}
    </Box>
  );

  return (
    <Box
      className="mb-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={ID === chosenID ? "0 0 5px 3px green" : "var(--cmn-shadow)"}
      onClick={() => dispatch(CHOOSE_SETUP(ID))}
    >
      <div className="px-2 flex justify-between">
        <Box className="flex align-center" width="24.5rem">
          {!isOg && <LinkButton ID={ID} shownID={setup.ID} />}
          <Text className="ellipsis" variant="h3" color="orange">
            {stName || setup.name}
          </Text>
        </Box>
        <div className="flex pb-3">
          <IconBtn
            className="mr-4 p-2 glow-on-hover"
            variant="positive"
            onClick={() =>
              dispatch(START_IMPORT({ importInfo: setup, type: 1 }))
            }
          >
            <FaWrench />
          </IconBtn>
          <IconBtn
            className="mr-4 p-2 glow-on-hover"
            variant="neutral"
            onClick={() => setUtil({ type: "share", ID: setup.ID })}
          >
            <FaShareAlt />
          </IconBtn>
          {isOg ? (
            <IconBtn
              className="glow-on-hover"
              variant="negative"
              onClick={() => setUtil({ type: "remove", ID })}
            >
              <FaTrashAlt />
            </IconBtn>
          ) : (
            <AddButton
              addable={Object.keys(allIDs).length < 4}
              add={() => setUtil({ type: "add", ID })}
            />
          )}
        </div>
      </div>
      <div className="flex">
        <Card className="flex">
          <Members
            ID={ID}
            char={setup.char}
            party={setup.party}
            teammates={teammates}
          />
          <Divider />
          <div>
            <Box className="mb-2 flex justify-center">
              {["Stats", "Modifiers"].map((type, i) => (
                <Button
                  key={i}
                  className="mx-2 glow-on-hover"
                  variant="default"
                  onClick={() => setModalType(type)}
                >
                  {type}
                </Button>
              ))}
            </Box>
            <Gears
              weapon={setup.weapon}
              artPieces={setup.art.pieces}
              setModalType={setModalType}
            />
          </div>
        </Card>
      </div>
    </Box>
  );
}
