import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_MCS_INPUT,
  CHANGE_TM_MCS_INPUT,
  TOGGLE_MCS,
  TOGGLE_TM_MCS
} from "../../../../app/calculatorSlice";
import { getThisChar } from "../../../../app/calculatorSlice/helpers";
import { findCharacter } from "../../../../data/controllers";
import { findByIndex } from "../../../../helpers";
import { processNumInp } from "../../../../helpers/processors";
import { Text } from "../../../../styledCpns/DataDisplay";
import { CharModSetter, NoContent, Section } from "../CmnCpns";

export function SelfDebuffs() {
  const { char, selfMCs, parties, curSetupI } = useSelector(calculator);
  const { debuffs } = findCharacter(char);
  const thisChar = getThisChar(char, curSetupI);
  const dispatch = useDispatch();
  const content = [];

  selfMCs[curSetupI].DCs.forEach((ctrl, dcIndex) => {
    const { activated, index } = ctrl;
    const debuff = findByIndex(debuffs, index);
    const path = {
      cat: "selfMCs",
      type: "DCs",
      mcIndex: dcIndex
    };
    if (debuff.isGranted(thisChar)) {
      content.push(
        <Section
          key={dcIndex}
          checked={activated}
          handleCheck={() => {
            dispatch(
              TOGGLE_MCS({
                ...path,
                activated: !activated
              })
            );
          }}
          heading={debuff.src}
          desc={debuff.desc({
            fromSelf: true,
            char: thisChar,
            party: parties[curSetupI]
          })}
          // v2.4 - AnemoMC C6 & Venti C6
          setters={
            <SelfSetterSection
              debuff={debuff}
              inputs={ctrl.inputs}
              path={path}
            />
          }
        />
      );
    }
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function SelfSetterSection({ debuff, inputs, path }) {
  const dispatch = useDispatch();
  if (!debuff.selfLabels) return null;
  return (
    <CharModSetter
      mod={debuff}
      inputs={inputs}
      labels={debuff.selfLabels}
      handleSelect={(value, i) =>
        dispatch(CHANGE_MCS_INPUT({ ...path, inpIndex: i, value }))
      }
      handleText={(value, i) =>
        dispatch(
          CHANGE_MCS_INPUT({
            ...path,
            inpIndex: i,
            value: processNumInp(value, inputs[i], 15)
          })
        )
      }
    />
  );
}

export function PartyDebuffs() {
  const { parties, curSetupI } = useSelector(calculator);
  const content = [];
  parties[curSetupI].forEach((teammate, tmIndex) => {
    if (teammate && teammate.DCs.length)
      content.push(
        <TeammateDebuffs key={tmIndex} teammate={teammate} tmIndex={tmIndex} />
      );
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function TeammateDebuffs({ teammate, tmIndex }) {
  const { char } = useSelector(calculator);
  const { vision, debuffs } = findCharacter(teammate);
  const dispatch = useDispatch();
  const subContent = [];

  teammate.DCs.forEach((ctrl, dcIndex) => {
    const { activated, index, inputs } = ctrl;
    const debuff = findByIndex(debuffs, index);
    const path = {
      tmIndex,
      type: "DCs",
      mcIndex: dcIndex
    };
    subContent.push(
      <Section
        key={dcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(
            TOGGLE_TM_MCS({
              ...path,
              activated: !activated
            })
          )
        }
        heading={debuff.src}
        desc={debuff.desc({
          fromSelf: false,
          char,
          inputs
        })}
        setters={
          <TmSetterSection debuff={debuff} inputs={inputs} path={path} />
        }
      />
    );
  });
  return (
    <>
      <Text className="pt-2 mb-m1" variant="h6" color={vision} align="center">
        <strong>{teammate.name.toUpperCase()}</strong>
      </Text>
      {subContent}
    </>
  );
}

function TmSetterSection({ debuff, inputs, path }) {
  const dispatch = useDispatch();
  if (!debuff.labels) return null;
  return (
    <CharModSetter
      mod={debuff}
      inputs={inputs}
      labels={debuff.labels}
      handleSelect={(value, inpIndex) =>
        dispatch(CHANGE_TM_MCS_INPUT({ ...path, inpIndex, value }))
      }
      handleText={(value, inpIndex) =>
        dispatch(
          CHANGE_TM_MCS_INPUT({
            ...path,
            inpIndex,
            value: processNumInp(value, inputs[inpIndex], 15)
          })
        )
      }
    />
  );
}
