import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  ADD_TEAMMATE,
  calculator,
  COPY_PARTY,
  REMOVE_TEAMMATE,
} from "../../../app/calculatorSlice";
import characters from "../../../data/characters";
import { indexByName } from "../../../helpers";
import { Box } from "../../../styledCpns/Layout";
import Picker from "../../Picker";
import { CopySection, Pedestal } from "../styles";
import { TeammateIcon, TeammateSlot } from "./Teammates";

export default function SectionParty() {
  const { setups, char, parties, curSetupI } = useSelector(calculator);
  const dispatch = useDispatch();
  const party = parties[curSetupI];
  const [pickerTmI, setPickerTmI] = useState(null);

  const copyOpts = [];
  if (party.every((tm) => !tm)) {
    for (let i in parties) {
      if (parties[i].some((tm) => tm)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  const isOg = setups[curSetupI].type === "original";
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection
          options={copyOpts}
          copy={(name) => dispatch(COPY_PARTY(indexByName(setups, name)))}
        />
      ) : null}
      <Box className="px-2 flex justify-around" width="100%">
        {party.map((tm, tmIndex) => (
          <Box
            key={tmIndex}
            className="relative"
            width="4.5rem"
            height="4.5rem"
          >
            {tm ? (
              <TeammateIcon
                mutable={isOg}
                name={tm.name}
                handleClick={() => {
                  if (isOg) setPickerTmI(tmIndex);
                }}
                removeTeammate={() => dispatch(REMOVE_TEAMMATE(tmIndex))}
              />
            ) : (
              <TeammateSlot handleClick={() => setPickerTmI(tmIndex)} />
            )}
          </Box>
        ))}
      </Box>
      {pickerTmI !== null && (
        <CharPicker
          char={char}
          party={party}
          tmIndex={pickerTmI}
          close={() => setPickerTmI(null)}
        />
      )}
    </Pedestal>
  );
}

function CharPicker({ char, party, tmIndex, close }) {
  const dispatch = useDispatch();
  const remains = [];
  for (const { name, code, beta, icon, rarity, vision, weapon } of characters) {
    if (name !== char.name && !party.find((tm) => tm?.name === name)) {
      remains.push({ code, beta, name, icon, rarity, vision, weapon });
    }
  }
  return (
    <Picker
      data={remains}
      dataType="Character"
      close={close}
      pick={({ name, vision, weapon }) =>
        dispatch(ADD_TEAMMATE({ name, vision, weapon, tmIndex }))
      }
    />
  );
}
