import { memo } from "react";
import styled from "styled-components";
import { wikiImg } from "../../configs";
import { findCharacter } from "../../data/controllers";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { Box, GradientBox } from "../../styledCpns/Layout";
import { colors } from "../../theme";

// [../MyCharacters/Info/SectionGears/Overview]
export default memo(Thumb, (prev, next) => {
  if (prev.clicked !== next.clicked || prev.chosen !== next.chosen)
    return false;
  for (const field of ["icon", "level", "refinement", "user"]) {
    if (prev.item[field] !== next.item[field]) return false;
  }
  return true;
});

function Thumb({
  noDeco,
  item: { beta, icon, rarity, level, refinement, user },
  clicked,
  chosen,
  handleMouseDown = () => {},
  choose,
}) {
  return (
    <StyledDiv
      onMouseDown={handleMouseDown}
      onMouseUp={choose}
      className={
        (clicked || noDeco ? "clicked" : "") + (chosen ? " chosen" : "")
      }
    >
      {user && !noDeco && <SideIcon user={user} />}
      <div
        className={
          "thumb flex-col pointer relative " +
          (chosen ? "glowing" : "cmn-shadow")
        }
      >
        {Boolean(refinement) && (
          <p className={"rfm" + (refinement === 5 ? " max" : "")}>
            {refinement}
          </p>
        )}
        <GradientBox bgImg={rarity || 5} borderRadius="0.25rem 0.25rem 1rem 0">
          <Img
            width="100%"
            src={beta ? icon : wikiImg(icon)}
            alt=""
            draggable={false}
          />
        </GradientBox>
        <Box
          className="flex-center"
          bgColor="#ebebeb"
          borderRadius="0 0 0.25rem 0.25rem"
        >
          <Text bold color="black">
            Lv. {refinement ? level.split("/")[0] : level}
          </Text>
        </Box>
      </div>
    </StyledDiv>
  );
}

function SideIcon({ user }) {
  const { icon, sideIcon } = findCharacter({ name: user });
  return (
    <div className={"side_icon" + (sideIcon ? "" : " beta")}>
      <img src={wikiImg(sideIcon || icon)} alt="" draggable={false} />
    </div>
  );
}

const StyledDiv = styled.div`
  &:hover:not(.clicked),
  &.chosen:not(.clicked) {
    transform: scale(1.04);
  }
  .side_icon {
    position: absolute;
    top: 0.375rem;
    right: 0.375rem;
    z-index: 1;
    width: 1.75rem;
    height: 1.75rem;
    background-color: rgba(0, 0, 0, 0.5);
    border: 2px solid white;
    border-radius: 50%;
  }
  .side_icon.beta {
    overflow: hidden;
  }
  .side_icon > img {
    width: 2.5rem;
    transform: translate(-8px, -17px);
  }
  .side_icon.beta > img {
    transform: translate(-8px, -7px);
  }
  &:hover > .side_icon,
  &.chosen > .side_icon {
    top: -0.275rem;
    right: -0.275rem;
  }
  &:hover.clicked > .side_icon,
  &.chosen.clicked > .side_icon {
    top: 0.25rem;
    right: 0.25rem;
  }
  &:hover > .thumb {
    box-shadow: 0 0 0 2.5px white;
  }
  .thumb {
    border-radius: 0.25rem;
    background-color: #ebebeb;
  }
  .rfm {
    position: absolute;
    top: 0.125rem;
    left: 0.125rem;
    border-radius: 0.25rem;
    padding: 0 0.25rem;
    background-color: rgba(0, 0, 0, 0.6);
    color: rgba(235, 235, 235, 0.8);
    font-size: 0.875rem;
    font-weight: 700;
  }
  .rfm.max {
    background-color: black;
    color: ${colors.orange};
  }
`;
