import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { calculator, selCharData } from "../../../app/calculatorSlice";
import { getThisChar } from "../../../app/calculatorSlice/helpers";
import { SAVE_SETUP, selSetupById } from "../../../app/setupsSlice";
import { CHANGE_TAB, CLOSE_SETTINGS } from "../../../app/uiSlice";
import { deepCopy, findById } from "../../../helpers";
import { filterAllMCs } from "../../../helpers/processors";
import { Text } from "../../../styledCpns/DataDisplay";
import { TextInput } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar, ShareLayout } from "../../CommonCpns";
import Modal from "../../CommonCpns/Modal";

export function SaveCtrl({ savedSetup: { name, ID, index }, close }) {
  const charData = useSelector(selCharData);
  const existed = useSelector(selSetupById(ID));
  const [input, setInput] = useState(
    existed ? existed.name : charData.name + " - " + name
  );
  const data = useProcessData(index, true);
  const dispatch = useDispatch();

  const saveSetup = () => {
    dispatch(SAVE_SETUP({ ID, name: input, data }));
    dispatch(CHANGE_TAB("Setups"));
    dispatch(CLOSE_SETTINGS());
  };

  return (
    <Modal close={close}>
      <Box
        className="px-5 py-4 flex-col white-glow-shadow max-width-95"
        width="30rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text className="mb-2 mx-auto" variant="h5" color="orange" bold>
          {existed ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mt-1 mb-5 px-3 pt-3 pb-2"
          type="text"
          borderRadius="0.25rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 34) setInput(value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              saveSetup();
              close();
            }
          }}
        />
        <ButtonBar
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              saveSetup();
              close();
            }
          ]}
        />
      </Box>
    </Modal>
  );
}

export function SharedCtrl({ sharedSetup: { name, index }, close }) {
  return (
    <ShareLayout
      setupName={name}
      data={useProcessData(index, false)}
      close={close}
    />
  );
}

function useProcessData(index, doCompare) {
  const {
    char,
    selfMCs,
    wpRack,
    subWpMCs,
    parties,
    elmtMCs,
    customMCs,
    target
  } = useSelector(calculator);
  const { myArts } = useSelector((state) => state.database);
  const thisChar = getThisChar(char, index);
  const art = deepCopy(useSelector(calculator).artChest[index]);
  if (doCompare) {
    let newID = Date.now();
    for (const p of art.pieces) {
      if (!p) continue;
      const existed = findById(myArts, p.ID);
      if (existed && !isOne(p, existed)) {
        p.ID = newID;
        newID++;
      }
    }
  }
  return {
    char: thisChar,
    ...filterAllMCs({
      char: thisChar,
      selfMCs: selfMCs[index],
      weapon: wpRack[index],
      subWpMCs: subWpMCs[index],
      art,
      party: parties[index],
      elmtMCs: elmtMCs[index]
    }),
    customMCs: customMCs[index],
    target
  };
}

function isOne(p1, p2) {
  for (const field of ["code", "level", "mainSType"]) {
    if (p1[field] !== p2[field]) return false;
  }
  for (let i = 0; i < 4; i++) {
    if (
      p1.subS[i].type !== p2.subS[i].type ||
      p1.subS[i].val !== p2.subS[i].val
    )
      return false;
  }
  return true;
}
