import { useDispatch, useSelector } from "react-redux";
import { getArtSets } from "../../../app/calculatorSlice/helpers";
import {
  CHANGE_TALENT,
  selCharByName,
  selWpById,
  TOGGLE_CONS
} from "../../../app/databaseSlice";
import getBaseStats from "../../../calculators/baseStats";
import { charDataSelect } from "../../../calculators/calculateAll";
import { findCharacter } from "../../../data/controllers";
import { Card } from "../../../styledCpns/Database";
import { Box } from "../../../styledCpns/Layout";
import ConsList from "../../Readers/ConsList";
import TalentList from "../../Readers/TalentList";
import SectionAttr from "./SectionAttr";
import SectionGears from "./SectionGears";

export default function Info() {
  const chosenChar = useSelector((state) => state.database.chosenChar);
  const { weaponID, artIDs, ...char } = useSelector(selCharByName(chosenChar));
  const weapon = useSelector(selWpById(weaponID));
  // use reselect
  const art = useSelector((state) => {
    const pieces = artIDs.map((id) => state.database.myArts.byId[id] || null);
    return { pieces, sets: getArtSets(pieces) };
  });
  const charData = findCharacter(char, charDataSelect);
  const [ATTRs, artSBnes] = getBaseStats(charData, char, weapon, art);

  const dispatch = useDispatch();
  return (
    <Box
      className="py-3 flex overflow-auto"
      width={window.innerWidth <= 480 ? "calc(100% - 2rem)" : "88%"}
      height="98%"
    >
      <SectionAttr charData={charData} level={char.level} ATTRs={ATTRs} />
      <SectionGears weapon={weapon} art={art} artSBnes={artSBnes} />
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <ConsList
            char={char}
            handleClick={(consIndex) =>
              dispatch(TOGGLE_CONS({ name: char.name, consIndex }))
            }
          />
        </Box>
      </Card>
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <TalentList
            char={char}
            upgradeTl={(type, level) =>
              dispatch(CHANGE_TALENT({ name: char.name, type, level }))
            }
          />
        </Box>
      </Card>
    </Box>
  );
}
