import { useState } from "react";
import { FaFilter } from "react-icons/fa";
import { artNames, visionIcons, weaponIcons, wikiImg } from "../../configs";
import { StyledHeading } from "../../styledCpns/Database";
import { Text } from "../../styledCpns/DataDisplay";
import { Checkbox, IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { CloseBtn } from "../CommonCpns";
import { Collapse } from "../CommonCpns/HiddenSpace";
import { HeadingFilter } from "./styles";

export default function Heading({
  dataType,
  needMassAdd,
  massAdd,
  setMassAdd,
  count,
  filter,
  setFilter,
  close,
}) {
  const [filterOn, setFilterOn] = useState(false);
  return (
    <StyledHeading>
      {(window.innerWidth >= 769 || !needMassAdd) && (
        <p className="heading_text">{artNames[dataType] || dataType}</p>
      )}
      {dataType === "Character" && (
        <>
          <IconBtn
            className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
            onClick={() => setFilterOn(!filterOn)}
          >
            <FaFilter />
          </IconBtn>
          <AbsoluteBox className="full-w" top="100%" left={0} zIndex={3}>
            <Box
              borderRadius="0 0 0.5rem 0.5rem"
              bgColor="cardBg3"
              boxShadow="var(--cmn-shadow)"
            >
              <Collapse in={filterOn}>
                <CharFilter
                  {...filter}
                  setFilter={setFilter}
                  setFilterOn={setFilterOn}
                />
              </Collapse>
            </Box>
          </AbsoluteBox>
        </>
      )}
      {needMassAdd && (
        <AbsoluteBox right="4rem" className="flex align-center">
          <Checkbox
            scale={1.6}
            checked={massAdd}
            onChange={() => setMassAdd(!massAdd)}
          />
          <Text className="mt-1 ml-2" bold color="black">
            Mass Add ({count})
          </Text>
        </AbsoluteBox>
      )}
      <CloseBtn className="close_btn" onClick={close} />
    </StyledHeading>
  );
}

function CharFilter({ type, value, setFilter, setFilterOn }) {
  return (
    <HeadingFilter>
      <div className="py-3 flex-wrap justify-around">
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(visionIcons).map((pair, i) => {
            const chosen = type === "vision" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon vision" + (chosen ? " chosen" : "")}
                src={wikiImg(pair[1])}
                alt={pair[0]}
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "vision", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(weaponIcons).map((pair, i) => {
            const chosen = type === "weapon" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon weapon" + (chosen ? " chosen" : "")}
                src={wikiImg(pair[1])}
                alt={pair[0]}
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "weapon", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
      </div>
    </HeadingFilter>
  );
}
