import { useState } from "react";
import { useSelector } from "react-redux";
import { findWeapon } from "../../data/controllers";
import { findById } from "../../helpers";
import { useGetHeight } from "../../helpers/hooks";
import { Card, Cell, StyledHeading } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn, EquippedChar, NoItems } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { MyWpCard } from "./MyItemCard";
import Thumb from "./Thumb";

export default function WpInventory({
  wpType,
  owner,
  btnText,
  btnClick,
  close,
}) {
  const { myWps } = useSelector((state) => state.database);
  const filteredData = myWps.filter((wp) => wp.type === wpType);
  const [chosenID, setChosenID] = useState(filteredData[0]?.ID || 0);
  const chosenWp = findById(filteredData, chosenID);
  const [hRef, height] = useGetHeight();

  return (
    <Modal standard close={close}>
      <Box height="10%" className="p-2">
        <StyledHeading>
          {window.innerWidth > 380 && <p className="heading_text">{wpType}</p>}
          <CloseBtn className="close_btn" onClick={close} />
        </StyledHeading>
      </Box>
      <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
        <div className="full-h flex hide-sb">
          {filteredData.length ? (
            <Rack
              data={filteredData}
              chosenID={chosenID}
              setChosenID={setChosenID}
            />
          ) : (
            <Box className="grow-1" minWidth="18.75rem">
              <NoItems type={wpType} />
            </Box>
          )}
          <div ref={hRef} className="flex-col justify-between">
            <Card
              className="flex-col"
              style={{
                minHeight: "26rem",
                maxHeight: height / 16 - 3 + "rem",
              }}
            >
              <Box className="grow-1 hide-sb" width="17rem">
                {chosenWp ? (
                  <MyWpCard weapon={chosenWp} mutable={false} />
                ) : null}
              </Box>
              {chosenWp && chosenWp.user !== owner ? (
                <ButtonBar
                  className="mt-3"
                  variants={["positive"]}
                  texts={[btnText]}
                  handlers={[
                    () => {
                      btnClick(chosenWp);
                      close();
                    },
                  ]}
                />
              ) : null}
            </Card>
            {chosenWp?.user ? <EquippedChar user={chosenWp.user} /> : null}
          </div>
        </div>
      </Box>
    </Modal>
  );
}

function Rack({ data, chosenID, setChosenID }) {
  const [clickedID, setClickedID] = useState(null);
  return (
    <Box
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      minWidth="18.75rem"
    >
      <div className="flex-wrap">
        {data.map(({ type, code, ID, ...others }, i) => {
          const { beta, icon, rarity } = findWeapon({ type, code });
          return (
            <Cell key={ID}>
              <Thumb
                index={i}
                item={{ beta, icon, rarity, ...others }}
                chosen={ID === chosenID}
                clicked={ID === clickedID}
                handleMouseDown={() => setClickedID(ID)}
                choose={() => {
                  setClickedID(ID);
                  setChosenID(ID);
                }}
              />
            </Cell>
          );
        })}
      </div>
    </Box>
  );
}
