import { useDispatch } from "react-redux";
import {
  CHANGE_SUB_WP_MCS_INPUT,
  REFINE_SUB_WP,
  TOGGLE_SUB_WP_MCS,
} from "../../../../app/calculatorSlice";
import { findWeapon } from "../../../../data/controllers";
import { findByIndex } from "../../../../helpers";
import { Section, StyledSelect, StyledSetter } from "../CmnCpns";

export default function WpBuffsSub({ subBCs }) {
  const dispatch = useDispatch();
  const content = [];
  for (const wpType in subBCs) {
    subBCs[wpType].forEach((ctrl, bcIndex) => {
      const { activated, code } = ctrl;
      const { name, buffs } = findWeapon({ type: wpType, code });
      const buff = findByIndex(buffs, ctrl.index);
      const path = {
        type: "BCs",
        wpType,
        mcIndex: bcIndex,
      };
      content.push(
        <Section
          key={code.toString() + bcIndex}
          checked={activated}
          handleCheck={() =>
            dispatch(
              TOGGLE_SUB_WP_MCS({
                ...path,
                activated: !activated,
              })
            )
          }
          heading={name}
          desc={buff.desc({ wpRfm: ctrl.refinement })}
          setters={<SetterSection buff={buff} ctrl={ctrl} path={path} />}
        />
      );
    });
  }
  return content;
}

function SetterSection({ buff, ctrl, path }) {
  const dispatch = useDispatch();
  const xtraSetters = [];
  if (buff.labels) {
    buff.labels.forEach((label, i) => {
      const type = buff.inputTypes[i];
      let input;
      if (type === "choices") {
        input = (
          <StyledSelect
            value={ctrl.inputs[i]}
            onChange={(e) =>
              dispatch(
                CHANGE_SUB_WP_MCS_INPUT({
                  ...path,
                  inpIndex: i,
                  value: e.target.value,
                })
              )
            }
          >
            {buff.values[i].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </StyledSelect>
        );
      }
      xtraSetters.push(
        <StyledSetter key={i}>
          <p className="label">{label}</p>
          {input}
        </StyledSetter>
      );
    });
  }
  return (
    <>
      <StyledSetter>
        <p className="label">Refinement</p>
        <StyledSelect
          value={ctrl.refinement}
          onChange={(e) =>
            dispatch(REFINE_SUB_WP({ ...path, value: +e.target.value }))
          }
        >
          {[1, 2, 3, 4, 5].map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </StyledSelect>
      </StyledSetter>
      {xtraSetters}
    </>
  );
}
