import { useState, memo } from "react";
import { useSelector } from "react-redux";
import { selWpById } from "../../app/databaseSlice";
import { findWeapon } from "../../data/controllers";
import { StyledRack } from "../../styledCpns/Database";
import { NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";

function WpThumb({ ID, chosen, clicked, setChosenID, setClickedID }) {
  const { type, code, ...others } = useSelector(selWpById(ID));
  const { beta, name, icon, rarity } = findWeapon({ type, code });
  return (
    <div key={ID} className="cell relative">
      <Thumb
        item={{ beta, name, icon, rarity, ID, ...others }}
        chosen={chosen}
        clicked={clicked}
        handleMouseDown={() => setClickedID(ID)}
        choose={() => {
          setClickedID(null);
          setChosenID(ID);
        }}
      />
    </div>
  );
}

const MemoWpThumb = memo(WpThumb);

export default function LeftSection({ ids, chosenID, setChosenID }) {
  const [clickedID, setClickedID] = useState(null);
  return (
    <StyledRack className="hide-sb">
      {ids.length ? (
        <div className="flex-wrap">
          {ids.map((ID) => (
            <MemoWpThumb
              key={ID}
              ID={ID}
              chosen={ID === chosenID}
              clicked={ID === clickedID}
              setChosenID={setChosenID}
              setClickedID={setClickedID}
            />
          ))}
        </div>
      ) : (
        <NoItems type="Weapons" />
      )}
    </StyledRack>
  );
}
