import cn from "classnames";
import { memo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  COPY_ART,
  SET_ART_PIECE
} from "../../../app/calculatorSlice";
import { artifactIcons, artTypes, wikiImg } from "../../../configs";
import { findArtifact } from "../../../data/controllers";
import { indexByName } from "../../../helpers";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box, GradientBox } from "../../../styledCpns/Layout";
import { ArtPicker } from "../../Picker/ItemPicker";
import { CopySection, Pedestal } from "../styles";
import ArtPieceInfo from "./ArtPieceInfo";

export default memo(SectionArts);

function SectionArts() {
  const { setups, artChest, curSetupI } = useSelector(calculator);
  const [tabNo, setTabNo] = useState(0);
  const [infoOn, setInfoOn] = useState(false);
  const [pickerOn, setPickerOn] = useState(false);
  const dispatch = useDispatch();
  const { pieces } = artChest[curSetupI];

  const copyOpts = [];
  if (pieces.every((artP) => artP === null)) {
    for (let i in artChest) {
      if (artChest[i].pieces.some((artP) => artP !== null)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection
          options={copyOpts}
          copy={(name) => dispatch(COPY_ART(indexByName(setups, name)))}
        />
      ) : null}
      <TabBar
        pieces={pieces}
        tabNo={tabNo}
        setTabNo={setTabNo}
        infoOn={infoOn}
        setInfoOn={setInfoOn}
      />
      <ArtPieceInfo
        pieces={pieces}
        tabNo={tabNo}
        infoOn={infoOn}
        setPickerOn={setPickerOn}
      />
      {pickerOn && (
        <ArtPicker
          artType={artTypes[tabNo]}
          close={() => setPickerOn(false)}
          pick={(item) =>
            dispatch(
              SET_ART_PIECE({
                pieceIndex: tabNo,
                newPiece: { ID: Date.now(), ...item },
                isNew: true
              })
            )
          }
        />
      )}
    </Pedestal>
  );
}

function TabBar({ pieces, tabNo, setTabNo, infoOn, setInfoOn }) {
  return (
    <div className="flex">
      {artTypes.map((type, index) => {
        const artP = pieces[index];
        let src;
        if (artP) {
          const { beta, icon } = findArtifact({ code: artP.code, type });
          src = beta ? icon : wikiImg(icon);
        } else {
          src = wikiImg(artifactIcons[type]);
        }
        return (
          <Box
            key={index}
            minWidth="20%"
            border={
              index === tabNo && infoOn
                ? "2px solid white"
                : "0.5px solid transparent"
            }
          >
            <GradientBox
              className={cn({ "p-2": !artP }, "pointer")}
              bgImg={artP ? artP.rarity || 5 : 1}
              onClick={() => {
                setTabNo(index);
                setInfoOn(index === tabNo ? !infoOn : true);
                if (!infoOn) {
                  setTimeout(() => {
                    const body = document.getElementById("setupManager_body");
                    body.scrollTop = 9999;
                  }, 150);
                }
              }}
            >
              <Img className="full-w" src={src} alt={type} draggable={false} />
            </GradientBox>
          </Box>
        );
      })}
    </div>
  );
}
