import ReactDOM from "react-dom";
import { useEffect, useRef, useState } from "react";
import { FaUpload } from "react-icons/fa";
import convertToGOOD from "../../helpers/GOODconverter";
import { Text } from "../../styledCpns/DataDisplay";
import { Button } from "../../styledCpns/Inputs";
import { CloseBtn } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { Divider, LoadOptions, NavBtn } from "./styles";

export default function Upload({ outdates, loadData, closeMenu, uploadErr }) {
  const [optionsOn, setOptionsOn] = useState(false);
  return (
    <div>
      <NavBtn className="last-btn pointer" onClick={() => setOptionsOn(true)}>
        <FaUpload className="mr-2" />
        <Text bold>Upload</Text>
      </NavBtn>
      {optionsOn &&
        ReactDOM.createPortal(
          <Options
            loadData={loadData}
            close={() => setOptionsOn(false)}
            closeMenu={closeMenu}
            outdates={outdates}
            uploadErr={uploadErr}
          />,
          document.getElementById("portal")
        )}
    </div>
  );
}

function Options({ loadData, close, outdates, closeMenu }) {
  const [msType, setMsType] = useState([]);

  useEffect(() => {
    if (outdates) close();
  }, [outdates, close]);

  const tryToLoadFromLocalStorage = () => {
    let data = localStorage.getItem("GDC_Data");
    if (data) {
      data = JSON.parse(data);
      try {
        loadData(data);
        setMsType([0, "success"]);
      } catch (err) {
        setMsType([0, "fail"]);
      }
    } else {
      setMsType([0, "noData"]);
    }
  };
  const inputRef = useRef();
  const upload = () => {
    const file = inputRef.current.files[0];
    const reader = new FileReader();
    const isJson = file?.type.match(/application.*/);
    if (file?.type.match(/text.*/) || isJson) {
      reader.onload = function (event) {
        try {
          let data = JSON.parse(event.target.result);
          if (isJson) data = convertToGOOD(data);
          loadData(data);
          setMsType([1, "success"]);
          if (closeMenu) closeMenu();
        } catch (err) {
          console.log(err);
          setMsType([1, "fail"]);
        }
      };
      reader.readAsText(file);
    }
  };

  const fail =
    "An Error has occured while loading your data. Please contact me for this matter.";
  const msgs = [
    {
      success: "Successfully loaded data from Local Storage",
      noData: "! There's no data in your Local Storage !",
      fail
    },
    { success: "Successfully uploaded your File", fail }
  ];
  const msgColor = msType[1] === "success" ? "green" : "lightRed";

  return (
    <Modal close={close}>
      <LoadOptions className="pt-2 pb-3 white-glow-shadow">
        <CloseBtn className="ml-auto mr-2 mb-3 glow-on-hover" onClick={close} />
        <div className="option flex-col align-center">
          <Text className="px-3 py-2" variant="h5">
            Load from Local Storage
          </Text>
          {msType[0] === 0 && (
            <Text
              className="mb-2"
              variant="h6"
              color={msgColor}
              bold
              align="center"
            >
              {msgs[0][msType[1]]}
            </Text>
          )}
          <Button
            className="my-1"
            variant="positive"
            onClick={tryToLoadFromLocalStorage}
          >
            Load
          </Button>
        </div>
        <Divider />
        <div className="option flex-col align-center">
          <Text className="px-3 py-2" variant="h5" align="center">
            Upload a .TXT file or a .JSON file in GOOD format
          </Text>
          {msType[0] === 1 && (
            <Text className="mb-2" color={msgColor} bold align="center">
              {msgs[1][msType[1]]}
            </Text>
          )}
          <input
            ref={inputRef}
            hidden
            type="file"
            accept="text/*,application/json"
            onChange={upload}
          />
          <Button
            className="my-1"
            variant="positive"
            onClick={() => {
              inputRef.current.click();
            }}
          >
            Choose File
          </Button>
        </div>
      </LoadOptions>
    </Modal>
  );
}
