import { memo, useEffect, useState } from "react";
import { unknownIcons, wikiImg } from "../../configs";
import { findCharacter } from "../../data/controllers";
import { Green, Img, Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { CloseBtn, InfoSign } from "../CommonCpns";
import { SharedSpace, SlideShow } from "./styles";

export default function PreCheck({ char, handleClick }) {
  const info = findCharacter(char, "code vision constellation");
  if (!info.constellation.length) {
    return (
      <div className="pt-3 px-3">
        <Text variant="h5" align="center">
          The time has not yet come for this person's corner of the night sky to
          light up.
        </Text>
      </div>
    );
  }
  return (
    <ConsList {...info} consLv={char.constellation} handleClick={handleClick} />
  );
}

function ConsList({ code, vision, constellation, consLv, handleClick }) {
  const [ctrlLv, setCtrlLv] = useState(0);
  const [atDetails, setAtDetails] = useState(false);

  useEffect(() => {
    setAtDetails(false);
  }, [code]);

  return (
    <SharedSpace>
      <div className={"left_part " + (atDetails ? "close" : "open")}>
        <MemoOverview
          vision={vision}
          constellation={constellation}
          consLv={consLv}
          handleClick={handleClick}
          setCtrlLv={setCtrlLv}
          setAtDetails={setAtDetails}
        />
      </div>
      {ctrlLv ? (
        <div className={"right_part " + (atDetails ? "open" : "close")}>
          <Details
            vision={vision}
            constellation={constellation}
            ctrlLv={ctrlLv}
            setCtrlLv={setCtrlLv}
            setAtDetails={setAtDetails}
          />
        </div>
      ) : (
        <div className="right_part close" />
      )}
    </SharedSpace>
  );
}

const MemoOverview = memo(Overview);

function Overview(props) {
  return (
    <Box className="full-h hide-sb">
      {props.constellation.map((cons, i) => {
        const available = props.consLv >= i + 1 ? "" : " unavailable";
        return (
          <div key={i} className={"flex" + (i ? " mt-3" : "")}>
            <div className="flex-center">
              <Img
                className={"my-1 mr-2 pointer" + available}
                width="3.25rem"
                src={wikiImg(cons.image || unknownIcons[props.vision])}
                alt=""
                draggable={false}
                onClick={() => props.handleClick(i)}
              />
            </div>
            <div
              className={"grow-1 flex align-center contain-info_sign"}
              onClick={() => {
                props.setAtDetails(true);
                props.setCtrlLv(i + 1);
              }}
            >
              <Text className={"px-2" + available} variant="h6" bold>
                {cons.name}
              </Text>
              <div className="ml-auto">
                <InfoSign />
              </div>
            </div>
          </div>
        );
      })}
    </Box>
  );
}

function Details(props) {
  const { vision, constellation, ctrlLv, setCtrlLv } = props;
  const { name, desc } = constellation[ctrlLv - 1];
  return (
    <div className="full-h flex-col">
      <div className="hide-sb">
        <SlideShow
          index={ctrlLv - 1}
          images={constellation.map((cons) => cons.image)}
          vision={vision}
          back={() => setCtrlLv(ctrlLv - 1)}
          next={() => setCtrlLv(ctrlLv + 1)}
          close={props.close}
        />
        <div className="py-2 cons_details">
          <Text variant="h5" color={vision} bold>
            {name}
          </Text>
          <Text variant="h6">
            Constellation Lv. <Green b>{ctrlLv}</Green>
          </Text>
          <Text className="mt-3">
            {ctrlLv === 3 || ctrlLv === 5 ? (
              <>
                Increases the Level of <Green>{desc}</Green> by{" "}
                <Green b>3</Green>.<br /> Maximum upgrade level is 15.
              </>
            ) : (
              desc
            )}
          </Text>
        </div>
      </div>
      <div className="mt-3">
        <CloseBtn
          className="mx-auto glow-on-hover"
          onClick={() => {
            props.setAtDetails(false);
            setTimeout(() => setCtrlLv(0), 200);
          }}
        />
      </div>
    </div>
  );
}
