import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_MCS_INPUT,
  TOGGLE_MCS,
} from "../../../../app/calculatorSlice";
import { findWeapon } from "../../../../data/controllers";
import { findByIndex } from "../../../../helpers";
import { Checkbox } from "../../../../styledCpns/Inputs";
import { Section, StyledSelect, StyledSetter } from "../CmnCpns";

export default function WpBuffsMain({ weapon }) {
  const { curSetupI, allATTRs } = useSelector(calculator);
  const dispatch = useDispatch();
  const { type, code, refinement: wpRfm, BCs } = weapon;
  const { name, buffs } = findWeapon({ type, code });

  return BCs.map((ctrl, bcIndex) => {
    const { activated } = ctrl;
    const buff = findByIndex(buffs, ctrl.index);
    return (
      <Section
        key={code.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(
            TOGGLE_MCS({
              cat: "wpRack",
              type: "BCs",
              mcIndex: bcIndex,
              activated: !activated,
            })
          )
        }
        heading={name + " (self)"}
        desc={buff.desc({ wpRfm, ATTRs: allATTRs[curSetupI] })}
        setters={
          <SetterSection buff={buff} inputs={ctrl.inputs} bcIndex={bcIndex} />
        }
      />
    );
  });
}

function SetterSection({ buff, inputs, bcIndex }) {
  const dispatch = useDispatch();
  if (!buff.labels) return null;
  const path = (inpIndex) => ({
    cat: "wpRack",
    type: "BCs",
    mcIndex: bcIndex,
    inpIndex,
  });
  return buff.labels.map((label, i) => {
    const type = buff.inputTypes[i];
    let inputCpn;
    if (type === "stacks") {
      inputCpn = (
        <StyledSelect
          value={inputs[i]}
          onChange={(e) =>
            dispatch(CHANGE_MCS_INPUT({ ...path(i), value: +e.target.value }))
          }
        >
          {[...Array(buff.values[i]).keys()].map((opt) => (
            <option key={opt}>{opt + 1}</option>
          ))}
        </StyledSelect>
      );
    } else if (type === "check") {
      inputCpn = (
        <Checkbox
          checked={inputs[i]}
          onChange={() =>
            dispatch(CHANGE_MCS_INPUT({ ...path(i), value: !inputs[i] }))
          }
        />
      );
    } else if (type === "choices") {
      inputCpn = (
        <StyledSelect
          value={inputs[i]}
          onChange={(e) =>
            dispatch(CHANGE_MCS_INPUT({ ...path(i), value: e.target.value }))
          }
        >
          {buff.values[i].map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </StyledSelect>
      );
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </StyledSetter>
    );
  });
}
