import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_ELMT_MCS,
  CHANGE_MCS_INPUT,
  TOGGLE_MCS,
  TOGGLE_RESONANCE
} from "../../../../app/calculatorSlice";
import { ampElmts, crystallizables, rsnBuffDesc } from "../../../../configs";
import {
  findArtBuff,
  findArtifact,
  findCharacter
} from "../../../../data/controllers";
import { AmpRxnDesc } from "../../../CommonCpns";
import { NoContent, Section, StyledSelect, StyledSetter } from "../CmnCpns";

export function ElmtBuffs() {
  const { char, elmtMCs, curSetupI, allInfusion } = useSelector(calculator);
  const dispatch = useDispatch();
  const content = [];

  elmtMCs[curSetupI].resonance.forEach((rsn, rsnIndex) => {
    content.push(
      <Section
        key={rsnIndex}
        checked={rsn.activated}
        handleCheck={() =>
          dispatch(TOGGLE_RESONANCE({ rsnIndex, activated: !rsn.activated }))
        }
        heading={rsn.name}
        desc={rsnBuffDesc[rsn.name]}
      />
    );
  });
  const { vision } = findCharacter(char);
  if (ampElmts.includes(vision)) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <RxnBuff key={rxn} rxn={rxn} vision={vision} />
        )),
        Hydro: <RxnBuff key="Vaporize" rxn="Vaporize" vision={vision} />,
        Cryo: <RxnBuff key="Melt" rxn="Melt" vision={vision} />
      }[vision]
    );
  }
  const infusion = allInfusion[curSetupI].NA;
  if (ampElmts.includes(infusion) && infusion !== vision) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <NaRxnBuff key={rxn} rxn={rxn} infusion={infusion} />
        )),
        Hydro: <NaRxnBuff key="Vaporize" rxn="Vaporize" infusion={infusion} />,
        Cryo: <NaRxnBuff key="Melt" rxn="Melt" infusion={infusion} />
      }[infusion]
    );
  }
  return content.length ? content : <NoContent type="buffs" />;
}

function RxnBuff({ rxn, vision }) {
  const { elmtMCs, curSetupI, allRxnBnes } = useSelector(calculator);
  const dispatch = useDispatch();
  const activated = elmtMCs[curSetupI].ampRxn === rxn;

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(
          CHANGE_ELMT_MCS({ type: "ampRxn", value: activated ? null : rxn })
        )
      }
      heading={rxn}
      desc={<AmpRxnDesc elmt={vision} mult={allRxnBnes[curSetupI][rxn]} />}
    />
  );
}

function NaRxnBuff({ rxn, infusion }) {
  const { elmtMCs, curSetupI, allRxnBnes } = useSelector(calculator);
  const dispatch = useDispatch();
  const activated = elmtMCs[curSetupI].naAmpRxn === rxn;

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(
          CHANGE_ELMT_MCS({ type: "naAmpRxn", value: activated ? null : rxn })
        )
      }
      heading={rxn + " (external infusion)"}
      desc={
        <AmpRxnDesc elmt={infusion} mult={allRxnBnes[curSetupI]["na" + rxn]} />
      }
    />
  );
}

export function ArtBuffs() {
  const { artChest, curSetupI, allATTRs } = useSelector(calculator);
  const dispatch = useDispatch();
  const { sets, BCs, subBCs } = artChest[curSetupI];
  const content = [];

  const mainCode = sets[0]?.code;
  BCs.forEach((ctrl, bcIndex) => {
    const { activated } = ctrl;
    const buff = findArtBuff({ code: mainCode }, ctrl.index);
    const path = {
      cat: "artChest",
      type: "BCs",
      mcIndex: bcIndex
    };
    content.push(
      <Section
        key={mainCode.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(
            TOGGLE_MCS({
              ...path,
              activated: !activated
            })
          )
        }
        heading={findArtifact({ code: mainCode }).name + " (self)"}
        desc={buff.desc({ ATTRs: allATTRs[curSetupI] })}
        setters={<SetterSection buff={buff} inputs={ctrl.inputs} path={path} />}
      />
    );
  });
  subBCs.forEach((ctrl, bcIndex) => {
    const { code, activated } = ctrl;
    const buff = findArtBuff({ code }, ctrl.index);
    const path = {
      cat: "artChest",
      type: "subBCs",
      mcIndex: bcIndex
    };
    content.push(
      <Section
        key={code.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(
            TOGGLE_MCS({
              ...path,
              activated: !activated
            })
          )
        }
        heading={findArtifact({ code }).name}
        desc={buff.desc()}
        setters={<SetterSection buff={buff} inputs={ctrl.inputs} path={path} />}
      />
    );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SetterSection({ buff, inputs, path }) {
  const dispatch = useDispatch();
  if (!buff.labels) return null;
  return buff.labels.map((label, i) => {
    const isStacks = buff.inputTypes[i] === "stacks";
    let options = isStacks ? [...Array(buff.maxs[i]).keys()] : crystallizables;
    if (isStacks) {
      if (buff.startsAt0?.[i]) {
        options.push(buff.maxs[i]);
      } else {
        options = options.map((n) => n + 1);
      }
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        <StyledSelect
          value={inputs[i]}
          onChange={(e) => {
            const { value } = e.target;
            dispatch(
              CHANGE_MCS_INPUT({
                ...path,
                inpIndex: i,
                value: isNaN(value) ? value : +value
              })
            );
          }}
        >
          {options.map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </StyledSelect>
      </StyledSetter>
    );
  });
}
