import { addAndTrack } from "../../../calculators/helpers";
import { elmtDmgBnes } from "../../../configs";
import {
  applyPct,
  findByCode,
  round1,
  round2,
  roundMaker
} from "../../../helpers";
import { Green, Red } from "../../../styledCpns/DataDisplay";
import { wpSimpleAnTmaker } from "../config";
import { LiyueSeries } from "../series";

const GoldPolearms = [
  {
    code: 77,
    name: "Skyward Spine",
    icon: "6/69/Weapon_Skyward_Spine",
    rarity: 5,
    mainStatScale: 48,
    subStat: { type: "Energy Recharge", scale: "8%" },
    addBnes: ({ ATTRs, wpRfm, tkDesc, tracker }) => {
      const fields = ["CRIT Rate", "Normal ATK SPD"];
      const bnValue = 6 + wpRfm * 2;
      addAndTrack(tkDesc, ATTRs, fields, [bnValue, 12], tracker);
    },
    pasvName: "Blackwing",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <Green>CRIT Rate</Green> by{" "}
            <Green b>{6 + wpRfm * 2}%</Green> and increases{" "}
            <Green>Normal ATK SPD</Green> by <Green b>12%</Green>. Additionally,
            Normal and Charged Attacks hits on opponents have a{" "}
            <Green>50% chance</Green> to trigger a vacuum blade that deals{" "}
            <Green b>{25 + wpRfm * 15}%</Green> of <Green>ATK</Green> as DMG in
            a small AoE. This effect can occur no more than once every 2s.
          </>
        )
      }
    ]
  },
  {
    code: 78,
    name: "Primordial Jade Winged-Spear",
    icon: "8/80/Weapon_Primordial_Jade_Winged-Spear",
    rarity: 5,
    mainStatScale: 48,
    subStat: { type: "CRIT Rate", scale: "4.8%" },
    buffs: [
      {
        index: 0,
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [7],
        addBnes: ({ ATTRs, hitBnes, wpRfm, inputs, tkDesc, tracker }) => {
          const bnValue1 = (2.5 + wpRfm * 0.7) * inputs[0];
          addAndTrack(tkDesc, ATTRs, "ATK%", bnValue1, tracker);
          if (inputs[0] === 7) {
            addAndTrack(tkDesc, hitBnes, "All.pct", 9 + wpRfm * 3, tracker);
          }
        },
        desc: ({ wpRfm }) =>
          findByCode(GoldPolearms, 78).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Eagle Spear of Justice",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            On hit, increases <Green>ATK</Green> by{" "}
            <Green b>{2.5 + wpRfm * 0.7}%</Green> for 6s. Max <Green b>7</Green>{" "}
            stacks. This effect can only occur once every 0.3s. While in
            possession of the maximum possible stacks, <Green>DMG</Green> dealt
            is increased by <Green b>{9 + wpRfm * 3}%</Green>.
          </>
        )
      }
    ]
  },
  {
    code: 79,
    name: "Engulfing Lightning",
    icon: "2/21/Weapon_Engulfing_Lightning",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "Energy Recharge", scale: "12%" },
    addFinalBnes: ({ ATTRs, wpRfm, tkDesc, tracker }) => {
      const ER = round1(ATTRs["Energy Recharge"] - 100);
      const mult = 21 + wpRfm * 7;
      let bnValue = (ER / 100) * mult;
      let desc = ` / ${mult}% of ${ER}% Energy Rechare => ${round2(bnValue)}%`;
      const limit = 70 + wpRfm * 10;
      if (bnValue > limit) {
        bnValue = limit;
        desc += ` (limited to ${limit}%)`;
      }
      desc += ` ATK = ${ATTRs["Base ATK"]} * ${roundMaker(4)(bnValue / 100)}`;
      bnValue = applyPct(ATTRs["Base ATK"], bnValue);
      addAndTrack(tkDesc + desc, ATTRs, "ATK", bnValue, tracker);
    },
    buffs: [
      {
        index: 0,
        affect: "self",
        addBnes: ({ ATTRs, wpRfm, tkDesc, tracker }) => {
          addAndTrack(
            tkDesc,
            ATTRs,
            "Energy Recharge",
            25 + wpRfm * 5,
            tracker
          );
        },
        desc: ({ wpRfm }) =>
          findByCode(GoldPolearms, 79).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Timeless Dream: Eternal Stove",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <Green>ATK</Green> increased by <Green b>{21 + wpRfm * 7}%</Green>{" "}
            of <Green>Energy Recharge</Green> over the base 100%. You can gain a
            maximum bonus of {70 + wpRfm * 10}% ATK.
          </>
        ),
        buff: (
          <>
            Gain <Green b>{25 + wpRfm * 5}%</Green>{" "}
            <Green>Energy Recharge</Green> for 12s after using an Elemental
            Burst.
          </>
        )
      }
    ]
  },
  {
    code: 80,
    name: "Staff of Homa",
    icon: "1/17/Weapon_Staff_of_Homa",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "CRIT DMG", scale: "14.4%" },
    addBnes: wpSimpleAnTmaker("ATTRs", "HP%", 5),
    addFinalBnes: ({ ATTRs, wpRfm, tkDesc, tracker }) => {
      const mult = 0.6 + wpRfm * 0.2;
      const bnValue = applyPct(ATTRs.HP, mult);
      const desc = ` / ${mult}% of ${ATTRs.HP} HP`;
      addAndTrack(tkDesc + desc, ATTRs, "ATK", bnValue, tracker);
    },
    buffs: [
      {
        index: 0,
        affect: "self",
        addFinalBnes: ({ ATTRs, wpRfm, tkDesc, tracker }) => {
          const { bnValue } = findByCode(GoldPolearms, 80).buffs[0];
          const result = bnValue(ATTRs, wpRfm);
          addAndTrack(tkDesc + result[1], ATTRs, "ATK", result[0], tracker);
        },
        bnValue: (ATTRs, wpRfm) => {
          const mult = 0.8 + wpRfm * 0.2;
          return [
            applyPct(ATTRs.HP, 0.8 + wpRfm * 0.2),
            ` / ${round1(mult)}% of ${ATTRs.HP} HP`
          ];
        },
        desc: ({ wpRfm, ATTRs }) => {
          const { buffs, pasvDescs } = findByCode(GoldPolearms, 80);
          return (
            <>
              {pasvDescs({ wpRfm })[0].buff}{" "}
              <Red>ATK Bonus: {buffs[0].bnValue(ATTRs, wpRfm)[0]}.</Red>
            </>
          );
        }
      }
    ],
    pasvName: "Reckless Cinnabar",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <Green>HP</Green> increased by <Green b>{15 + wpRfm * 5}%</Green>.
            Additionally, provides an <Green>ATK Bonus</Green> based on{" "}
            <Green b>{Math.round(6 + wpRfm * 2) / 10}%</Green> of the wielder's{" "}
            <Green>Max HP</Green>.
          </>
        ),
        buff: (
          <>
            When the wielder's HP is less than 50%, this{" "}
            <Green>ATK Bonus</Green> is increased by an additional{" "}
            <Green b>{Math.round(8 + wpRfm * 2) / 10}%</Green> of{" "}
            <Green>Max HP</Green>.
          </>
        )
      }
    ]
  },
  {
    code: 81,
    name: "Vortex Vanquisher",
    icon: "d/d6/Weapon_Vortex_Vanquisher",
    rarity: 5,
    mainStatScale: 46,
    subStat: { type: "ATK%", scale: "10.8%" },
    ...LiyueSeries
  },
  {
    code: 82,
    name: "Calamity Queller",
    icon: "8/8b/Weapon_Calamity_Queller",
    rarity: 5,
    mainStatScale: 49,
    subStat: { type: "ATK%", scale: "3.6%" },
    addBnes: wpSimpleAnTmaker("ATTRs", elmtDmgBnes, 3),
    buffs: [
      {
        index: 0,
        affect: "self",
        labels: ["Stacks", "Not on the field"],
        inputTypes: ["stacks", "check"],
        values: [6],
        addBnes: ({ ATTRs, wpRfm, inputs, tkDesc, tracker }) => {
          let bnValue = (2.4 + wpRfm * 0.8) * inputs[0];
          if (inputs[1]) bnValue *= 2;
          addAndTrack(tkDesc, ATTRs, "ATK%", bnValue, tracker);
        },
        desc: ({ wpRfm }) =>
          findByCode(GoldPolearms, 82).pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Eagle Spear of Justice",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Gain <Green b>{9 + wpRfm * 3}%</Green>{" "}
            <Green>All Elemental DMG Bonus</Green>.
          </>
        ),
        buff: (
          <>
            Obtain Consummation for 20s after using an Elemental Skill, causing{" "}
            <Green>ATK</Green> to increase by{" "}
            <Green b>{(24 + wpRfm * 8) / 10}%</Green> per second. This ATK
            increase can stack <Green b>6</Green> times. When the character
            equipped with this weapon is not on the field, Consummation's ATK
            increase is <Green>doubled</Green>.
          </>
        )
      }
    ]
  }
];

export default GoldPolearms;
