import ReactDOM from "react-dom";
import { useState } from "react";
import { FaDownload } from "react-icons/fa";
import { useSelector } from "react-redux";
import { Text } from "../../styledCpns/DataDisplay";
import { ButtonBar, CloseBtn } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { Divider, LoadOptions, NavBtn } from "./styles";
// import { selMyChars, selMyWps, selMyArts } from "../../app/databaseSlice";

export default function Download() {
  const [optionsOn, setOptionsOn] = useState(false);
  return (
    <div>
      <NavBtn
        className="last-btn border pointer"
        onClick={() => setOptionsOn(true)}
      >
        <FaDownload className="mr-2" />
        <Text bold>Download</Text>
      </NavBtn>
      {optionsOn &&
        ReactDOM.createPortal(
          <Options close={() => setOptionsOn(false)} />,
          document.getElementById("portal")
        )}
    </div>
  );
}

function Options({ close }) {
  const [msType, setMsType] = useState(0);
  // const myChars = useSelector(selMyChars);
  // const myWps = useSelector(selMyWps);
  // const myArts = useSelector(selMyArts);
  const myChars = [];
  const myWps = [];
  const myArts = [];
  const mySetups = useSelector((state) => state.setups.mySetups);
  const data = JSON.stringify({
    version: 2.1,
    Characters: myChars,
    Weapons: myWps,
    Artifacts: myArts,
    Setups: mySetups
  });

  const download = () => {
    // type: "application/json"
    const textBlob = new Blob([data], { type: "text/plain" });
    let newLink = document.createElement("a");
    newLink.download = "GDC_Data";
    if (window.webkitURL != null) {
      newLink.href = window.webkitURL.createObjectURL(textBlob);
    } else {
      newLink.href = window.URL.createObjectURL(textBlob);
      newLink.style.display = "none";
      document.body.appendChild(newLink);
    }
    newLink.click();
  };

  const saveToLocalStorage = () => {
    localStorage.setItem("GDC_Data", data);
    setMsType(2);
  };

  const trySaveToLocalStorage = () => {
    if (localStorage.getItem("GDC_Data")) {
      setMsType(1);
    } else {
      saveToLocalStorage();
    }
  };

  return (
    <Modal close={close}>
      <LoadOptions className="pt-2 pb-3 white-glow-shadow">
        <CloseBtn className="ml-auto mr-2 mb-3 glow-on-hover" onClick={close} />
        <div className="option" onClick={trySaveToLocalStorage}>
          <Text className="mt-1" variant="h5" bold color="lightGold">
            Save to Local Storage
          </Text>
          <Text className="mt-1">
             Your Database stays in this Browser only and will be removed if
            you clear the Local Storage.
          </Text>
        </div>
        {!!msType && (
          <>
            <Divider />
            <div className="p-3">
              <Text
                className="mt-1"
                variant="h6"
                bold
                color={msType === 1 ? "lightRed" : "green"}
                align="center"
              >
                {msType === 1
                  ? "We detect old data in your Local Storage. Do you want to replace it?"
                  : "Successfully saved to Local Storage!"}
              </Text>
              {msType === 1 && (
                <ButtonBar
                  className="my-2"
                  texts={["Cancel", "Confirm"]}
                  handlers={[close, saveToLocalStorage]}
                />
              )}
            </div>
          </>
        )}
        <Divider />
        <div className="option" onClick={download}>
          <Text className="mt-1" variant="h5" bold color="lightGold">
            Download a .TXT file
          </Text>
          <Text className="mt-1">
             Please DO NOT modify this file if you don't understand how it
            works.
          </Text>
        </div>
      </LoadOptions>
    </Modal>
  );
}
