import { wikiImg } from "../../../configs";
import { findCharacter, getPartyData } from "../../../data/controllers";
import { getFinalTlLv } from "../../../helpers";
import { Img, spanMaker, Text } from "../../../styledCpns/DataDisplay";

export default function Members({ char, party, teammates }) {
  const { icon, vision, actvTalents } = findCharacter(char);
  const talents = actvTalents.map((talent) => {
    return getFinalTlLv(char, talent, getPartyData(party));
  });
  const VisionSpan = spanMaker(vision);
  return (
    <div>
      <div className="flex justify-center">
        <Img
          width="6rem"
          src={wikiImg(icon)}
          alt={char.name}
          draggable={false}
        />
        <div className="ml-2 pt-2 flex-col justify-between">
          <Text variant="h5">
            Level <VisionSpan b>{char.level}</VisionSpan>
          </Text>
          <Text variant="h6">
            Constellation <VisionSpan b>{char.constellation}</VisionSpan>
          </Text>
          <Text variant="h6">
            Talents: <VisionSpan b>{talents[0]}</VisionSpan> /{" "}
            <VisionSpan b>{talents[1]}</VisionSpan> /{" "}
            <VisionSpan b>{talents[2]}</VisionSpan>
          </Text>
        </div>
      </div>
      {teammates}
    </div>
  );
}
