import { useState } from "react";
import { FaSyncAlt } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { calculator, LEVEL_CHAR } from "../../../app/calculatorSlice";
import { levels, wikiImg } from "../../../configs";
import { findCharacter } from "../../../data/controllers";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { AbsoluteBox } from "../../../styledCpns/Layout";
import { BetaMark, StarLine } from "../../CommonCpns";
import { MainSelect } from "../styles";
import TabContent from "./TabContent";

export default function Content({ setPickerOn }) {
  const { char, curSetupI } = useSelector(calculator);
  const [tab, setTab] = useState("Attributes");
  const sepCharInfo = Array.isArray(char.level);
  const charData = findCharacter(char);
  const dispatch = useDispatch();

  return (
    <>
      <div className="mt-2 pb-4 flex">
        <div className="mr-3 relative" onClick={() => setPickerOn(true)}>
          <AbsoluteBox top="-0.625rem" left="-0.625rem" zIndex={1}>
            <IconBtn
              className="glow-on-hover"
              variant="positive"
              fontSize="1.25rem"
            >
              <FaSyncAlt />
            </IconBtn>
          </AbsoluteBox>
          {charData.beta && (
            <AbsoluteBox top="-0.5rem" right="-0.5rem" zIndex={1}>
              <BetaMark />
            </AbsoluteBox>
          )}
          <Img
            className="pointer"
            width="6.25rem"
            src={wikiImg(charData.icon)}
            alt={char.name}
            draggable={false}
          />
        </div>
        <div className="overflow-hidden">
          <Text className="ellipsis" variant="h1" color={charData.vision}>
            {char.name}
          </Text>
          <StarLine className="mt-1" rarity={charData.rarity} />
          <div className="mt-1 flex">
            <Text variant="h6" className="mr-1">
              Level
            </Text>
            <Select
              color={charData.vision}
              fontSize="1.125rem"
              bold
              align="right"
              value={sepCharInfo ? char.level[curSetupI] : char.level}
              onChange={(e) => dispatch(LEVEL_CHAR(e.target.value))}
            >
              {levels.map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          </div>
        </div>
      </div>
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={[
          "Attributes",
          "Weapon",
          "Artifacts",
          "Constellation",
          "Talents",
        ]}
      />
      <TabContent tab={tab} />
    </>
  );
}
