import {
  baseSTypes,
  bnFields,
  hitBnTypes,
  pctOf,
  reactions,
  tfmRxns
} from "../../../configs";
import { applyPct, round1, round2, round3 } from "../../../helpers";
import { lib } from "../../../library";
import { Green, Orange, Text } from "../../../styledCpns/DataDisplay";
import { getTotal } from "./helpers";
import { Columns, Divider, error, heading, Lesser } from "./styles";

export function Attributes({ ATTRs, tracker }) {
  const fields = Object.keys(ATTRs).filter((k) => k.slice(0, 4) !== "Base");
  return (
    <Columns>
      {fields.map((type) => {
        const pct = pctOf(type);
        let total = getTotal(tracker[type]);
        if (baseSTypes.includes(type)) {
          const totalPct = getTotal(tracker[`${type}%`]);
          total += applyPct(ATTRs[`Base ${type}`], totalPct);
        }
        const err = Math.abs(total - ATTRs[type]) / ATTRs[type] >= 0.5;
        return (
          <div key={type}>
            {heading(type, round1(ATTRs[type]) + pct)}
            <div className="pl-2">
              {tracker[type].map(({ desc, value }, i) =>
                value ? (
                  <Lesser key={i}>
                     {desc}: <Green>{round1(value) + pct}</Green>
                  </Lesser>
                ) : null
              )}
              <ExtraPct type={type} tracker={tracker} ATTRs={ATTRs} />
              {err && error}
            </div>
          </div>
        );
      })}
    </Columns>
  );
}

function ExtraPct({ type, tracker, ATTRs }) {
  if (!baseSTypes.includes(type)) return null;
  return (
    <div>
      {tracker[`${type}%`].map(({ desc, value }, i) => {
        const base = ATTRs[`Base ${type}`];
        return (
          !!value && (
            <Lesser key={i}>
               {desc}: {round1(value)}% = {base} *{" "}
              {Math.round(value * 10) / 1000} ={" "}
              <Green>{applyPct(base, value)}</Green>
            </Lesser>
          )
        );
      })}
    </div>
  );
}

export function Bonuses({ hitBnes, rxnBnesFrEM, rxnBnes, tracker }) {
  const content = [];

  for (const bnType of hitBnTypes) {
    if (bnFields.every((field) => !tracker[bnType][field].length)) {
      continue;
    }
    content.push(
      <div key={bnType}>
        <Text color="orange">{lib[bnType] || bnType + " Attack"}</Text>
        {bnFields.map((field) => (
          <Field
            key={field}
            list={tracker[bnType][field]}
            field={field}
            bonus={hitBnes[bnType][field]}
          />
        ))}
      </div>
    );
  }
  return (
    <div>
      {!!content.length && (
        <>
          <Columns>{content}</Columns>
          <Divider />
        </>
      )}
      <Text variant="h5" color="orange">
        Reactions
      </Text>
      <Columns>
        {reactions.map((rxn) => {
          const bnFrEm =
            rxnBnesFrEM[rxn in tfmRxns ? "transformative" : "amplifying"];
          const totalBn = getTotal(tracker[rxn]) + bnFrEm;
          return (
            <div key={rxn}>
              <Text bold>
                {rxn} <Orange>{totalBn}%</Orange>
              </Text>
              <div className="pl-3">
                {bnFrEm ? (
                  <Lesser>
                     Bonus from Elemental Mastery: <Green>{bnFrEm}%</Green>
                  </Lesser>
                ) : null}
                {tracker[rxn].map(({ desc, value }, i) => (
                  <Lesser key={i}>
                     {desc}: <Green>{value}%</Green>
                  </Lesser>
                ))}
              </div>
            </div>
          );
        })}
      </Columns>
    </div>
  );
}

function process(field, value) {
  switch (field) {
    case "flat":
      return Math.round(value);
    case "pct":
      return round2(value) + "%";
    default:
      return value + "%";
  }
}

function Field({ list, field, bonus }) {
  if (!list.length) return null;
  return (
    <div className="mt-1 pl-2">
      <Text bold>
        {lib[field]} <Orange>{process(field, getTotal(list))}</Orange>
      </Text>
      <div className="pl-2">
        {list.map(({ desc, value }) => (
          <Lesser key={desc}>
             {desc}: <Green>{process(field, value)}</Green>
          </Lesser>
        ))}
        {getTotal(list) !== bonus && error}
      </div>
    </div>
  );
}
