import { useState } from "react";
import { FaCog } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  calculator,
  CHANGE_CUR_SETUP,
  PICK_WP_IN_DB,
  SET_ART_PIECE,
} from "../../../app/calculatorSlice";
import { CHANGE_STD_SETUP, OPEN_SETTINGS } from "../../../app/uiSlice";
import { artifactIcons, artTypes, wikiImg } from "../../../configs";
import { findCharacter } from "../../../data/controllers";
import { indexByName } from "../../../helpers";
import { useGetHeight } from "../../../helpers/hooks";
import { Img } from "../../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { colors } from "../../../theme";
import PrePicker from "../../Picker/PrePicker";
import ArtInventory from "../../Readers/ArtInventory";
import WpInventory from "../../Readers/WpInventory";
import Settings from "../Settings";
import { Card, MainSelect } from "../styles";
import ImportPreper from "./ImportPreper";
import SectionArts from "./SectionArts";
import SectionParty from "./SectionParty";
import SectionWp from "./SectionWp";

const StyledIcon = styled(Img)`
  border-radius: 50%;
  &:hover {
    background-color: ${colors.lightGold};
  }
`;

export default function PreCheck() {
  const { char } = useSelector(calculator);
  const [impPreping, setImpPreping] = useState(false);

  const importBtn = (
    <Box height="2rem">
      <Button
        className="full-w full-h glow-on-hover"
        bgColor="#2269d6"
        color="white"
        onClick={() => setImpPreping(true)}
      >
        Import
      </Button>
      {impPreping && <ImportPreper close={() => setImpPreping(false)} />}
    </Box>
  );

  if (!char) {
    return (
      <Card className="p-3" bgColor="cardBg3">
        {importBtn}
      </Card>
    );
  }
  return <SetupManager importBtn={importBtn} />;
}

function SetupManager({ importBtn }) {
  const { stdSetupI } = useSelector((state) => state.ui);
  const { char, setups, curSetupI } = useSelector(calculator);
  const [inventory, setInv] = useState(null);
  const [prePickerOn, setPrePickerOn] = useState(false);
  const chosen = stdSetupI === curSetupI;
  const [ref, height] = useGetHeight();
  const dispatch = useDispatch();
  return (
    <Card
      ref={ref}
      className="p-3 flex-col relative overflow-hidden"
      bgColor="cardBg3"
    >
      <MainSelect
        tab={setups[curSetupI].name}
        setTab={(name) => dispatch(CHANGE_CUR_SETUP(indexByName(setups, name)))}
        options={setups.map((st) => st.name)}
      />
      <div id="setupManager_body" className="mt-3 grow-1 hide-sb scroll-smooth">
        <SectionParty />
        <SectionWp />
        <SectionArts />
      </div>
      <div className="mt-3 flex justify-between align-center">
        <Button
          className={chosen ? "unavailable" : "glow-on-hover"}
          variant="positive"
          onClick={() => {
            if (!chosen) dispatch(CHANGE_STD_SETUP(curSetupI));
          }}
        >
          Choose
        </Button>
        <IconBtn
          className="glow-on-hover"
          fontSize="1.25rem"
          variant="positive"
          onClick={() => dispatch(OPEN_SETTINGS())}
        >
          <FaCog />
        </IconBtn>
        <div className="flex">
          <StyledIcon
            width="2.5rem"
            className="mr-1 pointer"
            src={wikiImg("7/7b/Icon_Inventory_Weapons")}
            alt="weapons"
            draggable={false}
            onClick={() => setInv("weapons")}
          />
          <StyledIcon
            width="2.5rem"
            className="pointer"
            src={wikiImg("6/6a/Icon_Inventory_Artifacts")}
            alt="artifacts"
            draggable={false}
            onClick={() => setPrePickerOn(true)}
          />
        </div>
      </div>
      <Settings height={height} importBtn={importBtn} />
      {inventory === "weapons" && (
        <WpInventory
          wpType={findCharacter(char).weapon}
          close={() => setInv(null)}
          btnText="Pick"
          btnClick={(targetWp) => dispatch(PICK_WP_IN_DB(targetWp))}
        />
      )}
      {prePickerOn && (
        <PrePicker
          choices={artifactIcons}
          pick={(artType) => {
            setInv(artType);
            setPrePickerOn(false);
          }}
          close={() => setPrePickerOn(false)}
        />
      )}
      {![null, "weapons"].includes(inventory) && (
        <ArtPicker artType={inventory} close={() => setInv(null)} />
      )}
    </Card>
  );
}

function ArtPicker({ artType, close }) {
  const { artChest, curSetupI } = useSelector(calculator);
  const dispatch = useDispatch();
  return (
    <ArtInventory
      curPieces={artChest[curSetupI].pieces}
      artType={artType}
      close={close}
      btnText="Pick"
      btnClick={({ user, ...info }) =>
        dispatch(
          SET_ART_PIECE({
            pieceIndex: artTypes.indexOf(artType),
            newPiece: info,
          })
        )
      }
    />
  );
}
