import styled from "styled-components";
import { levels, pctOf, wikiImg } from "../../configs";
import { findWeapon } from "../../data/controllers";
import { wpMainStatAtLv, wpSubStatAtLv } from "../../data/weapons/config";
import { Img, List, Text } from "../../styledCpns/DataDisplay";
import { Select } from "../../styledCpns/Inputs";
import { AbsoluteBox, GradientBox } from "../../styledCpns/Layout";
import { colors } from "../../theme";
import { BetaMark } from "../CommonCpns";

export default function WeaponCard({ weapon, mutable, upgrade, refine }) {
  if (!weapon) return null;
  const wpData = findWeapon(weapon);
  const { rarity, subStat } = wpData;
  const selectLvs = [...levels];
  if (rarity < 3) selectLvs.splice(-4);
  const HiliText = (props) => <Text color={rarity} {...props} />;

  const LeftSec = (
    <div className="grow-1 flex-col justify-between">
      <Group className="pt-1 grow-1 flex align-center">
        <Text className="mr-2" variant="h6" bold>
          Level
        </Text>
        {mutable ? (
          <Select
            color={rarity}
            fontSize="1.125rem"
            bold
            align="right"
            value={weapon.level}
            onChange={(e) => upgrade(e.target.value)}
          >
            {selectLvs.map((level) => (
              <option key={level}>{level}</option>
            ))}
          </Select>
        ) : (
          <Text variant="h6" bold color={rarity}>
            {weapon.level}
          </Text>
        )}
      </Group>
      {subStat ? (
        <Group className="grow-1 mt-1 pt-1 flex-col justify-center">
          <Text bold>{subStat.type}</Text>
          <HiliText variant="h3">
            {wpSubStatAtLv(subStat.scale, weapon.level) + pctOf(subStat.type)}
          </HiliText>
        </Group>
      ) : null}
      <Group className="grow-1 mt-1 pt-1 flex-col justify-center">
        <Text bold>Base ATK</Text>
        <HiliText variant="h2">
          {wpMainStatAtLv(wpData.mainStatScale, weapon.level)}
        </HiliText>
      </Group>
    </div>
  );

  const RightSec = (
    <div className="ml-2">
      <GradientBox className="relative" borderRadius="0.5rem" bgImg={rarity}>
        <Img
          width="8rem"
          src={wpData.beta ? wpData.icon : wikiImg(wpData.icon)}
          alt=""
          draggable={false}
        />
        {wpData.beta && (
          <AbsoluteBox bottom={0} right={0}>
            <BetaMark />
          </AbsoluteBox>
        )}
      </GradientBox>
      {rarity >= 3 && (
        <Group className="mt-2 py-1 flex-col align-center">
          <Text bold align="center">
            Refinement
          </Text>
          {mutable ? (
            <Select
              color={rarity}
              fontSize="1.125rem"
              bold
              value={weapon.refinement}
              onChange={(e) => refine(+e.target.value)}
            >
              {[1, 2, 3, 4, 5].map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          ) : (
            <Text variant="h6" bold color={rarity}>
              {weapon.refinement}
            </Text>
          )}
        </Group>
      )}
    </div>
  );
  return (
    <div className="full-w">
      <Text variant="h2" color={rarity}>
        {wpData.name}
      </Text>
      <div className="mt-2 flex">
        {LeftSec}
        {RightSec}
      </div>
      <div className="mt-2">
        <Text variant="h6" bold color="orange">
          {wpData.pasvName}
        </Text>
        <List position="inside">
          {wpData.pasvDescs({ wpRfm: weapon.refinement }).map((desc, i) => (
            <li key={i}>
              {desc.bonus} {desc.buff} {desc.others}
            </li>
          ))}
        </List>
      </div>
    </div>
  );
}

const Group = styled.div`
  background-color: ${colors.cardBg2};
  padding-right: 0.5rem;
  padding-left: 0.5rem;
`;
