import {
  FaCopy,
  FaEllipsisH,
  FaSave,
  FaShareAlt,
  FaTrashAlt,
} from "react-icons/fa";
import { getSetupInfo } from "../../../app/calculatorSlice/helpers";
import { deepCopy } from "../../../helpers";
import { Text } from "../../../styledCpns/DataDisplay";
import { Checkbox, IconBtn, TextInput } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { Collapse } from "../../CommonCpns/HiddenSpace";

export default function SetupCtrl({
  setup,
  index,
  setTempSetups,
  belowLimit,
  setSavedI,
  setSharedI,
}) {
  const isNew = setup.index === null;
  const { expanded, isStandard } = setup;
  const moreBtns = [
    {
      Icon: FaSave,
      handler: () => setSavedI(index),
    },
    {
      Icon: FaShareAlt,
      handler: () => setSharedI(index),
    },
  ];

  return (
    <Box
      className={(index ? "my-2" : "mb-2") + " p-2"}
      borderRadius="0.5rem"
      bgColor="cardBg1"
    >
      <TextInput
        type="text"
        className="full-w px-3 pt-2 pb-1"
        borderRadius="0.375rem"
        fontSize="1.125rem"
        align="center"
        value={setup.name}
        onChange={(e) => {
          if (e.target.value.length <= 16) {
            setTempSetups((prev) => {
              const newTSs = deepCopy(prev);
              newTSs[index].name = e.target.value;
              return newTSs;
            });
          }
        }}
      />
      <div className="mt-3 flex justify-between">
        <div className="ml-1 flex">
          <IconBtn
            className={
              "p-2 mr-4 " + (isStandard ? "unavailable" : "glow-on-hover")
            }
            variant="negative"
            onClick={() => {
              if (!isStandard) {
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs.splice(index, 1);
                  return newTSs;
                });
              }
            }}
          >
            <FaTrashAlt />
          </IconBtn>
          <IconBtn
            className={"mr-4 " + (isNew ? "unavailable" : "glow-on-hover")}
            variant="positive"
            onClick={() => {
              if (!isNew && belowLimit) {
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs.push({
                    ...getSetupInfo({ name: setup.name + " (copy)" }),
                    index: setup.index,
                    checked: false,
                    expanded: false,
                  });
                  return newTSs;
                });
              }
            }}
          >
            <FaCopy />
          </IconBtn>
          <IconBtn
            className={!isNew ? "glow-on-hover" : "unavailable"}
            variant={expanded ? "neutral" : "default"}
            onClick={() => {
              if (!isNew)
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs[index].expanded = !newTSs[index].expanded;
                  return newTSs;
                });
            }}
          >
            <FaEllipsisH />
          </IconBtn>
        </div>
        <div className="flex align-center relative">
          {isStandard ? (
            <Text className="mr-1" bold>
              Standard
            </Text>
          ) : (
            <>
              <Text className="mr-5 pr-2">Compared</Text>
              <AbsoluteBox right="0.5rem">
                <Checkbox
                  scale={2}
                  checked={setup.checked}
                  onChange={() => {
                    if (!isStandard) {
                      setTempSetups((prev) => {
                        const newTSs = deepCopy(prev);
                        newTSs[index].checked = !newTSs[index].checked;
                        return newTSs;
                      });
                    }
                  }}
                />
              </AbsoluteBox>
            </>
          )}
        </div>
      </div>
      <Collapse in={expanded}>
        <div className="pl-1 pt-4 flex">
          {moreBtns.map(({ Icon, handler }, i) => (
            <IconBtn
              key={i}
              className="mr-4 glow-on-hover"
              variant="positive"
              onClick={handler}
            >
              <Icon />
            </IconBtn>
          ))}
        </div>
      </Collapse>
    </Box>
  );
}
