import { useState, memo } from "react";
import { useSelector } from "react-redux";
import { selArtById } from "../../app/databaseSlice";
import { findArtifact } from "../../data/controllers";
import { StyledRack } from "../../styledCpns/Database";
import { NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";

function ArtThumb({ ID, chosen, clicked, setChosenID, setClickedID }) {
  const { code, type, user, rarity, level } = useSelector(selArtById(ID));
  const { beta, icon } = findArtifact({ code, type });
  return (
    <div className="cell relative">
      <Thumb
        item={{ ID, user, beta, icon, rarity, level }}
        chosen={chosen}
        clicked={clicked}
        handleMouseDown={() => setClickedID(ID)}
        choose={() => {
          setClickedID(null);
          setChosenID(ID);
        }}
      />
    </div>
  );
}

const MemoArtThumb = memo(ArtThumb);

export default function LeftSection({ ids, chosenID, setChosenID }) {
  const [clickedID, setClickedID] = useState(null);
  return (
    <StyledRack className="hide-sb">
      {ids.length ? (
        <div className="flex-wrap">
          {ids.map((ID) => (
            <MemoArtThumb
              key={ID}
              ID={ID}
              chosen={ID === chosenID}
              clicked={ID === clickedID}
              setChosenID={setChosenID}
              setClickedID={setClickedID}
            />
          ))}
        </div>
      ) : (
        <NoItems type="Artifacts" />
      )}
    </StyledRack>
  );
}
