import styled from "styled-components";
import { swirlables } from "../../../configs";
import { Text } from "../../../styledCpns/DataDisplay";
import { Checkbox, Select, TextInput } from "../../../styledCpns/Inputs";

export function Section(props) {
  return (
    <div className="pt-2">
      <div className="mb-1 flex align-center">
        {!props.immutable && (
          <Checkbox
            className="ml-1 mr-2"
            scale={1.8}
            checked={props.checked}
            onChange={props.handleCheck}
          />
        )}
        <Text className="pl-1" bold color="lightGold">
          {props.heading}
        </Text>
      </div>
      <Text>{props.desc}</Text>
      {props.setters}
    </div>
  );
}

export const StyledSetter = styled.div`
  margin: 0.5rem 0.5rem 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  :not(:last-child) {
    margin-bottom: 1.25rem;
  }
  .label {
    margin-right: 1rem;
    font-size: 1rem;
    line-height: 1.5;
    text-align: right;
  }
`;

export const StyledSelect = styled((props) => (
  <Select bgColor="white" borderRadius="0.25rem" bold {...props} />
))`
  padding: 0.25rem 0.5rem;
`;

export const NoContent = ({ type }) => (
  <Text className="pt-4 pb-3" align="center">
    No {type} found
  </Text>
);

export function CharModSetter({
  labels,
  mod,
  inputs,
  startsAt0,
  handleText,
  handleCheck,
  handleSelect
}) {
  return labels.map((label, i) => {
    const type = mod.inputTypes[i];
    let inputCpn;
    if (type === "text") {
      inputCpn = (
        <TextInput
          className="px-2 py-2"
          width="4rem"
          borderRadius="0.25rem"
          align="right"
          value={inputs[i]}
          onChange={(e) => handleText(e.target.value, i)}
        />
      );
    } else if (type === "check") {
      inputCpn = (
        <Checkbox
          className="mr-1"
          scale={1.8}
          checked={inputs[i]}
          onChange={() => handleCheck(i)}
        />
      );
    } else {
      let options;
      if (type === "select") {
        // options = [...Array(mod.maxs[i]).keys()];
        options = Array.from({ length: mod.maxs[i] }, (_, i) => i + 1);
        // if (startsAt0 && startsAt0[i]) options.unshift(0);
      } else {
        // swirl & absorption
        options = swirlables;
      }
      inputCpn = (
        <StyledSelect
          value={inputs[i]}
          onChange={(e) => handleSelect(e.target.value, i)}
        >
          {options.map((opt, j) => (
            <option key={j}>{opt}</option>
          ))}
        </StyledSelect>
      );
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </StyledSetter>
    );
  });
}
