import { useState } from "react";
import { FaCopy } from "react-icons/fa";
import styled from "styled-components";
import { colors } from "../../theme";
import { IconBtn, Select } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";

export const StyledCalculator = styled.div`
  width: 95%;
  height: calc(100vh - 3rem);
  display: flex;
  align-items: center;
  padding-bottom: 0.5rem;
  overflow: auto;
  @media (min-width: 1456px) {
    width: 1416px;
  }
`;

export const StyledCol = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media (min-width: 1456px) {
    width: 704px;
  }
`;

export const Card = styled.div`
  &:not(:last-child) {
    margin-right: 0.5rem;
  }
  min-width: 21.75rem;
  max-width: 21.75rem;
  border-radius: 0.625rem;
  background-color: ${(props) => colors[props.bgColor]};
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2), 0 3px 10px rgba(0, 0, 0, 0.2);
`;

export function MainSelect({ tab, setTab, options, className = "" }) {
  return (
    <Box borderRadius="1.25rem" bgColor="orange">
      <Select
        className={
          "px-3 py-1 appearance-none bg-for-icon black-arrow-bg calc-main-select " +
          className
        }
        fontSize="1.25rem"
        bold
        align="center"
        value={tab}
        onChange={(e) => setTab(e.target.value)}
      >
        {options.map((opt, i) => (
          <option key={i} style={{ fontSize: "1.25rem" }}>
            {opt}
          </option>
        ))}
      </Select>
    </Box>
  );
}

// used in sections of SetupManager
export const Pedestal = styled.div`
  margin-bottom: 0.5rem;
  padding: 0.75rem 0;
  border: 2px solid white;
  border-radius: 0.75rem;
  background-color: ${colors.cardBg1};
`;

// [sections of SetupManager, ./Modifiers/CustomMods/index]
export function CopySection({ options, copy }) {
  const [opt, setOpts] = useState(options[0]);
  return (
    <div className="mb-3 px-3 flex justify-end">
      <Select
        className="px-1"
        value={opt}
        borderRadius="0.25rem"
        bgColor="white"
        bold
        onChange={(e) => setOpts(e.target.value)}
      >
        {options.map((opt, i) => (
          <option key={i}>{opt}</option>
        ))}
      </Select>
      <IconBtn
        className="ml-3 glow-on-hover"
        variant="positive"
        onClick={() => copy(opt)}
      >
        <FaCopy />
      </IconBtn>
    </div>
  );
}

export const TrackerCtrlBtn = styled(IconBtn)`
  background-color: ${(pr) => (pr.state ? colors.green : colors.defaultBg)};
  &:hover {
    background-color: ${colors.lightGold};
  }
`;
