import { useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import { selSetupById } from "../../app/setupsSlice";
import { Box } from "../../styledCpns/Layout";
import { NoItems } from "../CommonCpns";
import Setup from "./Setup";

// use reselect
const selVisibleIds = (state) => {
  const { byId, allIds } = state.setups.mySetups;
  return allIds.filter((id) => byId[id].type !== "combined");
};

function ActualSetup({ ID, setup, setUtil }) {
  let actualSetup = useSelector(selSetupById(setup.shownID));
  return (
    <Setup
      key={ID}
      ID={ID}
      setup={actualSetup}
      stName={setup.name}
      allIDs={setup.allIDs}
      setUtil={setUtil}
    />
  );
}

function SetupProccessor({ ID, setModalType, setUtil }) {
  let setup = useSelector(selSetupById(ID));
  if (setup.type === "original") {
    return (
      <Setup
        ID={ID}
        setup={setup}
        setUtil={setUtil}
        setModalType={setModalType}
      />
    );
  } else if (setup.type === "complex") {
    return (
      <ActualSetup
        ID={ID}
        setup={setup}
        setUtil={setUtil}
        setModalType={setModalType}
      />
    );
  }
}

export default function SetupList({ setModalType, setUtil }) {
  const chosenID = useSelector((state) => state.setups.chosenID);
  const visibleIds = useSelector(selVisibleIds);
  const ref = useRef();

  useEffect(() => {
    if (chosenID) {
      const index = visibleIds.indexOf(chosenID);
      ref.current.scrollTop = 326 * index - (index > 0 ? 40 : 0);
    }
  }, [chosenID]);

  return (
    <Box
      ref={ref}
      className="pl-2 pt-2 flex-col align-start overflow-auto scroll-smooth"
      width="40.5rem"
      minWidth="40.5rem"
    >
      {visibleIds.length ? (
        visibleIds.map((ID) => (
          <SetupProccessor
            key={ID}
            ID={ID}
            setModalType={setModalType}
            setUtil={setUtil}
          />
        ))
      ) : (
        <div className="full-w">
          <NoItems type="Setups" />
        </div>
      )}
    </Box>
  );
}
