import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  IMPORT_SETUP,
  INIT_SESSION_WITH_SETUP
} from "../../app/calculatorSlice";
import { END_IMPORT, RESET_CALC_UI, TO_IMPORTED } from "../../app/uiSlice";
import Modal from "../CommonCpns/Modal";
import { areEqual, useImportedSetup } from "./helpers";
import ImportPending from "./ImportPending";
import { LoadingBar } from "./styles";

export default function Importing() {
  const { setups, char, target } = useSelector(calculator);
  const [pendingCode, setPendingCode] = useState(0);
  const dispatch = useDispatch();
  const imported = useImportedSetup();

  const importSetup = (overwriteChar, overwriteTg) => {
    dispatch(
      IMPORT_SETUP({
        imported,
        overwriteChar,
        overwriteTg
      })
    );
    dispatch(END_IMPORT());
    dispatch(TO_IMPORTED());
  };

  const startSession = () => {
    dispatch(INIT_SESSION_WITH_SETUP(imported));
    dispatch(END_IMPORT());
    dispatch(RESET_CALC_UI());
  };

  useEffect(() => {
    const execute = (fn) => setTimeout(fn, 100);
    if (char) {
      if (char.name === imported.char.name) {
        if (setups.length === 4) {
          execute(() => setPendingCode(2));
        } else {
          const sameChar = areEqual(char, imported.char);
          const sameTg = areEqual(target, imported.target);
          if (sameChar && sameTg) {
            execute(() => importSetup(false, false));
          } else {
            let code = 3;
            if (!sameChar) code = code * 10;
            if (!sameTg) code = code * 10 + 1;
            setPendingCode(code);
          }
        }
      } else {
        execute(() => setPendingCode(1));
      }
    } else {
      execute(startSession);
    }
  }, []);

  return !pendingCode ? (
    <Modal>
      <LoadingBar>
        <div />
      </LoadingBar>
    </Modal>
  ) : (
    <ImportPending
      imported={imported}
      code={pendingCode}
      startSession={startSession}
      importSetup={importSetup}
    />
  );
}
