import { findByCode, findByName } from "../helpers";
import artifacts from "./artifacts";
import characters from "./characters";
import weapons from "./weapons";

export const findCharacter = (char, select) => {
  const data = findByName(characters, char.name);
  if (select) {
    const result = {};
    for (const field of select.split(" ")) {
      result[field] = data[field];
    }
    return result;
  }
  return data;
};

export const findWeapon = ({ type, code }) => findByCode(weapons[type], code);

export function findArtifact({ code, type }) {
  const tgSet = findByCode(artifacts, code);
  if (type) {
    const { name, icon } = tgSet[type];
    return { beta: tgSet.beta, name, icon };
  }
  return tgSet;
}
export function findArtBuff(art, index) {
  const { buffs } = findArtifact(art);
  return buffs?.[index];
}

export function getCharacterData(char, select) {
  const defaultSelect = "nation vision";
  select = (select || defaultSelect) + " actvTalents";
  const { actvTalents, ...info } = findCharacter(char, select);
  let EBcost = 0;
  for (const tl of actvTalents) {
    if (tl.energyCost) {
      EBcost = tl.energyCost;
      break;
    }
  }
  return { ...info, EBcost };
}

export function getPartyData(party) {
  return party
    .filter((tm) => tm)
    .map((tm) => getCharacterData(tm, "name nation vision"));
}

