export const wpSimpleAnTmaker = (target, paths, rootScale) => (args) => {
  const { wpRfm, tkDesc, tracker } = args;
  const rootValue = Array.isArray(rootScale)
    ? rootScale.map((scale) => scale * (wpRfm + 3))
    : rootScale * (wpRfm + 3);
  if (args[target])
    addAndTrack(tkDesc, args[target], paths, rootValue, tracker);
};

