export const wpMainStatAtLv = (scale, lv) => {
  return baseAtkType[scale][levels.indexOf(lv)];
};

export const wpSubStatAtLv = (scale, lv) => {
  const curLv = bareLv(lv);
  const index = curLv === 1 ? 0 : curLv === 20 ? 1 : (curLv - 20) / 10;
  return subStatScale[scale][index];
};

