import { Span } from "../../styledCpns/DataDisplay";

const caStaminaMaker = (stamina) => ({
  name: "Charged Attack Stamina Cost",
  noCalc: true,
  getValue: () => stamina,
});
export const CaStamina = {
  20: caStaminaMaker(20),
  25: caStaminaMaker(25),
  50: caStaminaMaker(50),
};

// ----- SWORD -----
export const SwordNaDesc = {
  heading: "NA",
  content: <>Performs up to 5 rapid strikes.</>,
};
export const SwordCaDesc = {
  heading: "CA",
  content: (
    <>Consumes a certain amount of Stamina to unleash 2 rapid sword strikes.</>
  ),
};
export const SwordPaDesc_content = (
  <>
    Plunges from mid-air to strike the ground below, damaging opponents along
    the path and dealing AoE DMG upon impact.
  </>
);
export const SwordPaDesc = {
  heading: "PA",
  content: SwordPaDesc_content,
};
export const SwordDesc = [SwordNaDesc, SwordCaDesc, SwordPaDesc];

// ----- CLAYMORE -----
export const CaStaminaClaymore = [
  {
    name: "Charged Attack Stamina Cost",
    noCalc: true,
    getValue: () => "40/s",
  },
  {
    name: "Max Duration",
    noCalc: true,
    getValue: () => "5s",
  },
];
const ClaymoreNaDesc = (n) => <>Performs up to {n} consecutive strikes.</>;
const ClaymoreCaDesc = (type) => (
  <>
    Drains Stamina over time to perform continuous{" "}
    {type ? "spinning attacks against all nearby opponents" : "slashes"}.
    <br />
    At the end of the sequence, perform a more powerful slash.
  </>
);
const claymoreDescMaker = (n, type) => [
  { heading: "NA", content: ClaymoreNaDesc(n) },
  { heading: "CA", content: ClaymoreCaDesc(type) },
  { heading: "PA", content: SwordPaDesc },
];
export const ClaymoreDesc_4spin = claymoreDescMaker(4, 0);
export const ClaymoreDesc_5slash = claymoreDescMaker(5, 1);
export const ClaymoreDesc_4slash = claymoreDescMaker(4, 1);

// ----- CATALYST -----
const catalystCaDescMaker = (elmt) => ({
  heading: "CA",
  content: (
    <>
      Consumes a certain amount of Stamina to deal AoE{" "}
      <Span color={elmt}>{elmt} DMG</Span> after a short casting time.
    </>
  ),
});
export const CatalystCaDesc_Pyro = catalystCaDescMaker("Pyro");
export const CatalystCaDesc_Hydro = catalystCaDescMaker("Hydro");
export const CatalystCaDesc_Electro = catalystCaDescMaker("Electro");
export const CatalystCaDesc_Geo = catalystCaDescMaker("Geo");
export const CatalystCaDesc_Anemo = catalystCaDescMaker("Anemo");

export const CatalystPaDesc = (elmt, name) => ({
  heading: "CA",
  content: (
    <>
      Gathering the might of {elmt}, {name} plunges towards the ground from
      mid-air, damaging all opponents in her path. Deals AoE{" "}
      <Span color={elmt}>{elmt} DMG</Span> upon impact with the ground.
    </>
  ),
});

// ----- BOW -----
const bowNaDescMaker = (n) => ({
  heading: "NA",
  content: <>Perform up to {n} consecutive shots with a bow.</>,
});
export const BowNaDesc_4 = bowNaDescMaker(4);
export const BowNaDesc_5 = bowNaDescMaker(5);
export const BowNaDesc_6 = bowNaDescMaker(6);

export const BowCaDesc_contentMaker = (thing1, thing2, elmt) => (
  <>
    Perform a more precise Aimed Shot with increased DMG.
    <br />
    While aiming, {thing1} will accumulate on the arrowhead. {thing2} will deal{" "}
    <Span color={elmt}>{elmt} DMG</Span>.
  </>
);
export const BowCaDesc = (thing1, thing2, elmt) => ({
  heading: "CA",
  content: BowCaDesc_contentMaker(thing1, thing2, elmt),
});

export const BowPaDesc_content = (
  <>
    Fires off a shower of arrows in mid-air before falling and striking the
    ground, dealing AoE DMG upon impact.
  </>
);
export const BowPaDesc = {
  heading: "PA",
  content: BowPaDesc_content,
};

// ----- POLEARM -----
const polearmNaDescMaker = (n) => ({
  heading: "NA",
  content: <>Performs up to {n} consecutive spear strikes.</>,
});
export const PolearmNaDesc_5 = polearmNaDescMaker(5);
export const PolearmNaDesc_6 = polearmNaDescMaker(6);

const PolearmCaDesc = (
  <>
    Consumes a certain amount of Stamina to lunge forward, dealing damage to
    opponents along the way.
  </>
);
export const PolearmUpCaDesc = {
  heading: "CA",
  content: (
    <>Consumes a certain amount of Stamina to perform an upward thrust.</>
  ),
};

const polearmDescMaker = (n) => [
  { ...polearmNaDescMaker(n) },
  { heading: "CA", content: PolearmCaDesc },
  SwordPaDesc,
];
export const PolearmDesc_4 = polearmDescMaker(4);
export const PolearmDesc_5 = polearmDescMaker(5);
export const PolearmDesc_6 = polearmDescMaker(6);

