import styled from "styled-components";
import { colors } from "../../../theme";
import { VStack } from "../../../styledCpns/Layout";

export const StyledDmgTables = styled.div`
  & > div:not(:last-child) {
    margin-bottom: 1rem;
  }
`;

// also [../../Canopy/ImportPending]
export const StyledTable = styled.table`
  margin-bottom: 0.5rem;
  min-width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
  line-height: 1.43;
  th,
  td {
    padding: 0.25rem 0.5rem;
    border-left: 1px solid ${colors.cardBg1};
    border-right: 1px solid ${colors.cardBg1};
    cursor: default;
  }
  td:first-child {
    font-weight: 700;
  }
  td:not(:first-child) {
    text-align: right;
  }
  tr:nth-child(2n) {
    background-color: ${colors.cardBg2};
  }
  tr:nth-child(2n + 1) {
    background-color: ${colors.cardBg1};
  }
  tr:first-child {
    background-color: #01121b;
  }
  tr:not(:first-child):hover {
    background-color: #8a1200;
  }
  .lightGold-text {
    color: ${colors.lightGold};
  }
  .sticky-col {
    position: sticky;
    z-index: 2;
    left: 0;
    min-width: 8.5rem;
    background-color: inherit;
  }
  .limited-width {
    width: 8.5rem;
  }
  .long-arrow {
    position: absolute;
    top: 50%;
    right: 0.375rem;
    margin-top: -0.425rem;
    font-size: inherit;
  }
  .higher {
    color: ${colors.green};
  }
  .lower {
    color: #ff5d61;
  }
  .tooltip_wrapper {
    padding-right: 1.125rem !important;
    min-width: 5rem;
  }
  .tooltip_wrapper:hover .tooltip {
    display: block;
  }
  .tooltip {
    position: absolute;
    bottom: calc(50% + 0.625rem);
    right: 1rem;
    z-index: 1;
    display: none;
    padding: 0.25rem 0.5rem 0.125rem;
    border-radius: 0.25rem;
    background: black;
  }
`;

export const Group = styled(VStack)`
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: ${colors.cardBg1};
`;
