import { useDispatch } from "react-redux";
import {
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  ENHANCE_ART,
  ENHANCE_ART_BY_4,
  ENHANCE_ART_TO_MAX,
  REFINE_WP,
  UPGRADE_WP
} from "../../app/databaseSlice";
import ArtifactCard from "./ArtifactCard";
import WeaponCard from "./WeaponCard";

export function MyWpCard({ weapon, mutable }) {
  const dispatch = useDispatch();
  return (
    <WeaponCard
      weapon={weapon}
      mutable={mutable}
      upgrade={(level) => dispatch(UPGRADE_WP({ ID: weapon.ID, level }))}
      refine={(rfm) => dispatch(REFINE_WP({ ID: weapon.ID, rfm }))}
    />
  );
}

export function MyArtCard(props) {
  const {
    art: { ID, rarity }
  } = props;
  const dispatch = useDispatch();
  return (
    <ArtifactCard
      {...props}
      enhance={(e) =>
        dispatch(ENHANCE_ART({ ID, level: +e.target.value.substr(1) }))
      }
      up4={() => dispatch(ENHANCE_ART_BY_4({ ID, rarity }))}
      toMax={() => dispatch(ENHANCE_ART_TO_MAX({ ID, rarity }))}
      changeMSType={(e) =>
        dispatch(CHANGE_ART_MS_TYPE({ ID, type: e.target.value }))
      }
      changeSSType={(type, ssIndex) =>
        dispatch(CHANGE_ART_SS_TYPE({ ID, ssIndex, type }))
      }
      changeSSVal={(value, ssIndex) =>
        dispatch(CHANGE_ART_SS_VAL({ ID, ssIndex, value }))
      }
    />
  );
}
