import { elmtDebuffDesc, rsnNames } from "../../../configs";
import { findArtifact, findCharacter } from "../../../data/controllers";
import { findByIndex } from "../../../helpers";
import { Text } from "../../../styledCpns/DataDisplay";
import { NoContent } from "../../Calculator/Modifiers/CmnCpns";
import { ExpandableList } from "../../CommonCpns";
import { ModReader } from "./styles";

export default function Debuffs(props) {
  const { char, elmtMCs, party } = props;
  return (
    <ExpandableList
      headingList={[
        "Resonance & Reactions",
        "Self",
        "Party",
        "Artifacts",
        "Custom"
      ]}
      contentList={[
        <RnR
          superconduct={elmtMCs.superconduct}
          resonance={elmtMCs.resonance}
        />,
        <SelfDebuffs char={char} selfDCs={props.selfDCs} party={party} />,
        <PartyDebuffs char={char} party={party} />,
        <ArtifactDebuffs artSubDCs={props.artSubDCs} />,
        <CustomDebuffs customDCs={props.customDCs} />
      ]}
    />
  );
}

const check = (content) =>
  content.length ? content : <NoContent type="debuffs" />;

function RnR({ superconduct, resonance }) {
  const content = [];
  if (superconduct) {
    content.push(
      <ModReader
        key="sc"
        heading="Superconduct"
        desc={elmtDebuffDesc.Superconduct}
      />
    );
  }
  const geoName = rsnNames.Geo;
  if (resonance.some((rsn) => rsn.name === geoName)) {
    content.push(
      <ModReader key="geo" heading={geoName} desc={elmtDebuffDesc[geoName]} />
    );
  }
  return check(content);
}

function SelfDebuffs({ char, selfDCs, party }) {
  const { debuffs } = findCharacter(char);
  const content = selfDCs.map(({ index }) => {
    const debuff = findByIndex(debuffs, index);
    return (
      <ModReader
        key={index}
        heading={debuff.src}
        desc={debuff.desc({ fromSelf: true, char, party })}
      />
    );
  });
  return check(content);
}

function PartyDebuffs({ char, party }) {
  const content = [];
  for (const tm of party.filter((m) => m && m.DCs.length)) {
    const { name, vision, debuffs } = findCharacter(tm);
    content.push(
      <Text
        key={name}
        className="pt-2 mb-m1"
        variant="h6"
        bold
        color={vision}
        align="center"
      >
        {name.toUpperCase()}
      </Text>
    );
    for (const { index, inputs } of tm.DCs) {
      const debuff = findByIndex(debuffs, index);
      content.push(
        <ModReader
          key={name + index}
          heading={debuff.src}
          desc={debuff.desc({ fromSelf: false, char, inputs })}
          labels={debuff.labels}
          inputTypes={debuff.inputTypes}
          inputs={inputs}
        />
      );
    }
  }
  return check(content);
}

function ArtifactDebuffs({ artSubDCs }) {
  const content = artSubDCs.map(({ code, index, inputs }) => {
    const { name, debuffs } = findArtifact({ code });
    const debuff = debuffs[index];
    return (
      <ModReader
        key={index}
        heading={name}
        desc={debuff.desc}
        labels={debuff.labels}
        inputTypes={debuff.inputTypes}
        inputs={inputs}
      />
    );
  });
  return check(content);
}

function CustomDebuffs({ customDCs }) {
  const content = customDCs.map(({ type, val }, i) => (
    <div key={i} className="pt-2 flex justify-end">
      <Text className="mr-3">{type}</Text>
      <Text color="orange" bold>
        {val}%
      </Text>
    </div>
  ));
  return check(content);
}
