import artifacts from "../data/artifacts";
import { findCharacter, findWeapon } from "../data/controllers";
import weapons from "../data/weapons";
import { findByIndex } from "./index";
import { filterAllMCs } from "./processors";

export default function adjustDB(data) {
  if (!data.Setups) data.Setups = [];
  const { version = 0 } = data;
  if (!version) {
    changeArtIDsAndParty(data);
  }
  if (version < 2) changeWpNameToCode(data);
  if (version < 2.1) changeArtNameToCode(data);

  const allOutdates = [];
  for (const st of data.Setups) {
    if (st.type === "complex") continue;
    const list = extractOutdatedCharMCs(st).concat(extractOutdatedWpMCs(st));
    if (list.length) {
      allOutdates.push({
        name: st.name,
        members: [st.char.name].concat(
          st.party.filter((tm) => tm).map((tm) => tm.name)
        ),
        list
      });
    }
  }
  return [data, allOutdates.length ? allOutdates : null];
}

function extractOutdatedCharMCs(st) {
  const result = [];
  function extract(list, name, type) {
    const outdates = [];
    const filtered = [];
    const mods = findCharacter({ name })[type];
    for (const item of list) {
      const { index } = item;
      const mod = findByIndex(mods, index);
      if (!mod) continue;
      if (mod.outdated) {
        outdates.push({ cate: 1, name, type, index });
      } else filtered.push(item);
    }
    return [filtered, outdates];
  }
  for (const ctrlType of ["BCs", "DCs"]) {
    const type = ctrlType === "BCs" ? "buffs" : "debuffs";
    const extracted1 = extract(st.selfMCs[ctrlType], st.char.name, type);
    st.selfMCs[ctrlType] = extracted1[0];
    result.push(...extracted1[1]);
    for (const tm of st.party) {
      if (!tm) continue;
      const extracted2 = extract(tm[ctrlType], tm.name, type);
      tm[ctrlType] = extracted2[0];
      result.push(...extracted2[1]);
    }
  }
  return result;
}

function extractOutdatedWpMCs(st) {
  const filtered = [];
  const outdates = [];
  const { buffs } = findWeapon(st.weapon);
  for (const ctrl of st.weapon.BCs) {
    const { index } = ctrl;
    const { outdated } = buffs.find((buff) => buff.index === index);
    if (outdated)
      outdates.push({
        cate: 2,
        name: st.weapon.code,
        type: st.weapon.type,
        index
      });
    else filtered.push(ctrl);
  }
  st.weapon.BCs = filtered;
  return outdates;
}

function changeArtIDsAndParty(data) {
  for (const char of data.Characters) {
    const { flower, plume, sands, goblet, circlet } = char.artIDs;
    char.artIDs = [flower, plume, sands, goblet, circlet];
  }
  for (const i in data.Setups) {
    if (data.Setups[i].type === "complex") continue;
    for (const tmI in data.Setups[i].party) {
      if (!data.Setups[i].party[tmI].name) {
        data.Setups[i].party[tmI] = null;
      } else {
        delete data.Setups[i].party[tmI].infusion;
      }
    }
    const { ID, name, type, char, target } = data.Setups[i];
    data.Setups[i] = {
      ID,
      name,
      type,
      char,
      ...filterAllMCs(data.Setups[i]),
      target
    };
  }
}

function changeWpNameToCode(data) {
  function getCode(wp) {
    return weapons[wp.type].find(({ name }) => name === wp.name).code;
  }
  for (const wp of data.Weapons) {
    wp.code = getCode(wp);
    delete wp.name;
  }
  for (const st of data.Setups) {
    if (st.type !== "complex") {
      const {
        weapon,
        subWpMCs: { BCs }
      } = st;
      weapon.code = getCode(weapon);
      delete weapon.name;
      delete weapon.ID;
      for (const type in BCs) {
        if (BCs[type].length) {
          for (const index in BCs[type]) {
            const { name } = BCs[type][index];
            BCs[type][index].code = getCode({ type, name });
            delete BCs[type][index].name;
          }
        }
      }
      delete st.subWpMCs.MCs;
      st.subWpMCs.DCs = {};
    }
  }
}

function changeArtNameToCode(data) {
  function getCode(set) {
    return artifacts.find(({ name }) => name === set).code;
  }
  for (const art of data.Artifacts) {
    art.code = getCode(art.set);
    delete art.set;
  }
  function change(arr) {
    for (const item of arr) {
      if (item) {
        item.code = getCode(item.set);
        delete item.set;
      }
    }
  }
  for (const st of data.Setups) {
    if (st.type !== "complex") {
      const { art } = st;
      change(art.pieces);
      for (const set of art.sets) {
        set.code = getCode(set.name);
        delete set.name;
      }
      change(art.subBCs);
      change(art.subDCs);
    }
  }
}
