import { addAndTrack } from "../../calculators/helpers";
import { round2 } from "../../helpers";
import { Green } from "../../styledCpns/DataDisplay";
import { wpSimpleAnTmaker } from "./config";

export const RoyalSeries = {
  buffs: [
    {
      index: 0,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      values: [5],
      addBnes: ({ ATTRs, wpRfm, inputs, tkDesc, tracker }) => {
        const bnValue = (6 + wpRfm * 2) * inputs[0];
        addAndTrack(tkDesc, ATTRs, "CRIT Rate", bnValue, tracker);
      },
      desc: ({ wpRfm }) => RoyalSeries.pasvDescs({ wpRfm })[0].buff
    }
  ],
  pasvName: "Focus",
  pasvDescs: ({ wpRfm }) => [
    {
      buff: (
        <>
          Upon dealing damage to an opponent, increases <Green>CRIT Rate</Green>{" "}
          by <Green b>{6 + wpRfm * 2}%</Green>. Max <Green b>5</Green> stacks. A
          CRIT hit removes all existing stacks.
        </>
      )
    }
  ]
};

