export const LiyueSeries = {
  addBnes: wpSimpleAnTmaker("ATTRs", "Shield Strength", 5),
  buffs: [
    {
      index: 0,
      affect: "self",
      labels: ["Stacks", "Protected by a Shield"],
      inputTypes: ["stacks", "check"],
      values: [5],
      addBnes: ({ ATTRs, wpRfm, inputs, tkDesc, tracker }) => {
        let bnValue = (3 + wpRfm) * inputs[0];
        if (inputs[1]) bnValue *= 2;
        addAndTrack(tkDesc, ATTRs, "ATK%", bnValue, tracker);
      },
      desc: ({ wpRfm }) => LiyueSeries.pasvDescs({ wpRfm })[0].buff
    }
  ],
  pasvName: "Golden Majesty",
  pasvDescs: ({ wpRfm }) => [
    {
      bonus: <>Increases Shield Strength by {15 + wpRfm * 5}%</>,
      buff: (
        <>
          Scoring hits on opponents increases <Green>ATK</Green> by{" "}
          <Green b>{3 + wpRfm}%</Green> for 8s. Max <Green b>5</Green> stacks.
          Can only occur once every 0.3s. While protected by a shield, this ATK
          increase effect is increased by <Green>100%</Green>.
        </>
      )
    }
  ]
};

export const LithicSeries = {
  addBnes: ({ ATTRs, wpRfm, charData, partyData, tkDesc, tracker }) => {
    if (partyData) {
      const stacks = partyData.reduce(
        (result, data) => (data.nation === "Liyue" ? result + 1 : result),
        charData.nation === "Liyue" ? 1 : 0
      );
      const bnValues = [(6 + wpRfm) * stacks, (2 + wpRfm) * stacks];
      addAndTrack(tkDesc, ATTRs, ["ATK%", "CRIT Rate"], bnValues, tracker);
    }
  },
  pasvName: "Lithic Axiom - Unity",
  pasvDescs: ({ wpRfm }) => [
    {
      buff: (
        <>
          For every character in the party who hails from Liyue, the character
          who equips this weapon gains <Green b>{6 + wpRfm}%</Green>{" "}
          <Green>ATK</Green> increase and <Green b>{2 + wpRfm}%</Green>{" "}
          <Green>CRIT Rate</Green> increase. This effect stacks up to{" "}
          <Green b>4</Green> times.
        </>
      )
    }
  ]
};

