export const WatatsumiSeries = {
  addBnes: ({ hitBnes, wpRfm, charData, partyData, tkDesc, tracker }) => {
    if (partyData && hitBnes) {
      const maxEnergy = partyData.reduce(
        (result, data) => result + data.EBcost,
        charData.EBcost
      );
      const bnValue = round2(maxEnergy * (0.09 + wpRfm * 0.03));
      addAndTrack(tkDesc, hitBnes, "EB.pct", bnValue, tracker);
    }
  },
  pasvName: "Watatsumi Wavewalker",
  pasvDescs: ({ wpRfm }) => [
    {
      bonus: (
        <>
          For every point of the entire party's combined maximum Energy
          capacity, the <Green>Elemental Burst DMG</Green> of the character
          equipping this weapon is increased by{" "}
          <Green b>{0.09 + wpRfm * 0.03}%</Green>, up to a maximum of{" "}
          <Green b>{30 + wpRfm * 10}%</Green>.
        </>
      )
    }
  ]
};

