import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CHANGE_ELMT_MCS,
  CHANGE_MCS_INPUT,
  TOGGLE_MCS,
  TOGGLE_RESONANCE
} from "../../../../app/calculatorSlice";
import { elmtDebuffDesc, swirlables } from "../../../../configs";
import { findArtifact } from "../../../../data/controllers";
import { indexByName } from "../../../../helpers";
import { Section, StyledSelect, StyledSetter } from "../CmnCpns";

export function ElmtDebuffs() {
  const { elmtMCs, curSetupI } = useSelector(calculator);
  const dispatch = useDispatch();
  const { resonance, superconduct } = elmtMCs[curSetupI];
  const geoRsnI = indexByName(resonance, "Enduring Rock");
  let geoRsnOn;
  if (geoRsnI >= 0) {
    geoRsnOn = resonance[geoRsnI].activated;
  }
  return (
    <>
      <Section
        checked={superconduct}
        handleCheck={() =>
          dispatch(
            CHANGE_ELMT_MCS({ type: "superconduct", value: !superconduct })
          )
        }
        heading="Superconduct"
        desc={elmtDebuffDesc.Superconduct}
      />
      {geoRsnI >= 0 && (
        <Section
          key="rock"
          checked={geoRsnOn}
          handleCheck={() =>
            dispatch(
              TOGGLE_RESONANCE({ rsnIndex: geoRsnI, activated: !geoRsnOn })
            )
          }
          heading="Enduring Rock"
          desc={elmtDebuffDesc["Enduring Rock"]}
        />
      )}
    </>
  );
}

export function ArtDebuffs() {
  const { artChest, curSetupI } = useSelector(calculator);
  const dispatch = useDispatch();
  const { activated, code, inputs, index } = artChest[curSetupI].subDCs[0];
  const { name, debuffs } = findArtifact({ code });
  const path = {
    cat: "artChest",
    type: "subDCs",
    mcIndex: 0
  };
  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(
          TOGGLE_MCS({
            ...path,
            activated: !activated
          })
        )
      }
      heading={name}
      desc={debuffs[index].desc}
      setters={
        <StyledSetter>
          <p className="label">Element swirled</p>
          <StyledSelect
            value={inputs[0]}
            onChange={(e) =>
              dispatch(
                CHANGE_MCS_INPUT({
                  ...path,
                  inpIndex: 0,
                  value: e.target.value
                })
              )
            }
          >
            {swirlables.map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </StyledSelect>
        </StyledSetter>
      }
    />
  );
}
