import { useState } from "react";
import { FaInfo } from "react-icons/fa";
import { useSelector } from "react-redux";
import { selActualChosenSetup } from "../../app/setupsSlice";
import calculateAll, { charDataSelect } from "../../calculators/calculateAll";
import { findCharacter } from "../../data/controllers";
import { deepCopy } from "../../helpers";
import { StyledInv } from "../../styledCpns/Database";
import { Green, Red, Text } from "../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import { HowToModal } from "../CommonCpns";
import { AddCtrl, CombineCtrl, RemoveCtrl, ShareCtrl } from "./Controllers";
import DmgResults from "./DmgResults";
import SetupModal from "./Modal";
import SetupList from "./SetupList";

export default function MySetups() {
  const [infoOn, setInfoOn] = useState(false);
  const [modalType, setModalType] = useState(null);
  const [util, setUtil] = useState({ type: "", ID: null });
  const [combining, setCombining] = useState(false);
  const chosenSetup = useSelector(selActualChosenSetup);

  const closeUtil = () => setUtil({ type: "", ID: null });

  let infusion;
  let ATTRs;
  let artSBnes;
  let rxnBnes;
  let dmg;

  if (chosenSetup) {
    const {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      customMCs = { BCs: [], DCs: [] },
      party,
      target
    } = chosenSetup;
    const charData = findCharacter(char, charDataSelect);
    [infusion, ATTRs, , rxnBnes, artSBnes, dmg] = calculateAll(
      deepCopy(char),
      charData,
      selfMCs,
      party,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      customMCs,
      target
    );
  }
  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <Box className="btn_bar" height="2.5rem">
          <IconBtn
            className="mr-3 glow-on-hover"
            variant="positive"
            size="1.625rem"
            fontSize="0.875rem"
            onClick={() => setInfoOn(true)}
          >
            <FaInfo />
          </IconBtn>
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => setCombining(true)}
          >
            Combine
          </Button>
        </Box>
        <Box className="body" height="35rem">
          <SetupList setModalType={setModalType} setUtil={setUtil} />
          <DmgResults setup={chosenSetup} dmg={dmg} />
        </Box>
      </StyledInv>
      {modalType && chosenSetup && (
        <SetupModal
          type={modalType}
          result={{ infusion, ATTRs, artSBnes, rxnBnes }}
          setup={chosenSetup}
          close={() => setModalType(null)}
        />
      )}
      {util.type === "remove" && util.ID && (
        <RemoveCtrl removedID={util.ID} close={closeUtil} />
      )}
      {util.type === "shared" && util.ID && (
        <ShareCtrl setup={chosenSetup} close={closeUtil} />
      )}
      {util.type === "add" && util.ID && (
        <AddCtrl addingID={util.ID} close={closeUtil} />
      )}
      {combining && <CombineCtrl close={() => setCombining(false)} />}
      {infoOn && <Info close={() => setInfoOn(false)} />}
    </Box>
  );
}

function Info({ close }) {
  return (
    <HowToModal
      content={
        <div>
          <Text>
            - <Green>Modify Setups</Green>: When you press the wrench button on
            a Setup, you're pushing a <Red>copy</Red> of it to the Calculator,
            so don't forget to save the modified copy if you want to apply the
            changes to that Setup.
          </Text>
          <Text className="mt-2">
            - <Green>Complex Setup</Green>: It is the result of combining Setups
            of 4 party members. On this complex, switch to teammates' Setups by
            pressing their icons. Break the complex into individual Setups again
            by pressing the link / chain icon before its name.
          </Text>
          <Text className="mt-2">
            - You cannot change teammates when modifying a Setup that is in a
            complex.
          </Text>
          <Text className="mt-2">
            - You can build <Green>teammate's Setups</Green> based on a saved
            Setup by hovering over teammate's icon and press the calculator
            button that would appear. Party members and Target will be the same.
            Some Modifiers will remain activated.
          </Text>
        </div>
      }
      close={close}
    />
  );
}
