import { useState } from "react";
import { FaPlus, FaUnlink, FaLink, FaCalculator } from "react-icons/fa";
import { useDispatch } from "react-redux";
import { CHOOSE_SETUP, UNCOMBINE_COMPLEX } from "../../../app/setupsSlice";
import { IconBtn } from "../../../styledCpns/Inputs";
import { LinkIcon } from "./styles";

export function AddButton({ addable, add }) {
  return (
    <IconBtn
      className={addable ? "glow-on-hover" : "unavailable"}
      variant="neutral"
      onClick={() => {
        if (addable) add();
      }}
    >
      <FaPlus />
    </IconBtn>
  );
}

export function LinkButton({ ID, shownID }) {
  const dispatch = useDispatch();
  const uncombine = () => {
    dispatch(UNCOMBINE_COMPLEX(ID));
    setTimeout(() => {
      dispatch(CHOOSE_SETUP(shownID));
    }, 10);
  };
  if (window.innerWidth <= 1050) {
    return (
      <IconBtn
        className="mr-2 glow-on-hover"
        variant="negative"
        onClick={uncombine}
      >
        <FaUnlink />
      </IconBtn>
    );
  }
  return <LargeVersion uncombine={uncombine} />;
}

function LargeVersion({ uncombine }) {
  const [hovered, setHovered] = useState(false);
  const Icon = hovered ? FaUnlink : FaLink;
  return (
    <LinkIcon
      className={"mr-2 flex-center pointer " + (hovered ? "unlink" : "link")}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={uncombine}
    >
      <Icon />
    </LinkIcon>
  );
}

export function BuildTmBtn({ buildTmSetup }) {
  return (
    <div className="calc-btn">
      <IconBtn variant="positive" onClick={buildTmSetup}>
        <FaCalculator />
      </IconBtn>
    </div>
  );
}
