import { pctOf, rsnBuffDesc } from "../../../configs";
import {
  findArtifact,
  findCharacter,
  findWeapon
} from "../../../data/controllers";
import { findByIndex } from "../../../helpers";
import { Text } from "../../../styledCpns/DataDisplay";
import { NoContent } from "../../Calculator/Modifiers/CmnCpns";
import { AmpRxnDesc, ExpandableList } from "../../CommonCpns";
import { ModReader } from "./styles";

export default function Buffs(props) {
  const { char, ATTRs, party, arts } = props;
  return (
    <ExpandableList
      headingList={[
        "Resonance & Reactions",
        "Self",
        "Party",
        "Weapons",
        "Artifacts",
        "Custom"
      ]}
      contentList={[
        <RnR
          char={char}
          elmtMCs={props.elmtMCs}
          rxnBnes={props.rxnBnes}
          infusion={props.infusion}
        />,
        <SelfBuffs
          char={char}
          ATTRs={ATTRs}
          selfBCs={props.selfBCs}
          party={party}
        />,
        <PartyBuffs char={char} party={party} />,
        <WeaponBuffs
          weapon={props.weapon}
          ATTRs={ATTRs}
          subWpBCs={props.subWpBCs}
        />,
        <ArtifactBuffs sets={arts.sets} BCs={arts.BCs} subBCs={arts.subBCs} />,
        <CustomBuffs customBCs={props.customBCs} />
      ]}
    />
  );
}

const check = (content) =>
  content.length ? content : <NoContent type="buffs" />;

function RnR(props) {
  const content = [];
  const { resonance, ampRxn, naAmpRxn } = props.elmtMCs;
  for (const { name } of resonance) {
    content.push(
      <ModReader key={name} heading={name} desc={rsnBuffDesc[name]} />
    );
  }
  if (ampRxn) {
    content.push(
      <ModReader
        key="ampRxn"
        heading={ampRxn}
        desc={
          <AmpRxnDesc
            elmt={findCharacter(props.char).vision}
            mult={props.rxnBnes[ampRxn]}
          />
        }
      />
    );
  }
  if (naAmpRxn) {
    content.push(
      <ModReader
        key="naAmpRxn"
        heading={naAmpRxn + " (external infusion)"}
        desc={
          <AmpRxnDesc
            elmt={props.infusion.NA}
            mult={props.rxnBnes["na" + naAmpRxn]}
          />
        }
      />
    );
  }
  return check(content);
}

function SelfBuffs({ char, ATTRs, selfBCs, party }) {
  const { buffs } = findCharacter(char);
  const content = selfBCs.map(({ index, inputs }) => {
    const buff = findByIndex(buffs, index);
    return (
      <ModReader
        key={index}
        heading={buff.src}
        desc={buff.desc({
          toSelf: true,
          ATTRs,
          char,
          charBCs: selfBCs,
          inputs,
          party
        })}
        labels={buff.selfLabels}
        inputTypes={buff.inputTypes}
        inputs={inputs}
      />
    );
  });
  return check(content);
}

function PartyBuffs({ char, party }) {
  const content = [];
  for (const tm of party.filter((m) => m && m.BCs.length)) {
    const { name, vision, buffs } = findCharacter(tm);
    content.push(
      <Text
        key={name}
        className="pt-2 mb-m1"
        variant="h6"
        bold
        color={vision}
        align="center"
      >
        {name.toUpperCase()}
      </Text>
    );
    for (const { index, inputs } of tm.BCs) {
      const buff = findByIndex(buffs, index);
      content.push(
        <ModReader
          key={name + index}
          heading={buff.src}
          desc={buff.desc({
            toSelf: false,
            char,
            charBCs: tm.BCs,
            inputs,
            party
          })}
          labels={buff.labels}
          inputTypes={buff.inputTypes}
          inputs={inputs}
        />
      );
    }
  }
  return check(content);
}

function WeaponBuffs({ weapon, ATTRs, subWpBCs }) {
  const content = [];
  for (const { index, inputs } of weapon.BCs) {
    const { name, buffs } = findWeapon(weapon);
    const buff = findByIndex(buffs, index);
    content.push(
      <ModReader
        key={weapon.code.toString() + index}
        heading={name}
        desc={buff.desc({ wpRfm: weapon.refinement, ATTRs })}
        labels={buff.labels}
        inputTypes={buff.inputTypes}
        inputs={inputs}
      />
    );
  }
  for (const type in subWpBCs) {
    for (const ctrl of subWpBCs[type]) {
      const { code, refinement: wpRfm, index } = ctrl;
      const { name, buffs, pasvDescs } = findWeapon({ type, code });
      const buff = findByIndex(buffs, index);
      content.push(
        <ModReader
          key={"sub" + code + index}
          heading={name}
          desc={pasvDescs({ wpRfm })[0].buff}
          labels={["Refinement", ...(buff.labels || [])]}
          inputTypes={["text", ...(buff.inputTypes || [])]}
          inputs={[wpRfm, ...(ctrl.inputs || [])]}
        />
      );
    }
  }
  return check(content);
}

function ArtifactBuffs({ sets, BCs, subBCs }) {
  const content = [];
  const mainCode = sets[0]?.code;
  for (const { index, inputs } of BCs) {
    const { name, buffs } = findArtifact({ code: mainCode });
    const buff = buffs[index];
    content.push(
      <ModReader
        key={mainCode.toString() + index}
        heading={name + " (self)"}
        desc={buff.desc()}
        labels={buff.labels}
        inputTypes={buff.inputTypes}
        inputs={inputs}
      />
    );
  }
  for (const { code, index, inputs } of subBCs) {
    const { name, buffs } = findArtifact({ code });
    const buff = buffs[index];
    content.push(
      <ModReader
        key={code.toString() + index}
        heading={name}
        desc={buff.desc()}
        labels={buff.labels}
        inputTypes={buff.inputTypes}
        inputs={inputs}
      />
    );
  }
  return check(content);
}

function CustomBuffs({ customBCs }) {
  const content = customBCs.map(({ catKey, type, val }, i) => (
    <div key={i} className="pt-2 flex justify-end">
      <Text className="mr-3">{type}</Text>
      <Text color="orange" bold>
        {val}
        {catKey > 1 ? "%" : pctOf(type)}
      </Text>
    </div>
  ));
  return check(content);
}
