import { useCallback, useState } from "react";
import { FaPlus } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { APPLY_SETTINGS_CALC, calculator } from "../../../app/calculatorSlice";
import {
  getNewSetupName,
  getSetupInfo
} from "../../../app/calculatorSlice/helpers";
import { APPLY_SETTINGS_UI, CLOSE_SETTINGS } from "../../../app/uiSlice";
import { Text } from "../../../styledCpns/DataDisplay";
import { Button } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { colors } from "../../../theme";
import { ButtonBar, CloseBtn } from "../../CommonCpns";
import { HiddenScroll } from "../../CommonCpns/HiddenSpace";
import Configs from "./Configs";
import { SaveCtrl, SharedCtrl } from "./ExtraCtrls";
import SetupCtrl from "./SetupCtrl";

export default function HiddenSettings({ height, ...others }) {
  const { settingsOn } = useSelector((state) => state.ui);
  return (
    <AbsoluteBox className="full-w" bottom={0} left={0}>
      <HiddenScroll
        on={settingsOn}
        style={{
          borderRadius: "0.625rem",
          backgroundColor: colors.cardBg3
        }}
        openHeight={height / 16 + 2 + "rem"}
        duration={250}
        timingFunc="ease-in-out"
      >
        <Settings {...others} />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function Settings({ importBtn }) {
  const { setups, curSetupI, configs } = useSelector(calculator);
  const { stdSetupI, cmpSetupIs } = useSelector((state) => state.ui);
  const [tempSetups, setTempSetups] = useState(
    setups.map((st, index) => {
      const temp = {
        ...st,
        index,
        checked: cmpSetupIs.includes(index),
        expanded: false
      };
      if (index === stdSetupI) temp.isStandard = true;
      if (index === curSetupI) temp.isCurrent = true;
      return temp;
    })
  );
  const [tempConfigs, setTempConfigs] = useState(configs);
  const [err, setErr] = useState(false);
  const [savedI, setSavedI] = useState(null);
  const [sharedI, setSharedI] = useState(null);
  const dispatch = useDispatch();

  const tryApply = useCallback(() => {
    let ok = true;
    const names = [];
    for (const { name } of tempSetups) {
      if (!name.length || names.includes(name)) {
        ok = false;
        break;
      } else names.push(name);
    }
    if (ok) {
      const setups = [];
      const cmpSetupIs = [];
      const indexes = [];
      let standardI;
      let currentI = -1;
      for (const i in tempSetups) {
        const st = tempSetups[i];
        const { name, ID, type } = tempSetups[i];
        setups.push({ name, ID, type });
        indexes.push(st.index);
        if (st.checked) cmpSetupIs.push(+i);
        if (st.isStandard) standardI = +i;
        if (st.isCurrent) currentI = +i;
      }
      dispatch(
        APPLY_SETTINGS_CALC({
          setups,
          indexes,
          tempConfigs,
          currentI,
          standardI
        })
      );
      dispatch(APPLY_SETTINGS_UI({ cmpSetupIs, standardI }));
      dispatch(CLOSE_SETTINGS());
    } else {
      setErr(true);
    }
  }, [tempConfigs, tempSetups, dispatch]);

  return (
    <div className="p-3 full-h flex-col">
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <CloseBtn
          className="glow-on-hover"
          onClick={() => dispatch(CLOSE_SETTINGS())}
        />
      </AbsoluteBox>
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box className="mt-2 grow-1 flex-col hide-sb">
        {tempSetups.map((setup, index) => (
          <SetupCtrl
            key={index}
            setup={setup}
            index={index}
            belowLimit={tempSetups.length < 4}
            setTempSetups={setTempSetups}
            setSavedI={setSavedI}
            setSharedI={setSharedI}
          />
        ))}
        {tempSetups.length < 4 && (
          <div className="mt-3">
            <Box className="mb-4" height="2rem">
              <Button
                className="full-h full-w flex-center glow-on-hover"
                bgColor="#2269d6"
                color="white"
                borderRadius="1rem"
                onClick={() => {
                  setTempSetups((prev) => {
                    const newTSs = [...prev];
                    newTSs.push({
                      ...getSetupInfo({ name: getNewSetupName(prev) }),
                      index: null,
                      checked: false,
                      expanded: false
                    });
                    return newTSs;
                  });
                }}
              >
                <FaPlus />
              </Button>
            </Box>
            {importBtn}
          </div>
        )}
        <Configs configs={tempConfigs} setConfigs={setTempConfigs} />
        {err && (
          <Text className="mt-3" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <ButtonBar
        className="mt-3"
        variants={["positive"]}
        texts={["Apply"]}
        handlers={[tryApply]}
      />
      {savedI !== null && (
        <SaveCtrl
          savedSetup={tempSetups[savedI]}
          close={() => setSavedI(null)}
        />
      )}
      {sharedI !== null && (
        <SharedCtrl
          sharedSetup={tempSetups[sharedI]}
          close={() => setSharedI(null)}
        />
      )}
    </div>
  );
}
