import { useSelector } from "react-redux";
import { FaLongArrowAltDown, FaLongArrowAltUp } from "react-icons/fa";
import { calculator } from "../../../app/calculatorSlice";

const display = (value) =>
  Array.isArray(value)
    ? value.map((nc) => Math.round(nc)).join(" + ")
    : Math.round(value);

export function OneSetup({ stdValues, subNames }) {
  return (
    <tbody>
      <tr>
        <th className="limited-width" />
        <th>Non-crit</th>
        <th>Crit</th>
        <th className="lightGold-text">Avg.</th>
      </tr>
      {subNames.map((name, i) => {
        return (
          <tr key={i}>
            <td className="limited-width">{name}</td>
            <td>{display(stdValues[name]["Non-crit"])}</td>
            <td>{display(stdValues[name].Crit)}</td>
            <td className="lightGold-text">
              {display(stdValues[name].Average)}
            </td>
          </tr>
        );
      })}
    </tbody>
  );
}

export function MultiSetup({ stdValues, focus, name: { main, subs } }) {
  const { setups, allDmg } = useSelector(calculator);
  const { cmpSetupIs, stdSetupI } = useSelector((state) => state.ui);
  const otherSetupIs = cmpSetupIs.filter((i) => i !== stdSetupI);
  return (
    <tbody>
      <tr>
        <th className="sticky-col" />
        <th color="lightGold-text">{setups[stdSetupI].name}</th>
        {otherSetupIs.map((index, i) => (
          <th key={i}>{setups[index].name}</th>
        ))}
      </tr>
      {subs.map((name, i) => {
        const stdVal = stdValues[name][focus];
        const isArr = Array.isArray(stdVal);
        return (
          <tr key={i}>
            <td className="sticky-col">{name}</td>
            <td color="lightGold-text">{display(stdVal)}</td>
            {otherSetupIs.map((index, j) => {
              const thisVal = allDmg[index][main][name][focus];
              const diff = isArr ? thisVal[0] - stdVal[0] : thisVal - stdVal;
              const pctDiff =
                Math.round(
                  (Math.abs(diff) * 1000) / (isArr ? stdVal[0] : stdVal)
                ) / 10;
              return (
                <OtherVals
                  key={j}
                  value={thisVal}
                  higher={diff > 0}
                  pctDiff={pctDiff}
                />
              );
            })}
          </tr>
        );
      })}
    </tbody>
  );
}

function OtherVals({ value, higher, pctDiff }) {
  const Icon = higher ? FaLongArrowAltUp : FaLongArrowAltDown;
  return (
    <td className={"relative" + (pctDiff ? " tooltip_wrapper" : "")}>
      {display(value)}
      {pctDiff ? (
        <>
          <span>
            <Icon className={"long-arrow " + (higher ? "higher" : "lower")} />
          </span>
          <span
            className={
              "white-glow-shadow tooltip " + (higher ? "higher" : "lower")
            }
          >
            <b>{(higher ? "+" : "-") + pctDiff + "%"}</b>
          </span>
        </>
      ) : null}
    </td>
  );
}
