import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ADD_ART } from "../../app/databaseSlice";
import { artifactIcons } from "../../configs";
import { artMS } from "../../data/artifacts/config";
import { initArtStatFilter } from "../../helpers";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";
import { StyledInv } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ArtPicker } from "../Picker/ItemPicker";
import PrePicker from "../Picker/PrePicker";
import BtnBar from "./BtnBar";
import LeftSection from "./LeftSection";
import RightSection from "./RightSection";

// use reselect
// [here, ../Readers/ArtInventory]
export const filterArtIds = ({ byId, allIds }, types, sets, stats) => {
  console.log("run s");
  let result = types.length
    ? allIds.filter((id) => types.includes(byId[id].type))
    : allIds;
  if (sets.length) {
    result = result.filter((id) => sets.includes(byId[id].code));
  }
  function compareMainStat(a, b) {
    const ms = (id) => {
      const { type, mainSType, rarity = 5, level } = byId[id];
      return artMS[type][mainSType][rarity || 5][level];
    };
    return ms(b) - ms(a);
  }
  if (stats.main !== "All") {
    result = result.filter((id) => byId[id].mainSType === stats.main);
    if (stats.subs[0] === "All") {
      result.sort(compareMainStat);
    }
  }
  if (stats.subs[0] !== "All") {
    const requires = stats.subs.filter((s) => s !== "All");
    result = result.filter((id) =>
      requires.every((rq) => byId[id].subS.map((ss) => ss.type).includes(rq))
    );
    function getVal(id, type) {
      return byId[id].subS.find((s) => s.type === type).val;
    }
    result.sort((a, b) => {
      if (stats.main !== "All") {
        const msResult = compareMainStat(a, b);
        if (msResult) {
          return msResult;
        }
      }
      for (const rq of requires) {
        const ssResult = getVal(b, rq) - getVal(a, rq);
        if (ssResult) {
          return ssResult;
        }
      }
      return false;
    });
  }
  return result;
};

export default function MyArtifacts() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [artType, setArtType] = useState(null);
  const [types, typeFilter, setTypes] = useTypeFilter(false);
  const [sets, setSets] = useState([]);
  const [stats, setStats] = useState(initArtStatFilter());
  const myArts = useSelector((state) => state.database.myArts);
  const filteredIds = filterArtIds(myArts, types, sets, stats);
  const [chosenID, setChosenID] = useState(filteredIds[0] || 0);
  const dispatch = useDispatch();

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <BtnBar
          add={() => setPrePickerOn(true)}
          filter={{ types, setTypes, sets, setSets, stats, setStats }}
          typeFilter={typeFilter}
        />
        <div className="body">
          <LeftSection
            ids={filteredIds}
            chosenID={chosenID}
            setChosenID={setChosenID}
          />
          <RightSection
            ids={filteredIds}
            chosenID={chosenID}
            setChosenID={setChosenID}
          />
        </div>
      </StyledInv>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={artifactIcons}
          pick={(artType) => {
            setArtType(artType);
            setPrePickerOn(false);
          }}
        />
      )}
      {artType && (
        <ArtPicker
          needMassAdd={true}
          artType={artType}
          close={() => setArtType(null)}
          pick={(newItem) => {
            const ID = Date.now();
            dispatch(ADD_ART({ ID, user: null, ...newItem }));
            setChosenID(ID);
          }}
        />
      )}
    </Box>
  );
}
