import { useSelector } from "react-redux";
import { findById } from "../../../helpers";
import { Span, Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Notes } from "../../Calculator/Modifiers/Target";
import { EquippedChar, SetBonus } from "../../CommonCpns";
import Modal from "../../CommonCpns/Modal";
import ArtifactCard from "../../Readers/ArtifactCard";
import StatsTable from "../../Readers/StatsTable";
import WeaponCard from "../../Readers/WeaponCard";
import Buffs from "./Buffs";
import Debuffs from "./Debuffs";
import { ModWrapper, StatWrapper } from "./styles";

export default function SetupModal({ type, result, setup, close }) {
  return (
    <Modal close={close}>
      {["Weapon", "Artifacts"].includes(type) ? (
        <Box
          className="px-3 py-4 flex overflow-auto white-glow-shadow max-width-95"
          borderRadius="0.5rem"
          bgColor="cardBg1"
        >
          {type === "Weapon" ? (
            <Weapon weapon={setup.weapon} />
          ) : (
            <ArtPieces artPieces={setup.art.pieces} />
          )}
        </Box>
      ) : (
        <Box
          className="custom-sb white-glow-shadow max-width-95"
          height="85%"
          borderRadius="0.5rem"
          bgColor="cardBg1"
        >
          {type === "Stats" ? (
            <Stats
              ATTRs={result.ATTRs}
              artSBnes={result.artSBnes}
              sets={setup.art.sets}
            />
          ) : (
            <Modifiers setup={setup} result={result} />
          )}
        </Box>
      )}
    </Modal>
  );
}

function Weapon({ weapon }) {
  return (
    <Box className="hide-sb" width="18.75rem" height="30rem">
      <WeaponCard mutable={false} weapon={weapon} />
    </Box>
  );
}

function ArtPieces({ artPieces }) {
  const { myArts } = useSelector((state) => state.database);
  return artPieces.map((artP, i) => {
    if (artP) {
      const existed = artP.ID ? findById(myArts, artP.ID) : undefined;
      return (
        <Box key={i} className="px-1" width="14.5rem">
          <ArtifactCard mutable={false} art={artP} ssSpace={2} />
          {existed ? <EquippedChar user={existed.user || "None"} /> : null}
        </Box>
      );
    }
    return null;
  });
}

function Stats({ ATTRs, artSBnes, sets }) {
  return (
    <div className="full-h flex">
      <StatWrapper>
        <Text variant="h6" bold color="orange">
          Final Attributes
        </Text>
        <div className="mt-1 hide-sb">
          <StatsTable stats={ATTRs} breakdown={true} />
        </div>
      </StatWrapper>
      <StatWrapper>
        <Text variant="h6" bold color="orange">
          Artifact Stats
        </Text>
        <div className="mt-1 hide-sb">
          <StatsTable stats={artSBnes} breakdown={false} />
        </div>
      </StatWrapper>
      <StatWrapper>
        <div className="full-h hide-sb">
          <SetBonus sets={sets} />
        </div>
      </StatWrapper>
    </div>
  );
}

function Modifiers({
  setup: {
    char,
    selfMCs,
    weapon,
    subWpMCs,
    art,
    elmtMCs,
    customMCs = { BCs: [], DCs: [] },
    party,
    target,
  },
  result: { ATTRs, rxnBnes, infusion },
}) {
  return (
    <div className="full-h flex">
      <ModWrapper type="BUFFS USED" padding="pl-3 pr-2">
        <Buffs
          char={char}
          selfBCs={selfMCs.BCs}
          weapon={weapon}
          subWpBCs={subWpMCs.BCs}
          arts={art}
          elmtMCs={elmtMCs}
          customBCs={customMCs.BCs}
          infusion={infusion}
          party={party}
          ATTRs={ATTRs}
          rxnBnes={rxnBnes}
        />
      </ModWrapper>
      <ModWrapper type="DEBUFFS USED" padding="px-2">
        <Debuffs
          char={char}
          selfDCs={selfMCs.DCs}
          artSubDCs={art.subDCs}
          elmtMCs={elmtMCs}
          customDCs={customMCs.DCs}
          party={party}
        />
      </ModWrapper>
      <ModWrapper type="TARGET" padding="pl-2 pr-3">
        <Box className="full-h px-2">
          {Object.entries(target).map((pair, i) => (
            <Text key={i} className="mb-1" variant="h6">
              <Span
                className="mr-2"
                color={
                  pair[0] === "Level" ? "lightGold" : pair[0].split(" ")[0]
                }
              >
                {pair[0]}:
              </Span>
              <b>{pair[1]}</b>
            </Text>
          ))}
          <div className="mt-4">
            <Notes charName={char.name} infusion={infusion} />
          </div>
        </Box>
      </ModWrapper>
    </div>
  );
}
