import styled from "styled-components";
import { colors, fontSizes, lineHeights } from "../theme";
import { bgColorProp, brProps, colorProp, fwProp } from "./helpers";

export const Text = styled.p`
  font-size: ${(pr) => fontSizes[pr.variant || "p"]};
  font-weight: ${(pr) => {
    return { h1: 900, h2: 700, h3: 700, h4: 700 }[pr.variant];
  }};
  line-height: ${(pr) => lineHeights[pr.variant || "p"]};
  ${colorProp};
  ${fwProp};
  line-height: ${(pr) => pr.lineHeight};
  text-align: ${(pr) => pr.align};
`;

export const Span = styled.span`
  ${colorProp};
  font-weight: ${({ b }) => b && 700};
  ${({ color }) => {
    if (["Pyro", "orange", "Red"].includes(color)) {
      return `
        position: relative;
        bottom: ${color === "Pyro" ? "-2px" : "-1px"};
      `;
    }
  }}
`;

export const spanMaker = (color) => (props) => (
  <Span color={color} {...props} />
);

export const GreenOn = ({ on, children }) => (
  <Span color="green" className={on ? "" : "unavailable"}>
    {children}
  </Span>
);

export const Green = spanMaker("green");
export const Red = spanMaker("lightRed");
export const Gold = spanMaker("Gold");
export const Orange = spanMaker("orange");
export const Pyro = spanMaker("Pyro");
export const Hydro = spanMaker("Hydro");
export const Cryo = spanMaker("Cryo");
export const Electro = spanMaker("Electro");
export const Geo = spanMaker("Geo");
export const Anemo = spanMaker("Anemo");

export const List = styled.ul`
  list-style-position: ${(pr) => pr.position};
  list-style-type: ${(pr) => pr.type};
  & > li {
    line-height: 1.5;
  }
`;

export const Img = styled.img`
  display: block;
  width: ${(pr) => pr.width};
  height: ${(pr) => pr.height}};
  ${brProps};
  ${bgColorProp};
`;

export const CollapseHeading = styled.p`
  padding: 0.25rem 1rem 0;
  background: ${colors.cardBg3};
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.6;
  &.mb-2p5 {
    margin-bottom: 0.75rem;
  }
  &.active {
    background: #f5dc6e;
    color: black;
  }
`;

export const CollapseContent = styled.div`
  padding: 0 1rem 0 0.5rem;
  & .mb-m1:first-child {
    margin-top: -0.5rem;
  }
`;

export const Link = styled.a`
  &:link,
  &:visited {
    color: ${colors.green};
    text-decoration: none;
  }
  &:hover {
    color: ${colors.lightRed};
  }
`;
