import { createSlice } from "@reduxjs/toolkit";
import { artTypes } from "../configs";
import { findArtifact, findWeapon } from "../data/controllers";
import { ascsFromLv, splitLv } from "../helpers";
import { processNumInp } from "../helpers/processors";
import { initCharInfo, initWeapon } from "./calculatorSlice/initiators";

const initialState = {
  myChars: { byName: {}, allNames: [] },
  myWps: { byId: {}, allIds: [] },
  myArts: { byId: {}, allIds: [] },
  chosenChar: ""
};

export const resetAndLoad = (state, field, data) => {
  state[field] = { byId: {}, allIds: [] };
  for (const item of data) {
    state[field].byId[item.ID] = item;
    state[field].allIds.push(item.ID);
  }
};

export const databaseSlice = createSlice({
  name: "database",
  initialState: initialState,
  reducers: {
    UPLOAD_DB: (state, action) => {
      const { myChars, myWps, myArts } = action.payload;
      state.myChars = { byName: {}, allNames: [] };
      for (const char of myChars) {
        state.myChars.byName[char.name] = char;
        state.myChars.allNames.push(char.name);
      }
      resetAndLoad(state, "myWps", myWps);
      resetAndLoad(state, "myArts", myArts);
      state.chosenChar = myChars.length ? myChars[0].name : "";
    },
    CHOOSE_MY_CHAR: (state, action) => {
      state.chosenChar = action.payload;
    },
    ADD_CHAR: (state, action) => {
      const { name, weapon } = action.payload;
      const weaponID = Date.now();
      state.chosenChar = name;
      state.myChars.allNames.unshift(name);
      state.myChars.byName[name] = {
        name,
        ...initCharInfo({}),
        weaponID,
        artIDs: [null, null, null, null, null]
      };
      state.myWps.allIds.unshift(weaponID);
      state.myWps.byId[weaponID] = {
        ID: weaponID,
        user: name,
        ...initWeapon({ type: weapon })
      };
    },
    SORT_CHARS: (state, action) => {
      state.myChars.allNames = action.payload.map(({ name }) => name);
    },
    TOGGLE_CONS: (state, action) => {
      const { name, consIndex } = action.payload;
      const char = state.myChars.byName[name];
      char.constellation =
        char.constellation === consIndex + 1 ? consIndex : consIndex + 1;
    },
    CHANGE_TALENT: (state, action) => {
      const { name, type, level } = action.payload;
      state.myChars.byName[name][type] = level;
    },
    LEVEL_CHAR: (state, action) => {
      const { name, level } = action.payload;
      const char = state.myChars.byName[name];
      char.level = level;
      char.ascension = ascsFromLv(level);
    },
    REMOVE_CHAR: (state, action) => {
      const { myChars, myWps, myArts } = state;
      const name = action.payload;
      const { weaponID, artIDs } = myChars.byName[name];
      myWps.byId[weaponID].user = null;
      for (const id of artIDs) {
        if (id) myArts.byId[id].user = null;
      }
      delete myChars.byName[name];
      let charI = myChars.allNames.indexOf(name);
      if (charI !== -1) myChars.allNames.splice(charI, 1);
      if (charI === myChars.length) charI--;
      state.chosenChar = myChars[charI]?.name || "";
    },
    SWITCH_WP: (state, action) => {
      const { newUser, newID, oldUser, oldID } = action.payload;
      const { myWps, myChars } = state;
      myWps.byId[newID].user = oldUser;
      myWps.byId[oldID].user = newUser;
      myChars.byName[oldUser].weaponID = newID;
      if (newUser) {
        myChars.byName[newUser].weaponID = oldID;
      }
    },
    SWITCH_ART: (state, action) => {
      const { newUser, newID, oldUser, oldID, pos } = action.payload;
      const { myArts, myChars } = state;
      myArts.byId[newID].user = oldUser;
      if (oldID) {
        myArts.byId[oldID].user = newUser;
      }
      myChars.byName[oldUser].artIDs[pos] = newID;
      if (newUser) {
        myChars.byName[newUser].artIDs[pos] = oldID;
      }
    },
    UNEQUIP_ART: (state, action) => {
      const { user, artID, pos } = action.payload;
      state.myChars.byName[user].artIDs[pos] = null;
      state.myArts.byId[artID].user = null;
    },
    // wp
    ADD_WP: (state, action) => {
      const { ID, item } = action.payload;
      state.myWps.byId[ID] = { ID, user: null, ...item };
      state.myWps.allIds.unshift(ID);
    },
    SORT_WPS: (state) => {
      const { byId, allIds } = state.myWps;
      allIds.sort((a, b) => {
        const rA = findWeapon(byId[a]).rarity;
        const rB = findWeapon(byId[b]).rarity;
        if (rA !== rB) return rB - rA;
        const [fA, sA] = splitLv(a);
        const [fB, sB] = splitLv(b);
        if (fA !== fB) {
          return fB - fA;
        }
        if (byId[a].type !== byId[b].type) {
          const type = {
            Bow: 5,
            Catalyst: 4,
            Polearm: 3,
            Claymore: 2,
            Sword: 1
          };
          return type[byId[b].type] - type[byId[a].type];
        }
        return sB - sA;
      });
    },
    SWAP_WP_USER: (state, action) => {
      const { newUser, wpID, oldUser } = action.payload;
      const { myChars, myWps } = state;
      myWps.byId[wpID].user = newUser;
      const newChar = myChars.byName[newUser];
      const { weaponID } = newChar;
      myWps.byId[weaponID].user = oldUser;
      if (oldUser) {
        myChars.byName[oldUser].weaponID = weaponID;
      }
      newChar.weaponID = wpID;
    },
    REMOVE_WP: (state, action) => {
      const { ID, user, type } = action.payload;
      const { myWps, myChars } = state;
      myWps.allIds.splice(myWps.allIds.indexOf(ID), 1);
      delete myWps.byId[ID];
      if (user) {
        const newWpID = Date.now();
        myWps.byId[newWpID] = {
          ID: newWpID,
          user,
          ...initWeapon({ type })
        };
        myWps.allIds.unshift(newWpID);
        myChars.byName[user].weaponID = newWpID;
      }
    },
    UPGRADE_WP: (state, action) => {
      const { ID, level } = action.payload;
      state.myWps.byId[ID].level = level;
    },
    REFINE_WP: (state, action) => {
      const { ID, rfm } = action.payload;
      state.myWps.byId[ID].refinement = rfm;
    },
    // art
    ADD_ART: (state, action) => {
      const { ID } = action.payload;
      state.myArts.byId[ID] = action.payload;
      state.myArts.allIds.unshift(ID);
    },
    SORT_ARTS: (state) => {
      const { byId, allIds } = state.myArts;
      allIds.sort((a, b) => {
        if (byId(a).level !== byId(b).level) {
          return byId(b).level - byId(a).level;
        }
        if (byId(a).type !== byId(b).type) {
          const type = {
            flower: 5,
            plume: 4,
            sands: 3,
            goblet: 2,
            circlet: 1
          };
          return type[byId(b).type] - type[byId(a).type];
        }
        const aName = findArtifact({ code: byId(a).code }).name;
        const bName = findArtifact({ code: byId(b).code }).name;
        return bName.localeCompare(aName);
      });
    },
    SWAP_ART_USER: (state, action) => {
      const { newUser, artID, artType, oldUser } = action.payload;
      const { myChars, myArts } = state;
      myArts.byId[artID].user = newUser;
      const { artIDs } = myChars.byName[newUser];
      const pos = artTypes.indexOf(artType);
      if (artIDs[pos]) {
        myArts.byId[artIDs[pos]].user = oldUser;
      }
      if (oldUser) {
        myChars.byName[oldUser].artIDs[pos] = artIDs[pos];
      }
      artIDs[pos] = artID;
    },
    REMOVE_ART: (state, action) => {
      const { ID, user, type } = action.payload;
      const { myArts, myChars } = state;
      myArts.allIds.splice(myArts.allIds.indexOf(ID), 1);
      delete myArts.byId[ID];
      if (user) {
        const pos = artTypes.indexOf(type);
        myChars.byName[user].artIDs[pos] = null;
      }
    },
    ENHANCE_ART: (state, action) => {
      const { ID, level } = action.payload;
      state.myArts.byId[ID].level = level;
    },
    ENHANCE_ART_BY_4: (state, action) => {
      const { ID, rarity } = action.payload;
      const art = state.myArts.byId[ID];
      art.level = Math.min(art.level + 4, rarity === 4 ? 16 : 20);
    },
    ENHANCE_ART_TO_MAX: (state, action) => {
      const { ID, rarity } = action.payload;
      state.myArts.byId[ID].level = rarity === 4 ? 16 : 20;
    },
    CHANGE_ART_MS_TYPE: (state, action) => {
      const { ID, type } = action.payload;
      state.myArts.byId[ID].mainSType = type;
    },
    CHANGE_ART_SS_TYPE: (state, action) => {
      const { ID, ssIndex, type } = action.payload;
      state.myArts.byId[ID].subS[ssIndex].type = type;
    },
    CHANGE_ART_SS_VAL: (state, action) => {
      const { ID, ssIndex, value } = action.payload;
      const subS = state.myArts.byId[ID].subS[ssIndex];
      subS.val = processNumInp(value, subS.val, 9999);
    },
    OVERWRITE_ART: (state, action) => {
      const { ID, ...stats } = action.payload;
      const { byId } = state.myArts.byId;
      byId[ID] = { ...byId[ID], ...stats };
    }
  }
});

export const {
  UPLOAD_DB,
  CHOOSE_MY_CHAR,
  ADD_CHAR,
  SORT_CHARS,
  TOGGLE_CONS,
  CHANGE_TALENT,
  LEVEL_CHAR,
  REMOVE_CHAR,
  SWITCH_WP,
  SWITCH_ART,
  UNEQUIP_ART,
  ADD_WP,
  SORT_WPS,
  SWAP_WP_USER,
  REMOVE_WP,
  UPGRADE_WP,
  REFINE_WP,
  ADD_ART,
  SORT_ARTS,
  SWAP_ART_USER,
  REMOVE_ART,
  ENHANCE_ART,
  ENHANCE_ART_BY_4,
  ENHANCE_ART_TO_MAX,
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  OVERWRITE_ART
} = databaseSlice.actions;

export const selAllCharNames = (state) => state.database.myChars.allNames;
export const selAllWpIds = (state) => state.database.myWps.allIds;
export const selAllArtIds = (state) => state.database.myArts.allIds;

export const selCharByName = (name) => (state) => {
  return state.database.myChars.byName[name];
};
export const selWpById = (id) => (state) => {
  return state.database.myWps.byId[id];
};
export const selArtById = (id) => (state) => {
  return state.database.myArts.byId[id];
};

// use reslect
export const selAllCharCons = (state) => {
  const { byName, allNames } = state.database.myChars;
  return allNames.map((name) => ({
    name,
    constellation: byName[name].constellation
  }));
};

export default databaseSlice.reducer;
