import cn from "classnames";
import { useEffect, useState } from "react";
import { FaSort, FaTh } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  CHOOSE_MY_CHAR,
  selAllCharCons,
  selAllCharNames
} from "../../app/databaseSlice";
import { wikiImg } from "../../configs";
import { findCharacter } from "../../data/controllers";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import CharSorting from "./CharSorting";
import { StyledTopBar } from "./styles";

export default function TopBar({ setPickerOn, charListRef }) {
  const [sorting, setSorting] = useState(false);
  const [gridview, setGridview] = useState(false);
  const charNames = useSelector(selAllCharNames);
  const chosen = useSelector((state) => state.database.chosenChar);

  useEffect(() => {
    scrolLeft(charListRef, charNames, chosen);
    const thisCpn = charListRef.current;
    const scrollHorizontally = (e) => {
      const delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));
      thisCpn.scrollLeft -= delta * 50;
      e.preventDefault();
    };
    thisCpn.addEventListener("wheel", scrollHorizontally);
    return () => thisCpn.removeEventListener("wheel", scrollHorizontally);
  }, [charListRef]);

  return (
    <Box className="full-w flex justify-center" bgColor="cardBg2">
      <StyledTopBar>
        {!!charNames.length && (
          <AbsoluteBox top="2rem" right="100%" className="flex">
            <IconBtn
              className="mr-3 glow-on-hover"
              variant="positive"
              fontSize="1.25rem"
              onClick={() => setSorting(true)}
            >
              <FaSort />
            </IconBtn>
            <IconBtn
              className="mr-3 glow-on-hover"
              variant="positive"
              onClick={() => setGridview(true)}
            >
              <FaTh />
            </IconBtn>
          </AbsoluteBox>
        )}
        <div ref={charListRef} className="mt-2 flex full-w hide-sb">
          {charNames.length ? (
            <CharList charNames={charNames} chosen={chosen} />
          ) : (
            <Box
              className="full-w flex justify-end align-center"
              height="4.875rem"
            >
              <Text variant="h2" color="lightGold">
                Add New Characters {">>"}
              </Text>
            </Box>
          )}
        </div>
        <img
          src={wikiImg("4/48/System_Wish")}
          alt="wish"
          className="wish_btn pointer"
          draggable={false}
          onClick={() => setPickerOn(true)}
        />
      </StyledTopBar>
      {sorting && <CharSorting close={() => setSorting(false)} />}
      {gridview && (
        <CharPicker
          charListRef={charListRef}
          close={() => setGridview(false)}
        />
      )}
    </Box>
  );
}

function CharList({ charNames, chosen }) {
  const dispatch = useDispatch();
  return charNames.map((name) => {
    const { beta, icon, sideIcon } = findCharacter({ name });
    const xtraClasses = beta
      ? "beta_wrapper m-1 overflow-hidden"
      : "side_wrapper m-2";
    return (
      <div
        key={name}
        className={cn("mx-1 cell pointer", { chosen: name === chosen })}
        onClick={() => dispatch(CHOOSE_MY_CHAR(name))}
      >
        <div className={"icon_wrapper " + xtraClasses}>
          <img
            src={wikiImg(beta ? icon : sideIcon)}
            alt="icon"
            draggable={false}
          />
        </div>
      </div>
    );
  });
}

function CharPicker({ charListRef, close }) {
  const charCons = useSelector(selAllCharCons);
  const dispatch = useDispatch();

  const data = charCons.map(({ name, constellation }) => {
    const { code, icon, rarity, vision, weapon } = findCharacter({ name });
    return { name, code, icon, rarity, vision, weapon, constellation };
  });
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        dispatch(CHOOSE_MY_CHAR(name));
        scrolLeft(
          charListRef,
          charCons.map(({ name }) => name),
          name
        );
      }}
    />
  );
}

function scrolLeft(charListRef, charNames, name) {
  if (charListRef.current) {
    charListRef.current.scrollLeft = charNames.indexOf(name) * 84;
  }
}
