import styled from "styled-components";
import { pctOf } from "../../../configs";
import { artSSTypes } from "../../../data/artifacts/config";
import { Span, Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import validSSVals from "./validSSVals";

export default function ArtSubstats({
  ssSpace,
  mutable,
  msType,
  rarity,
  substats,
  changeSSType,
  changeSSVal,
}) {
  const count = { [msType]: 1 };
  for (let { type } of substats) {
    count[type] = (count[type] || 0) + 1;
  }
  return substats.map(({ type, val }, i) => {
    const valid = !val || validSSVals[type][rarity].includes(val);
    return mutable ? (
      <Box key={i} className="mt-2 pt-1 flex align-center" bgColor="cardBg2">
        <Select
          color={count[type] === 1 ? "white" : "Red"}
          fontSize="1.0625rem"
          className="appearance-none bg-for-icon white-arrow-bg artSS-select"
          value={type}
          onChange={(e) => changeSSType(e.target.value, i)}
        >
          {artSSTypes.map((type) => (
            <option key={type}>{type}</option>
          ))}
        </Select>
        <Text>+</Text>
        <SSVal
          value={val}
          valid={valid}
          onChange={(e) => changeSSVal(e.target.value, i)}
        />
        <span>{pctOf(type)}</span>
      </Box>
    ) : (
      <Box
        key={i}
        className={`mt-2 pl-${ssSpace} pt-1 flex align-center`}
        bgColor="cardBg2"
      >
        <p className={"mr-" + ssSpace}></p>
        <Text>
          <Span className="mr-1" color={count[type] === 1 ? "white" : "Red"}>
            {type}
          </Span>
          <Span color={valid ? "green" : "Red"}>
            +{val}
            {pctOf(type)}
          </Span>
        </Text>
      </Box>
    );
  });
}

let SSVal = styled.input`
  position: relative;
  bottom: ${(props) => (props.valid ? 0 : "-1px")};
  margin-left: 3px;
  padding: 3px 8px 3px 0;
  width: 3.25rem;
  border: none;
  background-color: transparent;
  color: ${(props) => (props.valid ? "white" : "red")};
  font-size: 1.0625rem;
  text-align: right;
  outline: none;
`;
