import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  CREATE_WP,
  REFINE_WP,
  UPGRADE_WP,
} from "../../../app/calculatorSlice";
import { levels, wikiImg } from "../../../configs";
import { findWeapon } from "../../../data/controllers";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { AbsoluteBox, GradientBox } from "../../../styledCpns/Layout";
import { BetaMark } from "../../CommonCpns";
import { WpPicker } from "../../Picker/ItemPicker";
import { Pedestal } from "../styles";

export default function SectionWp() {
  const { wpRack, curSetupI } = useSelector(calculator);
  const weapon = wpRack[curSetupI];
  const [pickerOn, setPickerOn] = useState(false);
  const dispatch = useDispatch();
  const { beta, rarity, icon, name } = findWeapon(weapon);

  return (
    <Pedestal>
      <div className="px-2 flex-center">
        <GradientBox
          className="relative pointer"
          bgImg={rarity}
          borderRadius="0.375rem"
          onClick={() => setPickerOn(true)}
        >
          <Img
            src={beta ? icon : wikiImg(icon)}
            alt=""
            width="5.625rem"
            draggable={false}
          />
          {beta && (
            <AbsoluteBox top="-0.25rem" left="-0.25rem">
              <BetaMark />
            </AbsoluteBox>
          )}
        </GradientBox>
        <div className="ml-2 overflow-hidden">
          <Text className="ellipsis" variant="h4" color={rarity} bold>
            {name}
          </Text>
          <div className="mt-1 pl-1 flex-wrap">
            <Text className="mr-1">Level</Text>
            <Select
              color={rarity}
              fontSize="1rem"
              align="right"
              value={weapon.level}
              onChange={(e) => dispatch(UPGRADE_WP(e.target.value))}
            >
              {(rarity < 3 ? levels.slice(0, -4) : levels).map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          </div>
          {rarity >= 3 && (
            <div className="mt-1 pl-1 flex-wrap">
              <Text className="mr-2">Refinement rank</Text>
              <Select
                color={rarity}
                fontSize="1rem"
                value={weapon.refinement}
                onChange={(e) => dispatch(REFINE_WP(+e.target.value))}
              >
                {[1, 2, 3, 4, 5].map((level) => (
                  <option key={level}>{level}</option>
                ))}
              </Select>
            </div>
          )}
        </div>
      </div>
      {pickerOn && (
        <WpPicker
          wpType={weapon.type}
          close={() => setPickerOn(false)}
          pick={(item) => dispatch(CREATE_WP(item))}
        />
      )}
    </Pedestal>
  );
}
