import { useState } from "react";
import cn from "classnames";
import { FaBars, FaInfoCircle } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { CHANGE_TAB, TOGGLE_INTRO } from "../../app/uiSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import Modal from "../CommonCpns/Modal";
import Download from "./Download";
import { BarsBtn, NavBtn, NavRight, StyledMenu } from "./styles";
import Upload from "./Upload";

export default function NavBar(props) {
  return (
    <Box bgColor="rgba(0, 0, 0, 0.6)">
      {window.innerWidth >= 1050 ? (
        <PcInner {...props} />
      ) : (
        <MobileInner {...props} />
      )}
    </Box>
  );
}

function IntroBtn() {
  const dispatch = useDispatch();
  return (
    <NavBtn
      className="last-btn border pointer contain-info_sign"
      onClick={() => dispatch(TOGGLE_INTRO(true))}
    >
      <IconBtn className="mr-2 info_sign" size="1.125rem" fontSize="1.125rem">
        <FaInfoCircle />
      </IconBtn>
      <Text bold>Introduction</Text>
    </NavBtn>
  );
}

const tabs = ["Characters", "Weapons", "Artifacts", "Setups", "Calculator"];

function PcInner({ loadData, outdates }) {
  const tab = useSelector((state) => state.ui.tab);
  const dispatch = useDispatch();
  return (
    <div className="flex justify-between">
      <div className="flex">
        {tabs.map((item, i) => (
          <NavBtn
            key={i}
            className={cn("px-2 py-1", { current: item === tab })}
            onClick={() => dispatch(CHANGE_TAB(item))}
          >
            <Text className="pointer" bold>
              {i < 4 && "My"} {item}
            </Text>
          </NavBtn>
        ))}
      </div>
      <NavRight className="px-1 flex">
        <IntroBtn />
        <Download />
        <Upload loadData={loadData} outdates={outdates} />
      </NavRight>
    </div>
  );
}

function MobileInner(props) {
  const [showing, setShowing] = useState(false);
  return (
    <div className="flex">
      <div className="mr-auto relative">
        <BarsBtn
          className={cn({ showing })}
          onClick={() => setShowing(!showing)}
        >
          <FaBars />
        </BarsBtn>
        {showing && <Menu {...props} close={() => setShowing(false)} />}
      </div>
    </div>
  );
}

function Menu({ loadData, close }) {
  const tab = useSelector((state) => state.ui.tab);
  const dispatch = useDispatch();
  return (
    <Modal close={close}>
      <StyledMenu className="white-glow-shadow">
        <IntroBtn />
        {tabs.map((item, i) => (
          <NavBtn
            key={i}
            className={cn("px-4 py-2 border", { current: item === tab })}
            onClick={() => {
              dispatch(CHANGE_TAB(item));
              close();
            }}
          >
            <Text bold>
              {i < 4 && "My "}
              {item}
            </Text>
          </NavBtn>
        ))}
        <Download />
        <Upload loadData={loadData} closeMenu={close} />
      </StyledMenu>
    </Modal>
  );
}
