import {
  getArtSets,
  getMainArtBCs,
  getMainWpBCs,
  getSubArtBCs,
  getSubArtDCs,
  getSubWpBCs,
  getVisionCount,
  getWpRange
} from "../app/calculatorSlice/helpers";
import { initCharMCs, initParty } from "../app/calculatorSlice/initiators";
import {
  buffCats,
  buffTypes,
  debuffTypes
} from "../components/Calculator/Modifiers/CustomMods/Children";
import {
  ampRxnTrigElmts,
  artTypes,
  crystallizables,
  levels,
  physAndElmts,
  rsnNames,
  swirlables
} from "../configs";
import { allStatsTypes } from "../data/artifacts/config";
import characters from "../data/characters";
import {
  findArtBuff,
  findArtifact,
  findCharacter,
  findWeapon
} from "../data/controllers";
import weapons from "../data/weapons";
import { specialCodes } from "./encode";

export default function decodeImportedData(code) {
  try {
    let [
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      party,
      elmtMCs,
      target,
      customMCs
    ] = code.split("?");

    char = getChar(char);
    const charData = findCharacter(char);
    selfMCs = getSelfMCs(selfMCs, char, charData);
    // console.log(selfMCs);
    weapon = getWeapon(weapon);
    party = getParty(party);
    subWpMCs = getSubWpMCs(subWpMCs, party, weapon.code); // just BCs
    art = getArt(art);
    elmtMCs = getElmtMCs(elmtMCs, getVisionCount(char, party));
    target = getTarget(target);
    customMCs = getCustomMCs(customMCs);

    const result = {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      party,
      elmtMCs,
      customMCs,
      target
    };
    return result;
  } catch (err) {
    console.log(err.message);
    if (["1", "2"].includes(err.message)) return err.message;
    return "3";
  }
}

function getChar(code) {
  let [name, level, constellation, ...talents] = code.split("&");
  name = searchChar(name);
  if (!isNaN(name)) throw new Error("1");
  level = levels[level];
  if (!level) throw new Error("1");
  constellation = revertNum(constellation, 0, 6);
  talents = talents.map((tl) => revertNum(tl, 1, 10));
  const maxLv = +level.slice(-2);
  return {
    name,
    level,
    ascension: maxLv === 20 ? 0 : (maxLv - 30) / 10,
    constellation,
    "Normal Attack": talents[0],
    "Elemental Skill": talents[1],
    "Elemental Burst": talents[2]
  };
}

function getSelfMCs(code, char, charData) {
  const selfMCs = initCharMCs(char.name, true);
  const [BCs, DCs] = code.split("&");
  const { buffs, debuffs } = charData;
  if (BCs.length && buffs) adjustMCs(BCs.split("!"), selfMCs.BCs, buffs, true);
  if (DCs.length && debuffs)
    adjustMCs(DCs.split("!"), selfMCs.DCs, debuffs, true);
  return selfMCs;
}

function getWeapon(code) {
  let [wpC, level, rfm, BCs] = code.split("&");
  const wp = { type: getWpType(wpC), code: +wpC };
  level = levels[level];
  if (!level) throw new Error("1");
  const wpBCs = getMainWpBCs(wp);
  if (BCs.length) {
    if (!wpBCs.length) throw new Error("1");
    adjustMCs(BCs.split("!"), wpBCs, findWeapon(wp).buffs);
  }
  return { ...wp, level, refinement: revertNum(rfm, 1, 5), BCs: wpBCs };
}

function getParty(code) {
  const party = initParty();
  code.split("&").forEach((tmC, tmI) => {
    if (tmI > 2) throw new Error("1");
    if (tmC.length) {
      let [name, BCs, DCs] = tmC.split("#");
      name = searchChar(name);
      const tm = { name, ...initCharMCs(name, false) };
      const { buffs, debuffs } = findCharacter({ name });
      if (BCs.length && buffs) adjustMCs(BCs.split("!"), tm.BCs, buffs, true);
      if (DCs.length && debuffs)
        adjustMCs(DCs.split("!"), tm.DCs, debuffs, true);
      party[tmI] = tm;
    }
  });
  return party;
}

function getSubWpMCs(code, party, mainWpC) {
  let BCs = getWpRange(party);
  for (const type in BCs) {
    BCs[type] = getSubWpBCs(type, mainWpC);
  }
  if (code.length) {
    for (const ctrlC of code.split("!")) {
      let [wpC, rfm, inputs] = ctrlC.split(":");
      wpC = +wpC;
      const type = getWpType(wpC);
      const ctrlI = BCs[type].findIndex((ctrl) => ctrl.code === wpC);
      if (ctrlI === -1) throw new Error("1");
      const nCtrl = BCs[type][ctrlI];
      nCtrl.activated = true;
      nCtrl.refinement = revertNum(rfm, 1, 5);
      if (inputs) {
        const { inputTypes } = findWeapon({ type, code: wpC }).buffs[0]; // index always 0 (v2.3)
        adjustInputs(inputs.split("/"), nCtrl, inputTypes); // no maxs yet
      }
    }
  }
  return { BCs, DCs: {} };
}

function getArt(code) {
  let [pieces, BCsC, subBCs, subDCs] = code.split("&");
  pieces = pieces.split("!");
  if (pieces.length !== 5) throw new Error("1");
  pieces = pieces.map((pCode, i) => {
    if (pCode === "X") return null;
    const parts = pCode.split("#");
    if (parts.length === 4) {
      parts.splice(1, 0, "5");
    }
    let [artC, rarity, level, mainSType, subS] = parts;
    rarity = +rarity;
    if (rarity !== 4 && rarity !== 5) throw new Error("1");
    mainSType = getSType(mainSType);
    subS = subS.split("/");
    if (subS.length !== 4) throw new Error("1");
    subS = subS.map((statC) => {
      let [type, val] = statC.split(":");
      if (!type.length || !val.length) throw new Error("1");
      return { type: getSType(type), val: revertNum(val, 0, 9999) };
    });
    return {
      code: +artC,
      type: artTypes[i],
      rarity,
      level: revertNum(level, 0, 20),
      mainSType,
      subS
    };
  });
  const sets = getArtSets(pieces);
  const setC = sets[0] && sets[0].bonusLv === 1 && sets[0].code;

  const BCs = getMainArtBCs(setC);
  if (BCsC.length && BCs.length)
    adjustMCs(BCsC.split("!"), BCs, findArtifact({ code: setC }).buffs);
  const subMCs = { subBCs: getSubArtBCs(setC), subDCs: getSubArtDCs() };
  const subMCsCode = { subBCs, subDCs };
  for (const type of ["subBCs", "subDCs"]) {
    if (subMCsCode[type].length) {
      for (const ctrlC of subMCsCode[type].split("!")) {
        let [setCode, inputs] = ctrlC.split(":");
        setCode = +setCode;
        const ctrlI = subMCs[type].findIndex((ctrl) => ctrl.code === setCode);
        if (ctrlI === -1) throw new Error("1");
        subMCs[type][ctrlI].activated = true;
        if (!inputs) continue;
        const mod =
          type === "subBCs"
            ? findArtBuff({ code: setCode }, 0)
            : findArtifact({ code: setCode }).debuffs[0]; // index always 0 (v2.7)
        adjustInputs(
          inputs.split("/"),
          subMCs[type][ctrlI],
          mod.inputTypes,
          mod.maxs
        );
      }
    }
  }
  return { pieces, sets, BCs, ...subMCs };
}

function getElmtMCs(code, visions) {
  let [naAmpRxn, ampRxn, scCode, rsnC] = code.split("&");
  if (naAmpRxn.length) {
    naAmpRxn = Object.keys(ampRxnTrigElmts)[+naAmpRxn];
    if (!naAmpRxn) throw new Error("1");
  } else naAmpRxn = null;

  if (ampRxn.length) {
    ampRxn = Object.keys(ampRxnTrigElmts)[+ampRxn];
    if (!ampRxn) throw new Error("1");
  } else ampRxn = null;

  const resonance = [];
  for (const type in visions) {
    if (visions[type] >= 2)
      resonance.push({ name: rsnNames[type], activated: false });
  }
  rsnC.split("#").forEach((rsnI, i) => {
    if (i > 1) throw new Error("1");
    if (rsnI.length) {
      const name = Object.values(rsnNames)[+rsnI];
      if (!name) throw new Error("1");
      const index = resonance.findIndex((rsn) => rsn.name === name);
      if (index === -1) throw new Error("1");
      resonance[index].activated = true;
    }
  });
  return { naAmpRxn, ampRxn, superconduct: revertInp(scCode), resonance };
}

function getTarget(code) {
  let [lvC, ...res] = code.split("&");
  const target = { Level: revertNum(lvC, 0, 100) };
  physAndElmts.forEach((type, i) => {
    target[type + " RES"] = revertNum(res[i], 0, 999);
  });
  return target;
}

function getCustomMCs(code) {
  const result = { BCs: [], DCs: [] };
  for (const modCode of code.split("&")) {
    if (!modCode) continue;
    for (const ctrl of modCode.split("!")) {
      const parts = ctrl.split("#");
      if (parts.length === 2) {
        result.DCs.push({ type: debuffTypes[parts[0]], val: +parts[1] });
      } else if (parts.length === 3) {
        const catKey = +parts[0];
        result.BCs.push({
          catKey,
          type: buffTypes[buffCats[catKey]][parts[1]],
          val: +parts[2]
        });
      }
    }
  }
  return result;
}

// helpers
function searchChar(charC) {
  for (const { name, code } of characters) {
    if (code === +charC) {
      return name;
    }
  }
  throw new Error("1");
}

function getWpType(wpCode) {
  for (const type in weapons) {
    if (weapons[type].find((wp) => wp.code === +wpCode)) {
      return type;
    }
  }
  throw new Error("1");
}

function getSType(code) {
  if (!code.length) throw new Error("1");
  const type = allStatsTypes[+code];
  if (!type) throw new Error("1");
  return type;
}

function adjustMCs(newMCs, oldMCs, mods, doCheck) {
  for (const ctrl of newMCs) {
    let [index, inputs] = ctrl.split(":");
    if (!index.length) throw new Error("1");
    index = +index;
    const mod = doCheck ? mods.find((m) => m.index === index) : mods[index];
    const i = oldMCs.findIndex((c) => c.index === index);
    if (i === -1) {
      if (doCheck && mod && mod.outdated) throw new Error("2");
      throw new Error("1");
    }
    oldMCs[i].activated = true;
    const oldInputs = oldMCs[i].inputs;
    if (inputs && !oldInputs) throw new Error("1");
    if (oldInputs) {
      if (!inputs) throw new Error("1");
      adjustInputs(inputs.split("/"), oldMCs[i], mod.inputTypes, mod.maxs);
    }
  }
}

function adjustInputs(newInputs, ctrl, inputTypes, maxs) {
  newInputs.forEach((inp, j) => {
    const input = revertInp(inp);
    if (typeof input === typeof ctrl.inputs[j]) {
      const type = inputTypes[j];
      if (
        (["text", "select", "stacks"].includes(type) &&
          maxs &&
          input > maxs[j]) ||
        (["swirl", "absorption"].includes(type) &&
          swirlables.indexOf(input) === -1) ||
        (type === "crystallize" && crystallizables.indexOf(input) === -1)
      ) {
        throw new Error("1");
      }
      ctrl.inputs[j] = input;
    } else throw new Error("1");
  });
}

function revertInp(inp) {
  if (!inp.length) throw new Error("1");
  if (inp === "T") {
    return true;
  } else if (inp === "F") {
    return false;
  } else if (!isNaN(inp)) {
    if (+inp < 0) throw new Error("1");
    return +inp;
  }
  const str = revertSpecialCode(inp);
  if (str) return str;
  throw new Error("1");
  // return inp;
}

function revertSpecialCode(code) {
  for (const str in specialCodes) {
    if (specialCodes[str] === code) {
      return str;
    }
  }
  return null;
}

function revertNum(code, min, max) {
  if (!code.length) throw new Error("1");
  const result = +code;
  if (isNaN(result) || result < min || (max && result > max))
    throw new Error("1");
  return result;
}
